import { unescape } from 'lodash';
/**
 * Gives processed formula of formula indicator
 * @param {Object} formulaInfo contains formula information
 *
 * @returns {String} Processed formula
 */
export const getFormula = (formulaInfo) => {
	if (!formulaInfo || !formulaInfo.formula || !formulaInfo.formulaComponents)
		return '';

	let formula = formulaInfo.formula;
	const formulaComponents = formulaInfo.formulaComponents;

	let isNotFormulaIndicator = !(formula && formulaComponents);
	if (isNotFormulaIndicator || !Array.isArray(formulaComponents)) return '';

	for (let i = 0; i < formulaComponents.length; i++) {
		const part = formulaComponents[i];
		const display = unescape(part.display);
		const key = unescape(part.key);

		if (display && key) {
			if (/^\[\[/.test(display))
				formula = formula.split('[[' + key + ']]').join(display);
			else formula = formula.split('{{' + key + '}}').join(display);
		}
	}
	return formula;
};
