import { actionTypes } from '@servicenow/ui-core';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import get from 'lodash/get';
import {
	FETCH_THRESHOLDS_REQUESTED,
	FETCH_THRESHOLDS_STARTED,
	FETCH_THRESHOLDS_SUCCESS,
	FETCH_THRESHOLDS_FAILED,
	CREATE_THRESHOLD_CLICKED,
	HANDLE_NAVIGATE_BACK,
} from './constants';

import {
	CREATE_THRESHOLD_CANCEL,
	UPDATE_THRESHOLD_CANCEL,
	FORM_NAVIGATE_BACK_CLICKED,
	CREATE_THRESHOLD_SUCCEEDED,
	UPDATE_THRESHOLD_SUCCEEDED,
	DELETE_THRESHOLD_SUCCEEDED,
} from './ThresholdForm/constants';
import { wrapWithHandler } from './utils';

const { COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED } = actionTypes;

export const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: ({ dispatch, properties: { uuid } }) => {
		if (uuid)
			dispatch(FETCH_THRESHOLDS_REQUESTED, {
				uuid,
			});
	},

	[COMPONENT_PROPERTY_CHANGED]: ({
		dispatch,
		updateState,
		action: {
			payload: { name, value, previousValue },
		},
		state: { createButtonRef },
	}) => {
		if (name === 'uuid' && value !== previousValue) {
			dispatch(FETCH_THRESHOLDS_REQUESTED, {
				uuid: value,
			});
		} else if (name === 'opened' && value !== previousValue) {
			if (value === true && createButtonRef) {
				createButtonRef.elm.focus();
			} else if (value === null) {
				updateState({
					createThreshold: undefined,
					thresholdClickedValue: undefined,
				});
			}
		} else if (name === 'filterInfo' && value !== previousValue) {
			if (value.length !== undefined && createButtonRef) {
				createButtonRef.elm.focus();
			}
		} 
	},

	[FETCH_THRESHOLDS_REQUESTED]: createHttpEffect('/api/now/pa/thresholds', {
		method: 'GET',
		queryParams: ['uuid'],
		startActionType: FETCH_THRESHOLDS_STARTED,
		successActionType: FETCH_THRESHOLDS_SUCCESS,
		errorActionType: FETCH_THRESHOLDS_FAILED,
	}),
	[FETCH_THRESHOLDS_STARTED]: ({ updateState }) => {
		updateState({ isThresholdLoading: true });
	},
	[FETCH_THRESHOLDS_SUCCESS]: ({ action: { payload }, updateState }) => {
		const items = get(payload, 'result');
		updateState({ items: wrapWithHandler(items), isThresholdLoading: false });
	},
	[FETCH_THRESHOLDS_FAILED]: ({ action: { payload }, updateState }) => {
		console.error(payload, FETCH_THRESHOLDS_FAILED);
		updateState({ isThresholdLoading: false });
	},
	FOCUS_CREATE: ({
		action: {
			payload: { createButton },
		},
		updateState,
	}) => {
		if (createButton && createButton.elm) setTimeout(() => createButton.elm.focus(), 50);
		updateState({ focusToCreate: false });
	},

	/**
	 * Handles cancel event dispatched from threshold form
	 */
	[HANDLE_NAVIGATE_BACK]: ({ updateState }) => {
		updateState({
			focusToCreate: true,
			createThreshold: undefined,
			thresholdClickedValue: undefined,
		});
	},

	/**
	 * Cancel event dispatched from threshold form
	 */
	[CREATE_THRESHOLD_CANCEL]: ({ dispatch }) => dispatch(HANDLE_NAVIGATE_BACK),
	[UPDATE_THRESHOLD_CANCEL]: ({ dispatch }) => dispatch(HANDLE_NAVIGATE_BACK),
	[FORM_NAVIGATE_BACK_CLICKED]: ({ dispatch }) => dispatch(HANDLE_NAVIGATE_BACK),

	/**
	 * Handles threshold-form create, update and delete success event dispatch
	 */
	[CREATE_THRESHOLD_SUCCEEDED]: ({
		action: { payload },
		properties: { uuid },
		dispatch,
		updateState,
	}) => {
		updateState({
			thresholdType: get(payload, 'result[0].is_global', true)
				? 'global'
				: 'personal',
		});
		dispatch(FETCH_THRESHOLDS_REQUESTED, { uuid }),
		dispatch(HANDLE_NAVIGATE_BACK);
	},
	[UPDATE_THRESHOLD_SUCCEEDED]: ({ properties: { uuid }, dispatch }) => {
		dispatch(FETCH_THRESHOLDS_REQUESTED, { uuid }),
		dispatch(HANDLE_NAVIGATE_BACK);
	},
	[DELETE_THRESHOLD_SUCCEEDED]: ({ properties: { uuid }, dispatch }) => {
		dispatch(FETCH_THRESHOLDS_REQUESTED, { uuid }),
		dispatch(HANDLE_NAVIGATE_BACK);
	},

	/**
	 * Handles the event dispatched on change of Tabs selection on threshold panel
	 */
	'NOW_TABS#SELECTED_ITEM_SET': ({
		action: {
			payload: { value, type },
			stopPropagation,
		},
		updateState,
	}) => {
		if (type === 'THRESHOLD_TYPE') {
			stopPropagation();
			updateState({
				thresholdType: value,
			});
		}
	},

	/**
	 * Handles the event dispatched on click of tree item on threshold panel
	 */
	'NOW_CONTENT_TREE#ITEM_CLICKED': ({
		action: {
			payload: {
				item: { threshold },
			},
		},
		updateState,
	}) => {
		updateState({ thresholdClickedValue: threshold });
	},

	/**
	 * Handles the button click event on threshold panel
	 */
	'NOW_BUTTON_ICONIC#CLICKED': ({
		action: {
			payload: { type },
		},
		updateState,
	}) => {
		if (type === CREATE_THRESHOLD_CLICKED)
			updateState({ createThreshold: true });
	},
};
