import { actionTypes } from '@servicenow/ui-core';
import get from 'lodash/get';
import moment from 'moment';
import { getConfig } from '../../../helpers/get-config';
import { PANEL_MENU_SELECTED_SET } from '../../PanelMenu/panelMenuConstants';
import { SIDE_PANEL_CLOSE } from '../../SidePanel/sideMenuConstants';
import { getTargetMinMaxTimeStamp } from '../main-view-utils';
import { getFormattedDate } from '../../../helpers/dateUtil';
import {
	SERIES_DISPLAY_TYPE_SET,
	SERIES_DISPLAY_TYPE_SPLIT,
	SERIES_DISPLAY_TYPE_AGGREGATE,
	CHART_DATE_FORMAT,
	PICKER_DATE_FORMAT,
} from '../../../constants';
import {
	CREATE_THRESHOLD_SUCCEEDED,
	UPDATE_THRESHOLD_SUCCEEDED,
	DELETE_THRESHOLD_SUCCEEDED,
} from '../../../views/ThresholdsPanel/ThresholdForm/constants';

const { COMPONENT_PROPERTY_CHANGED } = actionTypes;

export const handlers = {
	[COMPONENT_PROPERTY_CHANGED]: ({
		action: {
			payload: { name, previousValue, value },
		},
		updateState,
	}) => {
		name === 'recordsQuery' &&
			previousValue !== value &&
			updateState({ currentPage: 1 });
	},

	[PANEL_MENU_SELECTED_SET]: ({
		action: { payload },
		state: { selectedMenu = {} },
		updateState,
	}) => {
		if (!selectedMenu || selectedMenu.menuId !== payload.menuId)
			updateState({ selectedMenu: payload });
		else updateState({ selectedMenu: null });
	},

	[SIDE_PANEL_CLOSE]: ({ updateState }) => updateState({ selectedMenu: null }),

	'SN_KPI_DETAILS#SIDE_NAV_REF': ({
		action: {
			payload: { sideNavRef },
		},
		updateState,
	}) => updateState({ sideNavRef }),

	'SN_KPI_DETAILS#SIDE_PANEL_CLOSE_REF': ({
		action: {
			payload: { sidePanelCloseRef },
		},
		updateState,
	}) => updateState({ sidePanelCloseRef }),

	'SN_KPI_DETAILS#VISUALIZATION_REF': ({
		action: {
			payload: { visualizationRef },
		},
		updateState,
	}) => updateState({ visualizationRef }),

	'SN_KPI_DETAILS#SIDE_PANEL_FOCUS': ({
		state: { selectedMenu, sideNavRef, sidePanelCloseRef },
	}) => {
		if (selectedMenu && sidePanelCloseRef) sidePanelCloseRef.focus();
		else if (sideNavRef) sideNavRef.focus();
	},

	'SN_KPI_DETAILS#VISUALIZATION_FOCUS': ({ state: { visualizationRef } }) => {
		if (visualizationRef) visualizationRef.focus();
	},

	/**
	 * Panel actions
	 */
	'SN_KPI_DETAILS#BREAKDOWN_COLLAPSE_SET': ({
		action: {
			payload: { breakdown },
		},
		state: { activeBreakdownCollapse },
		updateState,
	}) => {
		updateState({
			activeBreakdownCollapse:
				activeBreakdownCollapse === breakdown ? '' : breakdown,
		});
	},

	'SN_KPI_DETAILS#BREAKDOWN_ELEMENT_TOGGLE_SET': ({
		action: {
			payload: { element, breakdown },
		},
		state: {
			properties: { breakdownFilterManager },
		},
	}) => {
		breakdownFilterManager.toggleBreakdown(breakdown, element);
		breakdownFilterManager.syncStore();
	},

	'SN_KPI_DETAILS#BREAKDOWN_ELEMENT_CLEAR': ({
		action: {
			payload: { breakdown, element },
		},
		state: {
			properties: { breakdownFilterManager },
		},
	}) => {
		breakdownFilterManager.resetBreakdownInfo();
		breakdownFilterManager.toggleBreakdown(breakdown, element, true);
		breakdownFilterManager.syncStore();
	},

	'SN_KPI_DETAILS#BREAKDOWN_ELEMENT_APPLY': ({
		state: {
			properties: { breakdownFilterManager, isMultiElementSupported },
		},
		action: {
			payload: { splitViewEnabled, force },
		},
		dispatch,
		updateState,
	}) => {
		let splitValue = splitViewEnabled;

		if (!force && !splitViewEnabled && !isMultiElementSupported)
			splitValue = SERIES_DISPLAY_TYPE_SPLIT;

		dispatch(
			SERIES_DISPLAY_TYPE_SET,
			splitValue ? SERIES_DISPLAY_TYPE_SPLIT : SERIES_DISPLAY_TYPE_AGGREGATE
		);
		breakdownFilterManager.publishPreviewedBreakdowns();
		breakdownFilterManager.syncStore();
		updateState({ splitViewEnabled: splitValue });
	},

	'SN_KPI_DETAILS#BREAKDOWN_ELEMENT_FILTER_SET': ({
		action: {
			payload: { elementFilterSysId, breakdown },
		},
		state: {
			properties: { breakdownFilterManager },
		},
	}) => {
		breakdownFilterManager.toggleElementFilter(breakdown, elementFilterSysId);
	},

	'SN_KPI_DETAILS#BREAKDOWN_ELEMENT_SORT': ({
		action: {
			payload: { breakdownId, sortBy, sortDirection },
		},
		state: {
			properties: { breakdownFilterManager },
		},
	}) => {
		breakdownFilterManager.setBreakdownElementSort(
			breakdownId,
			sortBy,
			sortDirection
		);
		breakdownFilterManager.syncStore();
	},

	'SN_KPI_DETAILS#SEARCH_FILTERS': ({
		state: {
			properties: { breakdownFilterManager },
		},
		action: {
			payload: { searchKey },
		},
	}) => breakdownFilterManager.searchElements(searchKey),

	LIST_PAGE_UPDATED: ({
		action: {
			payload: { page },
		},
		updateState,
	}) => {
		updateState({ currentPage: page });
	},

	/**
	 * Toggles refresh chart property so that chart will referesh and load the new target line
	 */
	'SN_KPI_DETAILS#REFRESH_CHART_COMPONENT': ({
		state: { refreshChart },
		updateState,
	}) => {
		updateState({ refreshChart: !refreshChart });
	},

	/**
	 * Dispatches an action to fetch the scores
	 */
	'SN_KPI_DETAILS#UPDATE_SCORE': ({
		state: {
			properties: { indicator, selectedDate, domain, breakdownFilterManager },
		},
		dispatch,
	}) => {
		const appliedBreakdowns = breakdownFilterManager.getPublishedBreakdowns();
		let config = getConfig({}, 'indicator', indicator.sysId);
		if (selectedDate)
			config = getConfig(
				config,
				'date',
				getFormattedDate(selectedDate, PICKER_DATE_FORMAT, CHART_DATE_FORMAT)
			);
		if (appliedBreakdowns)
			config = getConfig(
				config,
				'breakdowns',
				Object.values(appliedBreakdowns).map((breakdown) => ({
					breakdownId: breakdown.sysId,
					elements: breakdown.elements.map((e) => e.sysId),
				}))
			);

		if (domain) config = getConfig(config, 'domain', domain);

		dispatch('SN_KPI_DETAILS#FETCH_ONLY_SCORE', {
			config: JSON.stringify(config),
		});
	},

	/**
	 * Target actions
	 */
	'SN_PA_TARGET_LIST_CONNECTED#TARGET_VALUE_EDIT_CLICKED': ({
		action: { payload },
		updateState,
	}) => {
		updateState({ targetValueClicked: payload.paTargetValue });
	},
	'SN_PA_TARGET_LIST_CONNECTED#NAVIGATE_BACK_CLICKED': ({ dispatch }) => {
		dispatch('TARGET_VALUE_CLICKED#CLEAR_VALUE');
	},
	'TARGET_VALUE_CLICKED#CLEAR_VALUE': ({ updateState }) => {
		updateState({ targetValueClicked: '' });
	},

	/**
	 * Refresh chart and update score on target create, update, delete action success
	 */
	'SN_PA_TARGET_CONNECTED#CREATE_TARGET_SUCCEEDED': ({
		action: { payload },
		state: {
			properties: {
				indicator,
				startDate,
				endDate,
				firstScoreDate,
				showTargets: showTargetsProp,
			},
			showTargets = showTargetsProp,
		},
		dispatch,
		updateState,
	}) => {
		const timeStampArray = getTargetMinMaxTimeStamp(payload.result);
		const momentTargetEndDate = moment.utc(timeStampArray[1]);
		const momentTargetStartDate = moment
			.utc(timeStampArray[0])
			.format(CHART_DATE_FORMAT);

		// Update the end date of the date range picker
		if (endDate < momentTargetEndDate.format(CHART_DATE_FORMAT))
			dispatch('SN_KPI_DETAILS#UPDATE_END_DATE', { date: momentTargetEndDate });

		// Update the start date of the date range picker
		if (
			firstScoreDate <= momentTargetStartDate &&
			momentTargetStartDate < startDate
		)
			dispatch('SN_KPI_DETAILS#UPDATE_START_DATE', {
				date: momentTargetStartDate,
			});

		// Refresh chart to show newly created target
		// Turn on the showTargets if it is turned off
		if (showTargets) dispatch('SN_KPI_DETAILS#REFRESH_CHART_COMPONENT');
		else {
			dispatch('SN_KPI_DETAILS#PREFERENCE_UPDATE', {
				data: { show_targets: true },
				uuid: indicator.sysId,
				client_type: 'workspace',
			});
			updateState({ showTargets: true });
		}

		// Dispatched to fetch the score for current selected date when new target is created
		dispatch('SN_KPI_DETAILS#UPDATE_SCORE');
	},

	'SN_PA_TARGET_CONNECTED#UPDATE_TARGET_SUCCEEDED': ({
		action: { payload },
		state: {
			properties: {
				indicator,
				startDate,
				endDate,
				firstScoreDate,
				showTargets: showTargetsProp,
			},
			showTargets = showTargetsProp,
		},
		dispatch,
		updateState,
	}) => {
		const timeStampArray = getTargetMinMaxTimeStamp(payload.result);
		const momentTargetEndDate = moment.utc(timeStampArray[1]);
		const momentTargetStartDate = moment
			.utc(timeStampArray[0])
			.format(CHART_DATE_FORMAT);

		// Update the end date of the date range picker
		if (endDate < momentTargetEndDate.format(CHART_DATE_FORMAT))
			dispatch('SN_KPI_DETAILS#UPDATE_END_DATE', { date: momentTargetEndDate });

		// Update the start date of the date range picker
		if (
			firstScoreDate <= momentTargetStartDate &&
			momentTargetStartDate < startDate
		)
			dispatch('SN_KPI_DETAILS#UPDATE_START_DATE', {
				date: momentTargetStartDate,
			});

		// Refresh chart to show the updated target
		// Turn on the showTargets if it is turned off
		if (showTargets) dispatch('SN_KPI_DETAILS#REFRESH_CHART_COMPONENT');
		else {
			dispatch('SN_KPI_DETAILS#PREFERENCE_UPDATE', {
				data: { show_targets: true },
				uuid: indicator.sysId,
				client_type: 'workspace',
			});
			updateState({ showTargets: true });
		}

		// Dispatched to fetch the score for current selected date when a target is updated
		dispatch('SN_KPI_DETAILS#UPDATE_SCORE');
	},

	'SN_PA_TARGET_CONNECTED#DELETE_TARGET_SUCCEEDED': ({
		state: {
			properties: { showTargets: showTargetsProp },
			showTargets = showTargetsProp,
		},
		dispatch,
	}) => {
		// Refresh chart to remove the deleted target
		if (showTargets) dispatch('SN_KPI_DETAILS#REFRESH_CHART_COMPONENT');

		// Dispatched to fetch the score for current selected date when a target is deleted
		dispatch('SN_KPI_DETAILS#UPDATE_SCORE');
	},

	/**
	 * Refresh chart on threshold create, update, delete action success
	 */
	[CREATE_THRESHOLD_SUCCEEDED]: ({
		state: {
			properties: { indicator, showThresholds: showThresholdsProp },
			showThresholds = showThresholdsProp,
		},
		dispatch,
		updateState,
	}) => {
		if (showThresholds) dispatch('SN_KPI_DETAILS#REFRESH_CHART_COMPONENT');
		else {
			dispatch('SN_KPI_DETAILS#PREFERENCE_UPDATE', {
				data: { show_thresholds: true },
				uuid: indicator.sysId,
				client_type: 'workspace',
			});
			updateState({ showThresholds: true });
		}
	},
	[UPDATE_THRESHOLD_SUCCEEDED]: ({
		state: {
			properties: { indicator, showThresholds: showThresholdsProp },
			showThresholds = showThresholdsProp,
		},
		dispatch,
		updateState,
	}) => {
		if (showThresholds) dispatch('SN_KPI_DETAILS#REFRESH_CHART_COMPONENT');
		else {
			dispatch('SN_KPI_DETAILS#PREFERENCE_UPDATE', {
				data: { show_thresholds: true },
				uuid: indicator.sysId,
				client_type: 'workspace',
			});
			updateState({ showThresholds: true });
		}
	},
	[DELETE_THRESHOLD_SUCCEEDED]: ({
		state: {
			properties: { showThresholds: showThresholdsProp },
			showThresholds = showThresholdsProp,
		},
		dispatch,
	}) => {
		if (showThresholds) dispatch('SN_KPI_DETAILS#REFRESH_CHART_COMPONENT');
	},

	/**
	 * Chart data fetch action succeeded
	 */
	'SN_VISUALIZATION#CHARTS_DATA_FETCH_SUCCEEDED': ({
		action: { payload },
		updateState,
	}) => {
		const chartPayload = get(payload, 'result.payload');
		const forecastData = get(chartPayload, 'forecastData', []);
		updateState({
			averageScore: get(chartPayload, 'statistics[0].avg_value'),
			hasNoForecastData: forecastData.length === 0,
			hasNoTargetData: get(chartPayload, 'targetsData', []).length === 0,
			hasNoThresholdData: get(chartPayload, 'thresholdData', []).length === 0,
			hasNoTrendLineData: get(chartPayload, 'trendlinesData', []).length === 0,
			hasNoCommentData: get(chartPayload, 'annotationsData', []).length === 0,
			forecastLastDataPoint:
				forecastData.length !== 0
					? forecastData[forecastData.length - 1].x
					: undefined,
		});
	},

	/**
	 * Handles the ClearAll action dispatched from chart options popover
	 */
	'NOW_BUTTON_BARE#CLICKED': ({
		action: {
			payload: { type },
			stopPropagation,
		},
		state: {
			properties: { indicator },
		},
		dispatch,
		updateState,
	}) => {
		if (type === 'clearAll') {
			stopPropagation();

			dispatch('SN_KPI_DETAILS#PREFERENCE_UPDATE', {
				data: {
					show_targets: false,
					show_thresholds: false,
					show_forecast: false,
					show_trend: false,
					show_comments: false,
					show_labels: false,
				},
				uuid: indicator.sysId,
				client_type: 'workspace',
			});

			updateState({
				showTargets: false,
				showThresholds: false,
				showForecast: false,
				showTrend: false,
				showComments: false,
				showLabels: false,
			});
		}
	},

	/**
	 * Handles the action dispatched from chart options popover
	 */
	'NOW_TOGGLE#CHECKED_SET': ({
		action: {
			payload: { type, value },
		},
		state: {
			properties: { indicator },
		},
		dispatch,
		updateState,
	}) => {
		switch (type) {
		case 'showTargets':
			dispatch('SN_KPI_DETAILS#PREFERENCE_UPDATE', {
				data: { show_targets: value },
				uuid: indicator.sysId,
				client_type: 'workspace',
			});
			updateState({ showTargets: value });
			break;

		case 'showThresholds':
			dispatch('SN_KPI_DETAILS#PREFERENCE_UPDATE', {
				data: { show_thresholds: value },
				uuid: indicator.sysId,
				client_type: 'workspace',
			});
			updateState({ showThresholds: value });
			break;

		case 'showForecast':
			dispatch('SN_KPI_DETAILS#PREFERENCE_UPDATE', {
				data: { show_forecast: value },
				uuid: indicator.sysId,
				client_type: 'workspace',
			});
			updateState({ showForecast: value });
			break;

		case 'showTrend':
			dispatch('SN_KPI_DETAILS#PREFERENCE_UPDATE', {
				data: { show_trend: value },
				uuid: indicator.sysId,
				client_type: 'workspace',
			});
			updateState({ showTrend: value });
			break;

		case 'showComments':
			dispatch('SN_KPI_DETAILS#PREFERENCE_UPDATE', {
				data: { show_comments: value },
				uuid: indicator.sysId,
				client_type: 'workspace',
			});
			updateState({ showComments: value });
			break;

		case 'showLabels':
			dispatch('SN_KPI_DETAILS#PREFERENCE_UPDATE', {
				data: { show_labels: value },
				uuid: indicator.sysId,
				client_type: 'workspace',
			});
			updateState({ showLabels: value });
			break;
		}
	},
};
