export const view = ({ properties: { selectedMenu, menuItems = [] } }) => {
	return (
		menuItems &&
		menuItems.map((menu, index) => (
			<sn-panel-menu-item
				key={menu.menuId}
				selected={selectedMenu && selectedMenu.menuId === menu.menuId}
				menu={menu}
				menu-id={menu.menuId}
				menu-index={index}
				menu-title={menu.menuTitle}
				icon-name={menu.iconName}
				badge-text={menu.badgeText}
				menu-index={index}
				focusable={
					(selectedMenu && selectedMenu.menuId === menu.menuId) ||
					((!selectedMenu || !selectedMenu.menuId) && index === 0)
						? true
						: false
				}
				disabled={menu.disabled}
			/>
		))
	);
};
