import { actionTypes } from '@servicenow/ui-core';
const { COMPONENT_PROPERTY_CHANGED } = actionTypes;

export const handlers = {
	[COMPONENT_PROPERTY_CHANGED]: ({
		action: {
			payload: { name, previousValue, value }
		},
		updateState
	}) => {
		if (name === 'selectedMenu') {
			updateState({
				pullFocusToClose:
					!!value && previousValue !== value,
				pullFocusToMenu:
					!value && previousValue.menuId
			});
		}
	},
	'SN_KPI_DETAILS#MENU_REF': ({ action: { payload: { menuId, ref } }, updateState}) => updateState({ [`ref-${menuId}`]: ref }),
	'SN_KPI_DETAILS#FOCUS_TO_MENU': ({
		state,
		action: { payload: { focusIndex } }}) => {
		const { properties: { menuItems }} = state;
		if (menuItems[focusIndex]) {
			state[`ref-${menuItems[focusIndex].menuId}`].focus();
		}
	},
	'SN_KPI_DETAILS#RESET_CLOSE_FOCUS': ({ updateState }) => updateState({ pullFocusToClose: false }),
	'SN_KPI_DETAILS#RESET_MENU_FOCUS': ({ updateState }) => updateState({ pullFocusToMenu: false })
};
