import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { view } from './score-details-view';
import styles from './score-details-styles.scss';

createCustomElement('sn-score-details', {
	renderer: {
		type: snabbdom
	},
	properties: {
		data: {
			default: {},
			reflect: false
		},
		selectedDate: {
			default: ''
		},
		scoreDate: {
			default: ''
		}
	},
	view,
	actions: {
		'NOW_RECORD_DATE_PICKER#VALUE_CHANGED': {
			private: false
		},
		'NOW_RECORD_DATE_PICKER#STAGED_VALUE_CHANGED': {
			private: false
		}
	},
	styles
});
