import '../../KPIDetailsHeader';
import { t } from 'sn-translate';
import INVALID_ACL_ICON from '../../../icons/no-acl.svg';

export const view = state => {
	const { indicator, formula, formulaComponents, tags, hasError } = state;

	const kpiDetails = {
		indicator,
		formulaInfo: {
			formula,
			formulaComponents
		},
		tags
	};
	return (
		<div className="ah__acl_error">
			{!hasError ? (
				<sn-kpi-details-header kpi-details={kpiDetails} />
			) : (
				undefined
			)}
			<div className="ah__acl_error--body">
				<img className="logo" src={INVALID_ACL_ICON} alt={t('Security issue, you need permission')} />
				<span className="info-primary">
					{t('You need permission to view this KPI.')}
				</span>
				<span className="info-secondary">
					{t('Contact your servicenow admin for permission.')}
				</span>
			</div>
		</div>
	);
};
