import get from 'lodash/get';
import { MAX_BREAKDOWN_LEVELS } from '../../constants';

export const searchResultsFound = ({ searchKey, breakdownFilterManager }) => {
	let isSearchResultsFound = false;
	let searchResults;
	if (searchKey) {
		searchResults = breakdownFilterManager.getElementsForSearchKey(searchKey);
		if (searchResults !== undefined) {
			if (Object.keys(searchResults).length === 0) {
				isSearchResultsFound = true;
			} else {
				let searchkeys = Object.keys(searchResults);
				searchkeys.map((x) => {
					if (searchResults[x] && searchResults[x].length === 0)
						isSearchResultsFound = true;
				});
			}
		}
	}
	return isSearchResultsFound;
};

export const breakdownInfo = ({ breakdowns = [], collectBreakdownMatrix, activeBreakdownCollapse, breakdownFilterManager }) => breakdowns.map((breakdown, index) => {
	let expanded = activeBreakdownCollapse === breakdown.sysId;

	if (breakdownFilterManager.hasSearchKey()) expanded = true;

	const fetchStatus = breakdownFilterManager.getElementFetchStatus(
		breakdown.sysId
	);
	let elements;
	let errorMessage;
	const previewBreakdownIds = Object.keys(breakdownFilterManager.getPreviewBreakdowns());
	// .map(breakdown => breakdown.sysId);

	const disableCollapse =
		previewBreakdownIds.indexOf(breakdown.sysId) === -1 &&
		(!collectBreakdownMatrix
			? previewBreakdownIds.length >= MAX_BREAKDOWN_LEVELS - 1
			: previewBreakdownIds.length >= MAX_BREAKDOWN_LEVELS);

	try {
		elements = breakdownFilterManager.getElementsForBreakdown(
			breakdown.sysId,
			expanded
		);
	} catch (error) {
		errorMessage = error.message;
	}

	if (
		(breakdownFilterManager.hasSearchKey() &&
			elements &&
			elements.length) ||
		!breakdownFilterManager.hasSearchKey()
	) {
		const selectedItems = breakdownFilterManager
			.getSelectedElements(breakdown.sysId)
			.map((element) => element.sysId);
		const elementFilter = breakdownFilterManager.getSelectedElementFilter(
			breakdown.sysId
		);
		const elementSort = breakdownFilterManager.getBreakdownElementSort(
			breakdown.sysId,
			elements
		);

		return {
			breakdown,
			...elementSort,
			expanded,
			elementFilter,
			selectedItems,
			elements,
			disableCollapse,
			fetchStatus,
			index,
			errorMessage
		};
	}

	return;
}).filter(breakdown => !!breakdown);

export const filterInfo = (previewBreakdowns) => Object.values(
	previewBreakdowns
).reduce((filtersInfo, breakdown) => {
	filtersInfo.previewBreakdownIds.push(breakdown.sysId);
	if (Array.isArray(breakdown.elements) && breakdown.elements.length) {
		filtersInfo.filters.push({
			name: breakdown.name,
			sysId: breakdown.sysId,
			selectionCount: breakdown.elements.length,
			breakdown,
		});
	}
	return filtersInfo;
}, { filters: [], previewBreakdownIds: [] });

const getMinMaxTimeStamp = (targetData = []) => {
	if (Array.isArray(targetData) && targetData.length === 0) return '';

	const length = targetData.length;
	let minMaxTimeStamp = [];

	// push minimum target date timestamp
	minMaxTimeStamp.push(targetData[0].x);

	// push maximum target date timestamp
	minMaxTimeStamp.push(
		targetData[length - 1].review_date
			? targetData[length - 1].review_date
			: targetData[length - 1].x
	);

	return minMaxTimeStamp;
};

/**
 *
 * @param {Array} targetData
 */
export const getTargetMinMaxTimeStamp = (data) => {
	if (!data) return '';

	const globalTargetResult = getMinMaxTimeStamp(get(data, 'global.data', []));
	const personalTargetResult = getMinMaxTimeStamp(get(data, 'personal.data', []));

	if (!globalTargetResult && !personalTargetResult) return '';
	else if (!personalTargetResult) return globalTargetResult;
	else if (!globalTargetResult) return personalTargetResult;
	else
		return [
			globalTargetResult[0] < personalTargetResult[0]
				? globalTargetResult[0]
				: personalTargetResult[0],
			globalTargetResult[1] > personalTargetResult[1]
				? globalTargetResult[1]
				: personalTargetResult[1],
		];
};
