/**
 * Add Key and Value to config
 * @param {Object} config
 * @param {String} key
 * @param {String} value
 *
 * @returns {Object} new config
 */
export function getConfig(config = {}, key, value) {
	const isValid = config && key && value;
	if (isValid) {
		if (key === 'date') {
			const processedValue = value.replace(/-/g, '');
			config[key] = processedValue;
		} else config[key] = value;
	}
	return config;
}

/**
 * Add Key and Value to config when
 * formatter function is passed
 * @param {Object} config
 * @param {String} key
 * @param {String} value
 * @param {Function} formatter
 *
 * @returns {Object} new config
 */
export function getConfigFormatted(config = {}, key, value, formatter) {
	const isValid = config && key && value;
	if (isValid) {
		if (formatter && typeof formatter === 'function')
			return formatter.call({}, { key, value }, config);

		return getConfig(config, key, value);
	}
	return config;
}