// @flow

import { type Breakdown, type Element } from '../common/types/AllTypes';
export class BreakdownStore {
	_breakdowns: Array<Breakdown>;
	breakdownMap: { [sysId: string]: Breakdown } = {};

	get breakdowns() {
		return this._breakdowns;
	}

	set breakdowns(breakdowns: Array<Breakdown>) {
		this._breakdowns = breakdowns;
		this.mapBreakdowns(breakdowns);
	}

	constructor(breakdowns: Array<Breakdown>) {
		this.breakdowns = breakdowns;
	}

	mapBreakdowns(breakdowns: Array<Breakdown>): void {
		Array.isArray(breakdowns) &&
			breakdowns.forEach(breakdown => {
				this.breakdownMap[breakdown.sysId] = breakdown;
			});
	}

	getElementsForBreakdown(breakdownId: string) {
		return this.breakdownMap[breakdownId] && this.breakdownMap[breakdownId].elements;
	}

	addElementsForBreakdown(breakdownId: string, elements: Array<Element>) {
		this.breakdownMap[breakdownId].elements = elements;
	}
}
