import '@servicenow/now-checkbox';
import '@servicenow/now-tooltip';
import { t } from 'sn-translate';
import '../TextHighlight';
import { getPointerStyle } from '../../helpers/changePointer';
import { FetchStatus } from '../../constants';

const getFilterOptions = breakdown => [
	{ id: 'none-selected', label: t('None') },
	...breakdown.elementFilters.map(filter => ({
		id: filter.sysId,
		label: filter.title,
		filter
	}))
];

const BREAKDOWN_ELEMENTS_LABEL = t('Breakdown element');
export const view = (state, { dispatch, updateState }) => {
	const {
		properties: {
			breakdown,
			elements,
			elementFilter,
			errorMessage,
			selectedItemsMap = {},
			expanded,
			disableCollapse = false,
			fetchStatus,
			sortBy,
			sortDirection,
			scoresAvailable,
			showMultiElementErrorMessage,
			searchKey,
			searchInProgress
		},
		collapse
	} = state;
	const sortByOptions = [{ id: 'name', label: t('Name') }];

	if (!disableCollapse && scoresAvailable) {
		sortByOptions.push({ id: 'score', label: t('Score') });
		sortByOptions.push({ id: 'absChange', label: t('Change') });
	}
	const containerClass = [
		'ah__breakdown--collapse',
		expanded ? 'expanded' : 'collapsed',
		disableCollapse ? 'disabled' : 'enabled'
	];

	const chevron = expanded ? 'chevron-up-outline' : 'chevron-down-outline';
	const hasElementFilters =
		breakdown && breakdown.elementFilters && breakdown.elementFilters.length;

	const setTargetRef = (ref, propName) =>
		setTimeout(() => updateState({ [propName]: ref }));
	const getAccordionElement = (element, index) => (
		<div
			className="breakdown-element"
			key={element.sysId || index}
			on-click={() =>
				!disableCollapse &&
				dispatch('SN_KPI_DETAILS#BREAKDOWN_ELEMENT_TOGGLE_SET', {
					breakdown,
					breakdownId: breakdown.sysId,
					element: element
				})
			}
		>
			<now-checkbox
				size="sm"
				manage-checked
				append-to-payload={{
					type: 'element-toggle',
					breakdown,
					element,
					breakdownId: breakdown.sysId,
					elementId: element.sysId
				}}
				on-focus={() => updateState({ focusFirst: index + 1 })}
				on-click={e => {
					e.preventDefault();
					e.stopPropagation();
				}}
				on-keyup={function (event) {
					const arrow = event.keyCode;
					if ([38, 40].indexOf(arrow) > -1) {
						event.preventDefault();
						event.stopPropagation();
						const nodes = event.target.parentElement.parentElement.querySelectorAll('now-checkbox');
						if (index + 1 < nodes.length || index - 1 >= 0)
							if (38 === arrow && nodes[index - 1]) nodes[index - 1].focus();
							else if (40 === arrow && nodes[index + 1]) nodes[index + 1].focus();
					} else if (37 === event.keyCode && collapse)
						collapse.focus();
				}}
				aria-label={`${element.name}, ${BREAKDOWN_ELEMENTS_LABEL}`}
				disabled={disableCollapse}
				checked={!!selectedItemsMap[element.sysId]}
			/>
			<div className="breakdown-element__container">
				<span
					className="breakdown-element__title"
					hook-insert={({ elm }) => setTargetRef(elm, `el-${element.sysId}`)}
					hook-destroy={() => setTargetRef(null)}
				>
					<sn-text-highlight text={element.name} highlight-text={searchKey} />
				</span>
				{disableCollapse && searchKey ? (
					''
				) : (
					<div className="breakdown-element__info">
						<span className="element-score" aria-label={t('Score {0}', [element.scoreFormatted])}>{element.scoreFormatted}</span>
						{(element.changeArrow !== 'steady' && (
							<div
								aria-label={element.changeArrow === 'up' ? t('Increase') : t('Decrease')}
								className={`change-pointer ${element.changeArrow}`}
								style={getPointerStyle(
									element.changeArrow,
									element.changeColor
								)}
							/>
						)) ||
								''}
						<span className="change-score" aria-label={t('Change score {0}', [element.absChangeFormatted])}>{element.absChangeFormatted}</span>
						{element.changePercent && (
							<span className="change-percent" aria-label={t('Change percent {0}', [element.changePercent])}>
									&#40;{element.changePercent}&#41;
							</span>
						)}
						{showMultiElementErrorMessage ? (
							<now-tooltip
								id={`ah-element-no-score-${element.sysId}`}
								target-ref={state[`el-${element.sysId}`]}
								content={t('Combined scores is not avaialble for this KPI.')}
								delay={100}
							/>
						) : ''}
					</div>
				)}
			</div>
		</div>
	);

	const getElements = elements => {
		if (searchKey && searchKey.length > 2)
			return elements.map((element, index) => getAccordionElement(element, index));
		switch (fetchStatus) {
		case FetchStatus.SUCCESS: {
			if (!elements.length)
				return (
					<div className="ah__elements--no-elements-message">
						<span role="alert" aria-live="polite">
							{hasElementFilters && elementFilter && elementFilter !== 'none-selected'? t(
								'No {0} elements available for this refine by option.', [(breakdown.name || '').toLowerCase()]
							) : t('No {0} elements available here.', [(breakdown.name || '').toLowerCase()])}
						</span>
					</div>
				);
			return elements.map((element, index) => getAccordionElement(element, index));
		}
		case FetchStatus.IN_PROGRESS: {
			return (
				<div className="ah__elements--status-message">
					<now-loader size="md"></now-loader>
					<span role="alert" aria-live="polite">
						{t('Fetching elements...')}
					</span>
				</div>
			);
		}
		default:
			if (!searchInProgress)
				return (
					<div className="ah__elements--no-elements-message">
						<span role="alert" aria-live="polite">
							{t(
								'No {0} elements available here.', [breakdown.name.toLowerCase()]
							)}
						</span>
					</div>
				);
		}
		return <div/>;
	};

	return (
		<div className={containerClass.join(' ')}>
			{disableCollapse ? (
				<div className="ah__breakdown--collapse-header">
					<now-icon variant="sm" icon={chevron} />
					<h6 className="ah__breakdown--collapse-title">{breakdown.name}</h6>
					{(Object.keys(selectedItemsMap).length && (
						<div className="ah__breakdown--collapse-count">
							<span>{Object.keys(selectedItemsMap).length}</span>
						</div>
					)) ||
						''}
				</div>
			) : (
				<now-collapse-trigger
					tabIndex="0"
					controls={`breakdown-${breakdown.sysId}`}
					hook-insert={({ elm }) => updateState({ collapse: elm })}
					on-keydown={e => {
						let toggle = false;
						if ([/*enter*/13, /*space*/ 32, /*left*/ 37, /*up 38,*/ /*right*/ 39 /*down 40*/].indexOf(e.keyCode) > -1) {
							e.preventDefault();
							if (37 === e.keyCode) {
								if (expanded) toggle = true;
							} else if (39 === e.keyCode) {
								if (!expanded) toggle = true;
							} else if ([13, 32].indexOf(e.keyCode) > -1) {
								toggle = true;
							}
						}

						if (toggle)
							dispatch('SN_KPI_DETAILS#BREAKDOWN_COLLAPSE_SET', {
								breakdown: breakdown.sysId
							});
					}}
					on-click={() => {
						if (!searchKey || searchKey.length < 3)
							dispatch('SN_KPI_DETAILS#BREAKDOWN_COLLAPSE_SET', {
								breakdown: breakdown.sysId
							});
					}}
				>
					<div className="ah__breakdown--collapse-header">
						<now-icon variant="sm" icon={chevron} />
						<h6 className="ah__breakdown--collapse-title">{breakdown.name}</h6>
						{(Object.keys(selectedItemsMap).length && (
							<div className="ah__breakdown--collapse-count">
								<span>{Object.keys(selectedItemsMap).length}</span>
							</div>
						)) ||
								''}
					</div>
				</now-collapse-trigger>
			)}
			{(
				<now-collapse
					id={`breakdown-${breakdown.sysId}`}
					manage-expanded
					expanded={expanded}
				>
					{errorMessage ? (
						<div className="ah__elements--status-message text">{errorMessage}</div>
					) : (
						<div className="ah__breakdown--collapse-container">
							<div
								className={[
									'ah__breakdown--collapse-actions',
									hasElementFilters ? 'has-filters' : 'no-filters'
								].join(' ')}
							>
								{(hasElementFilters ?
									<div className="ah__breakdown--collapse-actions-filter">
										<label className="ah__breakdown--collapse-actions-title">
											{t('Refine by')}
										</label>
										<now-dropdown
											id={`element-filter-${breakdown.sysId}`}
											bare
											items={getFilterOptions(breakdown)}
											selected-items={[elementFilter || 'none-selected']}
											size="sm"
											manage-selected
											variant="secondary"
											append-to-payload={{
												type: 'filter',
												breakdownId: breakdown.sysId,
												elementFilter
											}}
											disabled={searchKey && searchKey.length > 2}
										></now-dropdown>
									</div>
									: '')}
								<div className="ah__breakdown--collapse-actions-sort">
									<label className="ah__breakdown--collapse-actions-title">
										{t('Sort by')}
									</label>
									<div className="actions-sort-dropdown">
										<now-dropdown
											bare
											items={sortByOptions}
											selected-items={[sortBy]}
											size="sm"
											variant="secondary"
											append-to-payload={{ type: 'sort' }}
											config-aria={{
												'aria-label': `${t('Sort by')}`
											}}
										></now-dropdown>
										<now-button-iconic
											bare
											size="md"
											hide-padding
											on-click={() =>
												dispatch('SN_KPI_DETAILS#BREAKDOWN_ELEMENT_SORT', {
													sortDirection: !sortDirection,
													sortBy,
													breakdownId: breakdown.sysId
												})
											}
											icon={
												sortDirection
													? 'sort-ascending-outline'
													: 'sort-descending-outline'
											}
											config-aria={{
												'aria-label': sortDirection
													? t('Sort ascending')
													: t('Sort descending')
											}}
										/>
									</div>
								</div>
							</div>
							<div className="ah__breakdown--collapse-content">
								{getElements(elements)}
							</div>
						</div>
					)}
				</now-collapse>
			) || ''}
		</div>
	);
};
