import { createHttpEffect } from '@servicenow/ui-effect-http';
import { get } from 'lodash';
import { t } from 'sn-translate';

import ERROR_FETCHING_RECORDS_ICON from '../icons/interrupted-state.svg';

const getErrorData = () => {
	return {
		primary: t('Unable to load records'),
		secondary: t('Try refreshing the page.'),
		logo: ERROR_FETCHING_RECORDS_ICON,
		logoAltMessage: t('Unable to load records')
	};
};

const fetchSysids = createHttpEffect(
	'api/now/v2/pa/detailed_scorecard/snapshot/recordsQuery',
	{
		method: 'POST',
		dataParam: 'data',
		successActionType: 'SN_KPI_DETAILS#SYSIDS_FETCH_SUCCEEDED',
		errorActionType: 'SN_KPI_DETAILS#SYSIDS_FETCH_FAILED'
	}
);

export const recordsHandlers = {
	'SN_KPI_DETAILS#SYSIDS_FETCH_REQUESTED': fetchSysids,
	'SN_KPI_DETAILS#SYSIDS_FETCH_SUCCEEDED': coeffects => {
		const {
			action: {
				payload,
				payload: { result }
			},
			updateState
		} = coeffects;
		const errors = get(payload, 'errors');
		if (errors && errors.length !== 0) {
			const recordListError = getErrorData();
			updateState({ records: {}, recordListError });
		} else updateState({ records: result, recordListError: {} });
	},
	'SN_KPI_DETAILS#SYSIDS_FETCH_FAILED': ({ updateState }) => {
		const recordListError = getErrorData();
		updateState({ records: {}, recordListError });
	},
};
