import { createCustomElement } from '@seismic/core';
import snabbdom from '@seismic/snabbdom-renderer';
import { view } from './text-highlight-view';
import styles from './text-highlight-view.scss';

createCustomElement('sn-text-highlight', {
	renderer: {
		type: snabbdom
	},
	view,
	properties: {
		text: {
			default: '',
			reflect: true
		},
		computedText: {
			computed({ properties: { text } }) {
				return text && typeof text === 'string'
					? text.toLowerCase()
					: text;
			}
		},
		highlightText: {
			default: '',
			reflect: true
		},
		computedHighlightText: {
			computed({ properties: { highlightText } }) {
				return highlightText && typeof highlightText === 'string'
					? highlightText.toLowerCase()
					: highlightText;
			}
		}
	},
	styles
});
