import moment from 'moment';
import { userLanguage } from './common';

const locale = userLanguage();

/**
 * Converts the date passed into new format specified
 *
 * @param {String} date
 * @param {String} format
 * @param {String} newFormat
 *
 * @returns {String} Date with new format
 */
export function getFormattedDate(date, format, newFormat) {
	if (!date || !format || !newFormat) return;

	moment.locale(locale);
	const newDate = moment(date, format).format(newFormat);

	return newDate;
}

/**
 * Gives the number of days between start and end date
 * @param {String} startDate
 * @param {String} endDate
 * @param {String} format
 *
 * @returns {Integer} Range in Days
 */
export function getDateRange(startDate, endDate, format) {
	if (!startDate || !endDate || !format) return;

	moment.locale(locale);

	const startDateInTime = moment(startDate, format).valueOf();
	const endDateInTime = moment(endDate, format).valueOf();

	const rangeInDays = (endDateInTime - startDateInTime) / (1000 * 3600 * 24);
	return rangeInDays;
}

/**
 * Gives the new date number of days before endDate based on range passed
 * @param {String} startDate
 * @param {Integer} range
 * @param {String} format
 *
 * @returns {String} New Date
 */
export function getNewStartDate(endDate, range, format) {
	if (!endDate || !format || typeof range !== 'number') return;

	moment.locale(locale);
	const newStartDate = moment(endDate, format)
		.subtract(range, 'days')
		.format(format);

	return newStartDate;
}

/**
 * Converts date in miliseconds to YYYY-MM-DD format
 * @param {integer} x integer representing the number of milliseconds since 1 January 1970, 00:00:00 UTC
 *
 * @returns {String} Date in YYYY-MM-DD format
 */
export const getProcessedDate = (x) => {
	let date = new Date();
	date.setTime(x);
	let DD = date.getDate().toString();
	let MM = (date.getMonth() + 1).toString();
	let YYYY = date.getFullYear().toString();

	DD = DD.padStart(2, '0');
	MM = MM.padStart(2, '0');

	return YYYY + '-' + MM + '-' + DD;
};

/**
 * Returns true if the selected date is out of the date range
 * @param {String} firstScoreDate Format YYYY-MM-DD
 * @param {String} lastScoreDate Format YYYY-MM-DD
 * @param {String} selectedDate Format YYYY-MM-DD
 *
 * @returns {Boolean}
 */
export function getParsedDate(firstScoreDate, lastScoreDate, selectedDate) {
	let flag = false;
	let parsedFirstDate = Date.parse(firstScoreDate);
	let parsedLastDate = Date.parse(lastScoreDate);
	let parsedSelectedDate = Date.parse(selectedDate);
	if (
		parsedSelectedDate < parsedFirstDate ||
		parsedSelectedDate > parsedLastDate
	)
		flag = true;
	return flag;
}

/**
 * Returns true if date passed has format YYYY-MM-DD and is a valid date
 * @param {String} date
 *
 * @returns {Boolean}
 */
export function isValidDate(date) {
	if (!date || typeof date !== 'string') return false;

	let dateSplit = date.split('-');

	/**
	 * checks for the validity of passed date's format
	 */
	if (
		dateSplit.length === 3 &&
		dateSplit[0].length === 4 &&
		dateSplit[1].length === 2 &&
		dateSplit[2].length === 2
	) {
		/**
		 *  check for the validity of the passed date's year, month and date
		 */
		const d = new Date(dateSplit[0], dateSplit[1] - 1, dateSplit[2]);
		if (
			d.getFullYear() != dateSplit[0] ||
			d.getMonth() != dateSplit[1] - 1 ||
			d.getDate() != dateSplit[2]
		)
			return false;

		return true;
	} else return false;
}
