import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { view } from './chart-container-view';
import styles from './chart-container-styles.scss';

createCustomElement('sn-chart-container', {
	renderer: {
		type: snabbdom,
	},
	properties: {
		startDate: {
			default: '',
		},
		endDate: {
			default: '',
		},
		chartLineColor: {
			default: '',
		},
		chartColorScheme: {
			default: [],
		},
		firstScoreDate: {
			default: undefined,
		},
		lastScoreDate: {
			default: undefined,
		},
		showRecords: {
			default: false,
		},
		seriesDisplayType: {},
		showSignals: {
			default: false,
		},
		refreshChart: {
			default: false,
		},
		targetValueClicked: {},
		seriesDataConfig: {},
		derivedSeries: {
			default: {},
		},
		forecastLastDataPoint: {},
		computedSeriesDataConfig: {
			computed({ properties: { seriesDataConfig } }) {
				return seriesDataConfig
					? JSON.parse(seriesDataConfig)
					: seriesDataConfig;
			},
		},
	},
	view,
	styles,
	actionHandlers: {
		'SN_KPI_DETAILS#UPDATE_END_DATE_FLAG': ({
			action: { payload },
			updateState,
		}) => updateState({ endDateUpdatedFlag: payload.value }),
	},
});
