import { actionTypes } from '@servicenow/ui-core';
import isEqual from 'lodash/isEqual';
import get from 'lodash/get';
import { getConfigFormatted } from '../helpers/get-config';
import { isValidDate } from '../helpers/dateUtil';
import { getElementIds } from '../helpers/elements';
import { initialState } from '../state';
import { parseBreakdowns } from '../helpers/parse-breakdowns';

const { COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED } = actionTypes;

export const handleScorecardFetchRequest = ({
	state: {
		properties: { indicatorSysid, scoreDate, breakdowns, domain },
		breakdownFilterManager,
	},
	/* action, */
	dispatch,
}) => {
	let config = getConfigFormatted({}, 'indicator', indicatorSysid);

	if (scoreDate && isValidDate(scoreDate))
		config = getConfigFormatted(config, 'date', scoreDate);

	if (typeof breakdowns === 'string')
		breakdowns = parseBreakdowns(breakdowns);
	if (breakdowns && Array.isArray(breakdowns) && breakdowns.length) {
		const newBreakdownFormat = get(breakdowns, '[0].sys_id');

		if (newBreakdownFormat)
			breakdowns = breakdowns.map((breakdown) => ({
				sysId: breakdown.sys_id,
				elements: breakdown.element_sys_ids,
			}));

		config = getConfigFormatted(
			config,
			'breakdowns',
			breakdowns.map((breakdown) => ({
				breakdown_id: breakdown.sysId,
				elements: getElementIds(breakdown.elements),
			}))
		);
	}

	if (domain) config = getConfigFormatted(config, 'domain', domain);

	dispatch(
		'SN_KPI_DETAILS#FETCH_DETAILED_SCORECARD',
		{
			config: JSON.stringify(config),
		},
		{
			init: true,
		}
	);
	breakdownFilterManager.setDispatcher(dispatch);
	breakdownFilterManager.initSelection(breakdowns || []);
};

const handleBootstrap = (coeffects) => {
	coeffects.dispatch('SN_KPI_DETAILS#INIT_BOOTSTRAP');
};

export const lifeCycleHandlers = {
	[COMPONENT_BOOTSTRAPPED]: handleBootstrap,
	[COMPONENT_PROPERTY_CHANGED]: (...args) => {
		const [coeffects] = args;
		const {
			action: {
				payload: { previousValue, value },
			},
			state: { breakdownFilterManager },
		} = coeffects;

		/**
		 * On change of component's property, state should
		 * reset to initial state
		 */
		if (!isEqual(previousValue, value))
			coeffects.updateState({ ...initialState });

		breakdownFilterManager.resetBreakdowns();
		handleScorecardFetchRequest(...args);
	},
};
