import { SERIES_DISPLAY_TYPE } from '../../constants';
import { actionTypes } from '@servicenow/ui-core';
const { COMPONENT_PROPERTY_CHANGED } = actionTypes;

export const handlers = {
	FETCH_BREAKDOWN_ELEMENTS: ({
		dispatch,
		action: {
			payload: { breakdownId, elementFilter },
		},
	}) => {
		dispatch('SN_KPI_DETAILS#FETCH_BREAKDOWN_ELEMENT', {
			breakdownId,
			elementFilter,
			breakdownsSelected: [],
		});
	},
	'NOW_CHECKBOX#CHECKED_SET': ({
		action: {
			payload: { type, value },
		},
		updateState,
	}) => {
		if (type === SERIES_DISPLAY_TYPE) {
			updateState({ splitViewPreview: value });
		}
	},
	'SN_KPI_DETAILS#SET_SERIES_DISPLAY_TYPE': ({
		action: {
			payload: { value },
		},
		updateState,
	}) => {
		updateState({ splitViewPreview: value });
	},

	/**
	 * Handles the focus behavior just after the rendering of filter panel view and focuses on the search input field
	 * present in the Thresholds form
	 */
	[COMPONENT_PROPERTY_CHANGED]: ({
		state: { searchInputRef },
		action: { payload: { name, value, previousValue } }
	}) => {
		if (searchInputRef && name === 'isSelected' && value !== previousValue && value === true)
			searchInputRef.focus();
	},
};
