import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';
import { PANEL_MENU_ITEM_SELECTED_SET } from '../PanelMenu/panelMenuConstants';

export const view = (
	{
		properties: {
			iconName,
			selected,
			disabled,
			menu,
			menu: { menuTitle, tooltip },
			badgeText,
			menuIndex,
			focusable,
		},
	},
	{ dispatch }
) => {
	const classNames = ['ah__panel-menu--badge'];
	if (typeof badgeText === 'object') classNames.push('no-default');
	return (
		<Fragment>
			<slot
				on-click={() =>
					dispatch(PANEL_MENU_ITEM_SELECTED_SET, { menu, index: menuIndex })
				}
			>
				<div className="ah__panel-menu--container">
					{(badgeText && (
						<div className={classNames.join(' ')}>
							{typeof badgeText !== 'object' ? (
								<span>{badgeText}</span>
							) : (
								badgeText
							)}
						</div>
					)) ||
						''}
					<now-button-stateful
						on-keyup={(e) => {
							if ([/*up*/ 38, /*down*/ 40].indexOf(e.keyCode) > -1) {
								e.preventDefault();
								if (e.keyCode === 38)
									dispatch('SN_KPI_DETAILS#FOCUS_TO_MENU', {
										focusIndex: menuIndex - 1,
									});
								else if (e.keyCode === 40)
									dispatch('SN_KPI_DETAILS#FOCUS_TO_MENU', {
										focusIndex: menuIndex + 1,
									});
							}
						}}
						hook-insert={({ elm }) => {
							if (menuIndex === 0)
								dispatch('SN_KPI_DETAILS#SIDE_NAV_REF', { sideNavRef: elm });
							dispatch('SN_KPI_DETAILS#MENU_REF', {
								menuIndex,
								menuId: menu.menuId,
								ref: elm,
							});
						}}
						config-role="button"
						config-aria={{
							'aria-label': `${tooltip || menuTitle} ${t('side panel')}`,
						}}
						icon={iconName}
						size="lg"
						bare
						tabindex={focusable ? undefined : -1}
						tooltip-content={tooltip || menuTitle}
						manage-selected
						selected={selected}
						disabled={disabled}
						variant="secondary-highlighted"
					/>
				</div>
			</slot>
		</Fragment>
	);
};
