import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import focusBehavior from '@servicenow/behavior-focus';
import styles from './common-styles.scss';

import 'sn-component-visualization';

import { initialState } from './state';
import { actionHandlers } from './actions/action-handlers';
import { lifeCycleHandlers } from './actions/life-cycle-handlers';
import { filterManagerHandlers } from './actions/filter-manager-handlers';
import { updateScoreHandlers } from './actions/update-score-action-handlers';
import { recordsHandlers } from './actions/records-action-handler';
import { signalPanelConfigHandlers } from './actions/signal-panel-config-handlers';
import { MainView } from './views/main-view';

createCustomElement('sn-kpi-details', {
	renderer: { type: snabbdom },
	view: MainView,
	properties: {
		indicatorSysid: {
			reflect: true
		},
		panel: {
			schema: { type: 'string' }
		},
		scoreDate: {
			default: '',
			reflect: true
		},
		breakdowns: {},
		domain: {
			reflect: true
		},
		aggregate: {
			reflect: true
		},
		user: {
			default: '@context.session.user'
		}
	},
	styles,
	initialState,
	actionHandlers: {
		...actionHandlers,
		...lifeCycleHandlers,
		...filterManagerHandlers,
		...updateScoreHandlers,
		...recordsHandlers,
		...signalPanelConfigHandlers,
	},
	behaviors: [
		{
			behavior: focusBehavior,
			options: {
				focus: function(host) {
					requestAnimationFrame(() => {
						setTimeout(() => {
							// put the focus back on the first interactive element in kpi details
							// first interactive element resides inside the details-header
							const header = host.shadowRoot.querySelector('sn-kpi-details-header');

							if (header) header.focus();
						}, 0);
					});
				}
			}
		}],
});
