import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import focusBehavior from '@servicenow/behavior-focus';

import { view } from './kpi-details-header-view';
import styles from './kpi-details-header-styles.scss';
import { actionHandlers } from './actions/action-handlers';

createCustomElement('sn-kpi-details-header', {
	renderer: {
		type: snabbdom
	},
	view,
	properties: {
		kpiDetails: {
			default: {}
		},
		scoreInfo: {
			default: {}
		},
		selectedDate: {
			default: ''
		},
		lastScoreDate: {
			default: ''
		},
		firstScoreDate: {
			default: ''
		},
		scoreDate: {
			default: ''
		},
		aggregate: {
			default: ''
		},
		isPremium: {
			default: false
		},
		prominEnabled: {
			default: false
		},
		breakdownFilterManager: {},
	},
	styles,
	actionHandlers,
	behaviors: [
		{ 
			behavior: focusBehavior,
			options: {
				focus: function(host) {
					requestAnimationFrame(() => {
						setTimeout(() => {
							const btn = host.shadowRoot.querySelector('now-popover').querySelector('now-button-iconic');
							if (btn) btn.focus();
						}, 0);
					});
				}
			} 
		}
	]
});
