import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import get from 'lodash/get';
import moment from 'moment';
import { t } from 'sn-translate';
import {
	CHART_DATE_FORMAT,
	TARGET_DATE_FORMAT,
	SERIES_DISPLAY_TYPE_SPLIT,
} from '../../constants';
import { getHexColor } from '../../helpers/colorUtil';

export const view = (
	{
		properties: {
			startDate,
			endDate,
			chartLineColor,
			chartColorScheme,
			firstScoreDate,
			lastScoreDate,
			showRecords,
			seriesDisplayType,
			showSignals,
			refreshChart,
			targetValueClicked,
			derivedSeries,
			forecastLastDataPoint,
			computedSeriesDataConfig,
		},
		endDateUpdatedFlag, // flag to ensure we update the endDate only once when forecast is turned on
	},
	{ dispatch }
) => {
	const containerClass = ['ah__chart-container'];

	const isBreakdownsApplied =
		computedSeriesDataConfig &&
		computedSeriesDataConfig.uuid &&
		computedSeriesDataConfig.uuid.breakdowns;

	const showDerivedSeries = !showSignals;

	let seriesDataConfig = [];
	const timeSeriesConfig = {
		type: 'timeseries',
		start_time: startDate,
		end_time: endDate,
		source_type: 'analytics',
		include_scores: true,
		include_statistics: true,
		include_targets: derivedSeries.showTargets && showDerivedSeries,
		include_target_review_date: derivedSeries.showTargets && showDerivedSeries,
		include_thresholds: derivedSeries.showThresholds && showDerivedSeries,
		include_comments: derivedSeries.showComments && showDerivedSeries,
		include_trend: derivedSeries.showTrend && showDerivedSeries,
		include_forecast: derivedSeries.showForecast && showDerivedSeries,
		include_forecast_range: derivedSeries.showForecast && showDerivedSeries,
		include_signals: showSignals,
		uuid: computedSeriesDataConfig.uuid,
		number_of_groups: 'all',
		refreshChart,
	};

	if (
		Array.isArray(computedSeriesDataConfig.uuid.breakdowns) &&
		seriesDisplayType === SERIES_DISPLAY_TYPE_SPLIT &&
		isBreakdownsApplied
	) {
		for (const breakdown of computedSeriesDataConfig.uuid.breakdowns) {
			if (get(breakdown, 'element_sys_ids', []).length > 1) {
				seriesDataConfig.push({
					...timeSeriesConfig,
					group_by: breakdown.sys_id,
				});
				break;
			}
		}
	} else seriesDataConfig.push(timeSeriesConfig);

	const startDateConfigLabel = t('Start Date');
	const reviewDateConfigLabel = t('Review Date');

	let styleConfig = {
		width: 'auto',
		height: !showRecords ? 'auto' : 125,
		hideXGridlines: true,
		yAxisConfig: {
			orientation: 'right',
		},
		registerPosition: !showRecords ? 'top' : 'left',
		registerConfig: {
			justifyItems: 'start',
		},
		datasetConfig: {
			data_: {
				colorPalette:
					seriesDisplayType === SERIES_DISPLAY_TYPE_SPLIT
						? chartColorScheme
						: [getHexColor(chartLineColor)],
				labelTruncation: 60,
				strokeWidth: '2px',
			},
		},
		showDataLabels: derivedSeries.showLabels,
		dataLabelConfig: {
			type: 'interval',
		},
		cursorConfig: {
			hideYLines: true,
		},
	};

	// Annotation Data. To be shown only when we are editing an existing target
	if (derivedSeries.showTargets && showDerivedSeries && targetValueClicked) {
		styleConfig.annotationsData = [];

		// Annotation styling
		styleConfig.annotationsConfig = {
			[startDateConfigLabel]: {
				symbol: 'circle',
			},
			[reviewDateConfigLabel]: {
				symbol: 'diamond',
			},
		};

		let date = moment.utc(targetValueClicked.target_at, TARGET_DATE_FORMAT);
		let newStartDate;

		if (firstScoreDate <= date.format(CHART_DATE_FORMAT)) {
			newStartDate = date.format(CHART_DATE_FORMAT);
			styleConfig.annotationsData.push(
				// Data for start date
				{
					x: date.valueOf(),
					header: startDateConfigLabel,
					message: t('Target {0}', targetValueClicked.value),
					type: startDateConfigLabel,
				}
			);
		}

		// Data for review date
		if (targetValueClicked.target_review) {
			date = moment.utc(targetValueClicked.target_review, TARGET_DATE_FORMAT);
			if (firstScoreDate <= date.format(CHART_DATE_FORMAT))
				styleConfig.annotationsData.push({
					x: date.valueOf(),
					header: reviewDateConfigLabel,
					message:
						date.format(CHART_DATE_FORMAT) <= lastScoreDate
							? t('Target {0}', targetValueClicked.value)
							: '- -',
					type: reviewDateConfigLabel,
				});
		}

		if (endDate <= date.format(CHART_DATE_FORMAT))
			dispatch('SN_KPI_DETAILS#UPDATE_END_DATE', { date: date });

		if (newStartDate && newStartDate < startDate)
			dispatch('SN_KPI_DETAILS#UPDATE_START_DATE', { date: newStartDate });
	}

	/**
	 * Updating the end date to adjust to the last data point of the forecast
	 */
	if (
		derivedSeries.showForecast &&
		!endDateUpdatedFlag &&
		forecastLastDataPoint
	) {
		const momentDate = moment.utc(parseInt(forecastLastDataPoint));
		dispatch('SN_KPI_DETAILS#UPDATE_END_DATE_FLAG', { value: true });
		if (endDate < momentDate.format(CHART_DATE_FORMAT))
			dispatch('SN_KPI_DETAILS#UPDATE_END_DATE', { date: momentDate });
	} else if (!derivedSeries.showForecast && endDateUpdatedFlag)
		dispatch('SN_KPI_DETAILS#UPDATE_END_DATE_FLAG', { value: false });

	if (showSignals) {
		styleConfig = {
			...styleConfig,
			showTooltip: false,
			cursorConfig: {
				hideXLine: false,
				hideYLines: true,
				hideMarkers: true,
			},
		};
	}

	return (
		<Fragment>
			<div className={containerClass.join(' ')}>
				{startDate && endDate && (
					<sn-component-visualization
						tabIndex="0"
						hook-insert={({ elm }) =>
							dispatch('SN_KPI_DETAILS#VISUALIZATION_REF', {
								visualizationRef: elm,
							})
						}
						chartType="line"
						data_configurations={seriesDataConfig}
						styleConfigurations={styleConfig}
						auto-click-behavior
					/>
				)}
			</div>
		</Fragment>
	);
};
