import get from 'lodash/get';
export function parsePreferences(preferenceArray) {
	return Object.assign(
		{},
		...preferenceArray.map((preference) => {
			return { [preference.name]: preference.value };
		})
	);
}

export function isTrue(attr) {
	return attr === 'true';
}

export function shallowEquals(obj1, obj2) {
	if (!obj1 || !obj2) return obj1 === obj2;
	for (let k in obj1) {
		if (obj1[k] !== obj2[k]) return false;
	}
	return true;
}

export function buildUuidString(
	indicatorSysid,
	appliedBreakdown,
	domain,
	aggregate
) {
	if (!indicatorSysid) return '';

	let uuid = [];
	uuid.push(indicatorSysid);
	if (Object.keys(appliedBreakdown).length > 0) {
		Object.values(appliedBreakdown).map((breakdown) => {
			if (breakdown.elements && breakdown.elements.length > 0)
				uuid.push(breakdown.sysId, breakdown.elements[0].sysId);
		});
	}
	if (aggregate) uuid.push(aggregate);

	let UUID = uuid.join(':');
	if (domain) {
		return `${UUID};${domain}`;
	}
	return UUID;
}

export function buildQueryString(sysId, appliedBreakdown, domain, aggregate) {
	let query = [
		'target.ownerISEMPTY^ORtarget.ownerDYNAMIC90d1921e5f510100a9ad2572f2b477fe',
	];
	if (sysId) query.push(`target.indicator=${sysId}`);

	const breakdowns = Object.values(appliedBreakdown);

	const breakdownLevel1 = get(breakdowns, '[0].sysId', 'NULL');
	const breakdownLevel2 = get(breakdowns, '[1].sysId', 'NULL');

	const elementLevel1 = get(breakdowns, '[0].elements[0].sysId', 'NULL');
	const elementLevel2 = get(breakdowns, '[1].elements[0].sysId', 'NULL');

	query.push(`target.breakdown=${breakdownLevel1}`);
	query.push(`target.breakdown_level2=${breakdownLevel2}`);

	query.push(`target.element=${elementLevel1}`);
	query.push('ORtarget.any_element=true');
	query.push(`target.element_level2=${elementLevel2}`);
	query.push('ORtarget.any_element=true');

	if (domain) query.push(`target.domain=${domain}`);
	if (aggregate) query.push(`target.aggregate=${aggregate}`);
	
	query.push('ORDERBYDESCtarget_at');

	return query.join('^');
}
