// @flow
import { t } from 'sn-translate';
import { BreakdownStore } from '../BreakdownStore';
import { sortByProperty } from '../../helpers/sortUtil';
import { cloneDeep, debounce, difference, intersection } from 'lodash';
import {
	type AppliedBreakdown,
	type AppliedBreakdowns,
	type Breakdown,
	type BreakdownStoreType,
	type Element
} from '../../common/types/AllTypes';
import { FetchStatus } from '../../constants';
import { fromElementIds } from '../../helpers/elements';

export function getBreakdownFilterManager(
	dispatcher: (actionType: string, payload: any) => void
) {
	dispatcher = function () {
		throw new Error('Dispatcher needs to be initialized');
	};

	let store: BreakdownStoreType = new BreakdownStore([]);
	let appliedBreakdowns: AppliedBreakdowns = {};
	let appliedBreakdownFilter: {
		[breakdownId: string]: string
	} = {};
	let appliedBreakdownSort: {
		[breakdownId: string]: {
			sortBy: string,
			sortDirection: boolean,
			scoresAvailable?: boolean
		}
	} = {};
	let previewBreakdowns: {
		[breakdownId: string]: Object
	} = {};
	let fetchStatus: { [breakdownId: string]: string } = {};
	let appliedSearchKey: string;
	let hasSelectionInit = false;
	let excludedCombinations = {};
	const searchResults: { [key: string]: any } = {};

	class BreakdownFilterManager {
		lastUpdated: number;
		constructor() {
			this.lastUpdated = new Date().getTime();
		}

		resetBreakdowns = () => {
			store = new BreakdownStore([]);
			appliedBreakdowns = {};
			appliedBreakdownFilter = {};
			appliedBreakdownSort = {};
			previewBreakdowns = {};
			excludedCombinations = {};
			fetchStatus = {};
			this.syncStore();
		}

		initSelection = (breakdowns: Array<AppliedBreakdown>) => {
			if (Array.isArray(breakdowns) && breakdowns.length) {
				this.setAppliedBreakdowns(breakdowns);
				hasSelectionInit = true;
				this.syncStore();
			}
		}

		initBreakdowns = (breakdowns: Array<Breakdown>) => {
			store.breakdowns = cloneDeep(breakdowns);

			// update applied breakdowns as well
			this.setAppliedBreakdowns(breakdowns.filter(breakdown => appliedBreakdowns[breakdown.sysId]));
		}

		setAppliedBreakdowns = (breakdowns: Array<Breakdown>) => {
			if (!Array.isArray(breakdowns))
				return;
			appliedBreakdowns = breakdowns.reduce(
				(appliedBreakdowns, breakdown) => {
					appliedBreakdowns[breakdown.sysId] = breakdown;
					appliedBreakdowns[breakdown.sysId].elements = fromElementIds(breakdown.elements);
					appliedBreakdownFilter[breakdown.sysId] = breakdown.elementFilter;
					return appliedBreakdowns;
				},
				{}
			);
			previewBreakdowns = cloneDeep(appliedBreakdowns);
		}

		resetBreakdownInfo = () => {
			fetchStatus = {};
			excludedCombinations = {};
			store.breakdowns = store.breakdowns.map(breakdown => {
				breakdown.elements = [];
				return breakdown;
			});
			this.syncStore();
		}

		getBreakdowns = () => {
			return store.breakdowns;
		}

		/**
         *
         * @param {Function} _dispatcher @required function to dispatch actions
         */
		setDispatcher = (_dispatcher: any) => {
			dispatcher = _dispatcher;
		}

		getDerivedSearchResults = (
			searchKey: string,
			breakdownId?: string
		): { results: Array<any>, foundMatch: boolean } => {
			let foundMatch = false;
			const searchKeys = Object.keys(searchResults);
			return {
				results: searchKeys.reduce(
					(derivedResults: any, cachedSearchKey: string) => {
						if (searchKey.indexOf(cachedSearchKey) === 0) {
							foundMatch = true;

							if (searchKey === cachedSearchKey) {
								if (breakdownId)
									derivedResults[searchKey] = {
										[breakdownId]: searchResults[cachedSearchKey][breakdownId]
									};
								else
									derivedResults[searchKey] = Object.keys(
										searchResults[cachedSearchKey]
									).map(breakdownId => ({
										sys_id: breakdownId,
										extended_elements:
											searchResults[cachedSearchKey][breakdownId]
									}));
							} else {
								if (breakdownId) {
									derivedResults[searchKey] = searchResults[cachedSearchKey][
										breakdownId
									]
										? {
											[breakdownId]: searchResults[cachedSearchKey][
												breakdownId
											].filter(
												element =>
													element.name &&
													element.name
														.toLowerCase()
														.indexOf(searchKey.toLowerCase()) > -1
											)
										}
										: undefined;
								} else
									derivedResults[searchKey] = Object.keys(
										searchResults[cachedSearchKey]
									).reduce((searchKeyResults, breakdownId) => {
										if (searchResults[cachedSearchKey][breakdownId]) {
											const result = {
												sys_id: breakdownId,
												extended_elements: searchResults[cachedSearchKey][
													breakdownId
												].filter(
													element =>
														element.name &&
														element.name
															.toLowerCase()
															.indexOf(searchKey.toLowerCase()) > -1
												)
											};
											searchKeyResults.push(result);
										}
										return searchKeyResults;
									}, []);
							}
						}
						return derivedResults;
					},
					{}
				)[searchKey],
				foundMatch
			};
		}

		getElementsForSearchKey = (searchKey: string, breakdownId?: string) => {
			return searchResults[searchKey]
				? breakdownId
					? searchResults[searchKey][breakdownId]
					: searchResults[searchKey]
				: undefined;
		}

		hasSearchKey = () => {
			return appliedSearchKey && appliedSearchKey.length > 2;
		}

		/**
         * This function handles fetching elements
         * from API if not found, else return existing
         * elements for the breakdown
         * @param {string} breakdownId
         */
		getElementsForBreakdown = (breakdownId: string, expanded?: boolean) => {
			let previous = '';
			let current = '';
			let results =
				this.hasSearchKey() &&
				this.getElementsForSearchKey(appliedSearchKey, breakdownId);
			const isExclusion = this.isExcludedCombination(breakdownId);
			const preview = this.getPreviewBreakdowns();
			let previewId = Object.keys(this.getPreviewBreakdowns())[0];
			if (preview.hasOwnProperty(previewId)) previous = preview[previewId].name;
			if (store.breakdownMap.hasOwnProperty(breakdownId)) {
				current = store.breakdownMap[breakdownId].name;
			}

			if (isExclusion)
				throw new Error(
					t(
						'{0} is not available in combination with {1}. Remove {1} filters to apply {0}.',
						[current, previous]
					)
				);

			if (
				results ||
				(fetchStatus[breakdownId] === FetchStatus.SUCCESS &&
					store.breakdownMap[breakdownId] &&
					store.breakdownMap[breakdownId].elements)
			) {
				const elements = store.getElementsForBreakdown(breakdownId);
				if ((!appliedSearchKey || appliedSearchKey.length <= 2)
					&& previewBreakdowns[breakdownId]
					&& (appliedBreakdownFilter[breakdownId]
						&& appliedBreakdownFilter[breakdownId] !== 'none-selected')
					&& previewBreakdowns[breakdownId].elements
					&& previewBreakdowns[breakdownId].elements.length
					&& store.breakdownMap[breakdownId]) {
					if (Array.isArray(elements)) {
						const filterElements = elements.map(ele => ele.sysId);
						const previewElements = elements.length ? previewBreakdowns[breakdownId].elements.map(ele => ele.sysId) : [];
						if (previewElements) {
							if (!elements.length)
								delete previewBreakdowns[breakdownId];
							else {
								const elementsMatch = intersection(previewElements, filterElements);
								if (difference(previewElements, elementsMatch).length)
									previewBreakdowns[breakdownId].elements = elements.filter(ele => elementsMatch.indexOf(ele.sysId) > -1);
							}
							this.syncStore();
						}
					}
				}
				let elementsToSort = !this.hasSearchKey()
					? elements
					: results;
				const sortInfo = this.getBreakdownElementSort(breakdownId, elementsToSort);

				return this.sortElementsBy(
					[...(elementsToSort || [])],
					sortInfo.sortBy,
					sortInfo.sortDirection
				);
			} else if (
				expanded &&
				!this.hasSearchKey() &&
				fetchStatus[breakdownId] !== FetchStatus.IN_PROGRESS &&
				fetchStatus[breakdownId] !== FetchStatus.FAIL
			) {
				fetchStatus[breakdownId] = FetchStatus.IN_PROGRESS;
				dispatcher('FETCH_BREAKDOWN_ELEMENTS', {
					breakdownId,
					elementFilter: appliedBreakdownFilter[breakdownId],
					breakdown: previewBreakdowns[breakdownId] || {},
					appliedBreakdowns: Object.keys(previewBreakdowns)
						.map(
							previewBreakdownId =>
								previewBreakdownId !== breakdownId &&
								previewBreakdowns[previewBreakdownId].elements &&
								previewBreakdowns[previewBreakdownId].elements.length &&
								previewBreakdowns[previewBreakdownId]
						)
						.filter(value => !!value)
				});
				this.syncStore();
			}

			return [];
		}

		/**
         *
         * @param {string} breakdownId
         * @param {Array<Object>} elements
         */
		setElementsForBreakdown = (breakdownId: string, elements: Array<Element>) => {
			fetchStatus[breakdownId] = FetchStatus.SUCCESS;
			store.addElementsForBreakdown(breakdownId, elements);
		}

		addExcludedCombination = (breakdownId: string) => {
			const currentBreakdown = Object.keys(this.getPreviewBreakdowns())[0];
			if (currentBreakdown) {
				if (excludedCombinations[breakdownId])
					excludedCombinations[breakdownId].push(currentBreakdown);
				else excludedCombinations[breakdownId] = [currentBreakdown];
				this.syncStore();
			}
		}

		isExcludedCombination = (breakdownId: string) => {
			const currentBreakdown = Object.keys(this.getPreviewBreakdowns())[0];
			return (
				excludedCombinations[breakdownId] &&
				excludedCombinations[breakdownId].indexOf(currentBreakdown) > -1
			);
		}

		/**
         * @param {string} breakdownId Breakdown SysId
         * @param {string} status One of values of FetchStatus
         */
		setElementFetchStatus = (breakdownId: string, status: string) => {
			fetchStatus[breakdownId] = status;
			this.syncStore();
		}

		setBreakdownElementSort = (
			breakdownId: string,
			sortBy: string,
			sortDirection: boolean = true
		) => {
			appliedBreakdownSort[breakdownId] = {
				sortBy,
				sortDirection
			};
		}

		getBreakdownElementSort = (breakdownId: string, elements: Array<Element>) => {
			let isElementScorePresent = false;
			if (elements) {
				for (const ele of elements) {
					if (!isNaN(ele.score)) {
						isElementScorePresent = true;
						break;
					}
				}
			}

			const sortInfo = {
				sortBy: 'name',
				sortDirection: true,
				scoresAvailable: isElementScorePresent
			};

			if (appliedBreakdownSort[breakdownId]
				&& appliedBreakdownSort[breakdownId].scoresAvailable !== isElementScorePresent)
				appliedBreakdownSort[breakdownId].scoresAvailable = isElementScorePresent;

			if (appliedBreakdownSort
				&& appliedBreakdownSort[breakdownId]
				&& !isElementScorePresent)
				sortInfo.sortDirection = appliedBreakdownSort[breakdownId].sortDirection;

			return (
				isElementScorePresent ? (appliedBreakdownSort[breakdownId] || sortInfo) : sortInfo
			);
		}

		/**
         * This function handles adding breakdown
         * to selection if not present,
         * removes from selection if already
         * present
         */
		toggleBreakdown = (
			breakdown: Breakdown,
			element: Element,
			clearAll: boolean = false
		) => {
			if (clearAll && previewBreakdowns[breakdown.sysId]) {
				delete previewBreakdowns[breakdown.sysId];
				return;
			}

			if (breakdown.sysId) {
				const elements = store.getElementsForBreakdown(breakdown.sysId);
				if (Array.isArray(elements) && elements.length) {
					const e = elements.find((ele => ele.sysId === element.sysId));
					if (!e)
						this.toggleElementFilter(breakdown, '');
				} else
					this.toggleElementFilter(breakdown, '');
			}

			if (
				!previewBreakdowns[breakdown.sysId] ||
				!previewBreakdowns[breakdown.sysId].elements ||
				!previewBreakdowns[breakdown.sysId].elements.length
			) {
				previewBreakdowns[breakdown.sysId] = {
					...(previewBreakdowns[breakdown.sysId] || breakdown),
					elements: [element]
				};
			} else if (Array.isArray(previewBreakdowns[breakdown.sysId].elements)) {
				const elementIndex = previewBreakdowns[
					breakdown.sysId
				].elements.findIndex(el => el.sysId === element.sysId);

				if (elementIndex > -1) {
					previewBreakdowns[breakdown.sysId].elements.splice(elementIndex, 1);
				} else {
					previewBreakdowns[breakdown.sysId].elements = [
						...previewBreakdowns[breakdown.sysId].elements,
						element
					];
				}

				if (previewBreakdowns[breakdown.sysId].elements.length === 0)
					delete previewBreakdowns[breakdown.sysId];
			}

			Object.keys(fetchStatus).forEach(id => {
				if (id !== breakdown.sysId) fetchStatus[id] = FetchStatus.NOT_FETCHED;
			});
		}

		getElementFetchStatus = (breakdownId: string) => {
			return fetchStatus[breakdownId];
		}

		getSelectedElements = (breakdownId: string) => {
			if (
				previewBreakdowns[breakdownId] &&
				previewBreakdowns[breakdownId].elements
			) {
				const { elements } = previewBreakdowns[breakdownId];
				return [...elements];
			}
			return [];
		}

		sortElementsBy = (
			elements: Array<Element>,
			sortBy: string,
			sortDirection: boolean
		): Array<Element> => {
			return elements.sort((elementA, elementB) => {
				const sortOrder = sortDirection ? -1 : 1;
				let result;

				if (sortBy === 'name')
					result = sortByProperty(elementA, elementB, 'name');
				else
					result =
						elementA[sortBy] > elementB[sortBy]
							? -1
							: elementA[sortBy] === elementB[sortBy]
								? 0
								: 1;

				// Performs sorting by name when two elements are same
				if (result === 0 && sortBy !== 'name')
					result = sortByProperty(elementA, elementB, 'name');

				return result * sortOrder;
			});
		}

		getPreviewBreakdowns = () => {
			if (store.breakdowns && store.breakdowns.length && hasSelectionInit) {
				for (const breakdownId in previewBreakdowns) {
					if (previewBreakdowns.hasOwnProperty(breakdownId)) {
						let element = previewBreakdowns[breakdownId];
						const index = store.breakdowns.findIndex(
							breakdown => breakdown.sysId === breakdownId
						);
						if (index !== -1)
							element = { ...store.breakdowns[index], ...element };
						previewBreakdowns[breakdownId] = element;
					}
				}
				hasSelectionInit = false;
			}
			return previewBreakdowns;
		}

		getSelectedElementFilter = (breakdownId: string) => {
			return appliedBreakdownFilter[breakdownId] || '';
		}

		toggleElementFilter = (breakdown: Breakdown, elementFilter: string) => {
			if (
				!appliedBreakdownFilter[breakdown.sysId] ||
				appliedBreakdownFilter[breakdown.sysId] !== elementFilter
			)
				appliedBreakdownFilter[breakdown.sysId] = elementFilter;
			else delete appliedBreakdownFilter[breakdown.sysId];

			Object.keys(fetchStatus).forEach(id => {
				fetchStatus[id] = FetchStatus.NOT_FETCHED;
			});
			this.getElementsForBreakdown(breakdown.sysId, true);
		}

		/**
         * This function handles `on-click` of `Apply` button on
         * filter panel, applying previewed breakdowns
         */
		publishPreviewedBreakdowns = () => {
			appliedBreakdowns = cloneDeep(previewBreakdowns);
			dispatcher(
				'SN_KPI_DETAILS#BREAKDOWNS_APPLIED',
				cloneDeep(Object.values(appliedBreakdowns))
			);
		}
		getPublishedBreakdowns = () => {
			return appliedBreakdowns;
		}

		getAppliedElements = () => {
			return Object.values(appliedBreakdowns).reduce(
				(elements, breakdown: Breakdown) => {
					elements = [...elements, ...breakdown.elements];
					return elements;
				},
				[]
			);
		}

		getPreviewElements = () => {
			return Object.values(previewBreakdowns).reduce(
				(elements, breakdown: Breakdown) => {
					elements = [...elements, ...breakdown.elements];
					return elements;
				},
				[]
			);
		}

		getAppliedElementsCount = () => {
			return this.getAppliedElements().length;
		}

		getPreviewElementsCount = () => {
			return this.getPreviewElements().length;
		}

		hasSelectionChanged = () => {
			return (
				this.getPreviewElementsCount() !== this.getAppliedElementsCount() ||
				difference(this.getPreviewElements().map(ele => ele.sysId), this.getAppliedElements().map(ele => ele.sysId)).length > 0
			);
		}

		getSearchKey = () => {
			return appliedSearchKey;
		}

		searchElements = debounce((searchKey: string) => {
			if (!searchResults[searchKey]) {
				const derivedResults = this.getDerivedSearchResults(searchKey);

				if (derivedResults.foundMatch) {
					this.saveSearchResults(searchKey, derivedResults.results);
				} else if (
					searchKey &&
					searchKey.length > 2 &&
					!searchResults[searchKey]
				) {
					dispatcher('SN_KPI_DETAILS#BREAKDOWN_ELEMENT_SEARCH', {
						searchKey
					});
				}
			}
			appliedSearchKey = searchKey;
			this.syncStore();
		}, 320);

		saveSearchResults = (
			searchKey: string,
			results: Array<{ sys_id: string, extended_elements: Array<Object> }>
		) => {
			searchResults[searchKey] = results.reduce((searchResults, result) => {
				searchResults[result.sys_id] = result.extended_elements;
				return searchResults;
			}, {});
			this.syncStore();
		}

		/**
         * This function calls the implementation
         * to update filter manager in state to
         * trigger view re-render
         */
		syncStore = () => {
			// this.lastUpdated = new Date().getTime();
			dispatcher('SN_KPI_DETAILS#BREAKDOWN_FILTER_MANAGER_SYNC', cloneDeep(this));
		}
	}

	return new BreakdownFilterManager();
}
