import { t } from 'sn-translate';
import { getFormula } from '../../../helpers/formulaIndicator-get-formula';

export const view = ({ properties: { indicator, formulaInfo, tags } }) => {
	const formula = getFormula(formulaInfo);

	return (
		<div
			className="ah__popover-content grid-content"
			role="status"
			aria-live="assertive"
		>
			<div className="spacer">
				{formulaInfo.formula ? (
					<div className="info-group">
						<label>{t('Formula')}</label>
						<span>{formula}</span>
					</div>
				) : (
					''
				)}

				{indicator.description ? (
					<div className="info-group">
						<label>{t('Description')}</label>
						<span>{indicator.description}</span>
					</div>
				) : (
					''
				)}

				{indicator.frequencyLabel ? (
					<div className="info-group">
						<label>{t('Frequency')}</label>
						<span>{indicator.frequencyLabel}</span>
					</div>
				) : (
					''
				)}

				{indicator.direction ? (
					<div className="info-group">
						<label>{t('Direction')}</label>
						<span>{indicator.direction}</span>
					</div>
				) : (
					''
				)}

				{tags.length > 0 ? (
					<div className="info-group">
						<label>{t('Tags')}</label>
						<div className="tags-list grid-content">
							<span>{tags.join(', ')}</span>
						</div>
					</div>
				) : (
					''
				)}
			</div>
		</div>
	);
};
