import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { view } from './side-panel-view';
import styles from './side-panel-styles.scss';
import { handlers } from './sidePanelActionHandlers';

createCustomElement('sn-side-panel', {
	renderer: {
		type: snabbdom
	},
	properties: {
		selectedMenu: {
			reflect: false,
			schema: {
				type: 'object',
				properties: {
					menuTitle: {
						type: 'string'
					},
					menuId: {
						type: 'string'
					},
					iconName: {
						type: 'string'
					}
				}
			}
		},
		menuItems: {
			default: []
		},
		breakdownsMap: {},
		noPadding: { default: false }
	},
	view,
	actionHandlers: handlers,
	styles
});
