import { t } from 'sn-translate';
import '@servicenow/now-icon';
import '../PanelMenu';
import '../PanelMenuItem';
import '@servicenow/now-heading';

const focusHandler = (event, menuItems, selectedMenu) => {
	if (
		!event.relatedTarget ||
		event.relatedTarget.nodeName !== 'SN-PANEL-MENU'
	) {
		const index = menuItems.findIndex(
			(item) => item.menuId === selectedMenu.menuId
		);
		const menuItemNodes = event.target.parentElement.parentElement
			.querySelector('sn-panel-menu')
			.shadowRoot.querySelectorAll('sn-panel-menu-item');
		if (menuItemNodes[index])
			menuItemNodes[index].shadowRoot
				.querySelector('now-button-stateful')
				.focus();
	}
};

const renderPanelWithCustomHeader = (selectedMenu, { menuItems = [] }) => {
	return (
		<div
			class={{
				'ah__side-panel--content': true,
				'ah__side-panel--custom': true,
			}}
			style={{ overflow: 'hidden' }}
		>
			{selectedMenu && selectedMenu.menuId && (
				<slot name={selectedMenu.menuId} />
			)}
			<div
				tabindex="0"
				onfocus={(e) => focusHandler(e, menuItems, selectedMenu)}
			/>
		</div>
	);
};

const renderPanelWithoutCustomHeader = (selectedMenu, { menuItems }) => {
	return (
		<div
			class={{
				'ah__side-panel--content': true,
				'fill-vertical': true,
				'no-padding': selectedMenu.noPadding === true,
			}}
		>
			<div className="ah__side-panel--header">
				{selectedMenu.menuTitle && (
					<now-heading
						variant="header-secondary"
						label={selectedMenu.menuTitle}
						hasNoMargin
					/>
				)}
			</div>
			<div style={{ overflow: 'hidden' }}>
				{selectedMenu.menuId && <slot name={selectedMenu.menuId} />}
			</div>
			<div
				tabindex="0"
				onfocus={(e) => focusHandler(e, menuItems, selectedMenu)}
			/>
		</div>
	);
};

export const view = (
	{
		properties: { selectedMenu, menuItems, noPadding },
		pullFocusToClose,
		pullFocusToMenu,
		[`ref-${pullFocusToMenu}`]: focusRef,
		closeElmRef,
	},
	{ dispatch, updateState }
) => {
	/**
	 * Focus close on panel open
	 */
	if (pullFocusToClose && closeElmRef) {
		dispatch('SN_KPI_DETAILS#RESET_CLOSE_FOCUS');
		closeElmRef.focus();
	}

	/**
	 * Focus menu button on close
	 */
	if (pullFocusToMenu && focusRef) {
		dispatch('SN_KPI_DETAILS#RESET_MENU_FOCUS');
		focusRef.focus();
	}
	return (
		<div
			className={`ah__side-panel fill-vertical ${
				noPadding ? 'no-padding' : ''
			}`}
		>
			<div className="ah__side-panel--menu">
				<sn-panel-menu
					selectedMenu={selectedMenu}
					menu-items={menuItems}
				></sn-panel-menu>
			</div>
			{selectedMenu &&
				(selectedMenu.customHeader
					? renderPanelWithCustomHeader(selectedMenu, {
						dispatch,
						updateState,
						menuItems,
					  })
					: renderPanelWithoutCustomHeader(selectedMenu, {
						dispatch,
						updateState,
						menuItems,
					  }))}
		</div>
	);
};
