import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-input';
import '@servicenow/now-collapse';

import NO_SEARCH_RESULTS from '../../icons/no-search.svg';
import NO_BREAKDOWN from '../../icons/no-breakdowns.svg';

import '../../components/BreakdownCollapse';
import '@servicenow/now-checkbox';
import '@servicenow/now-icon';
import '@servicenow/now-tooltip';
import { convertArrayToObject } from '../../helpers/common';
import { omit } from 'lodash';

import { MAX_BREAKDOWN_LEVELS, SERIES_DISPLAY_TYPE } from '../../constants';
import { t } from 'sn-translate';
import { hasMultipleElements } from '../../helpers/common';

function getSelectedFiltersForInfo(filterInfo, dispatch) {
	if (filterInfo.length)
		return filterInfo.map((filter) => (
			<div className="ah__filters-panel--filter-info" key={filter.sysId}>
				<span className="filter-title">{filter.name}</span>
				<span> &#40;{filter.selectionCount}&#41;</span>
				<span className="split-dot">&#8226;</span>
				<now-button-bare
					variant="secondary"
					size="sm"
					on-click={() =>
						dispatch('SN_KPI_DETAILS#BREAKDOWN_ELEMENT_CLEAR', {
							breakdown: filter.breakdown,
						})
					}
					config-aria={{
						'aria-label': `${t('Remove filters applied({0}) for breakdown', [
							filter.selectionCount,
						])}, ${filter.name} `,
					}}
					label={t('Remove')}
				/>
			</div>
		));
	return <div className="elements-message">{t('No filters selected')}</div>;
}

export const view = (
	{
		properties: {
			breakdowns,
			isMultiElementSupported,
			collectBreakdownMatrix,
			isFilterPanelLoaded,
			searchKey,
			filterInfo: { filters: filterInfo = [], previewBreakdownIds },
			publishedBreakdowns,
			hasSelectionChanged,
			splitViewEnabled = false,
			searchInProgress,
		},
		applyButtonRef,
		infoIconTargetRef,
		checkboxTargetRef,
		searchInputRef,
		splitViewPreview = false,
	},
	{ dispatch, updateState }
) => {
	if (!searchKey && isFilterPanelLoaded && breakdowns.length === 0) {
		return (
			<div className="ah__filters-panel--no-breakdowns">
				<img
					className="logo"
					src={NO_BREAKDOWN}
					alt={t('No breakdowns to show')}
				/>
				<span className="info-primary">{t('Not set up yet')}</span>
				<span className="info-secondary">
					{t('Contact your ServiceNow administrator for more information')}
				</span>
			</div>
		);
	}

	const { filtersCount, hasMultiLevelMultiElement } = filterInfo.reduce(
		({ filtersCount, isMultiElement, hasMultiLevelMultiElement }, filter) => {
			if (isMultiElement && filter.selectionCount > 1)
				hasMultiLevelMultiElement = true;

			if (filter.selectionCount > 1) isMultiElement = true;

			filtersCount =
				filter.selectionCount > 1
					? filtersCount + filter.selectionCount
					: filtersCount;

			return { filtersCount, isMultiElement, hasMultiLevelMultiElement };
		},
		{ filtersCount: 0, isMultiElement: false, hasMultiLevelMultiElement: false }
	);

	let disableApply = false;

	/**
	 * DISABLING APPLY BUTTON
	 * 1) Multi-level multi-element(B1 - E11, E12... & B2 - E21, E22,...)
	 * is not supported
	 * 2) If selection not changed
	 * 3) Display separate selection changed but still element selection
	 * does not make sense to split view
	 */
	if (hasMultiLevelMultiElement) disableApply = true;
	else if (!hasSelectionChanged)
		if (
			!isMultiElementSupported ||
			splitViewPreview === splitViewEnabled ||
			(splitViewPreview !== splitViewEnabled && filtersCount <= 1)
		)
			disableApply = true;

	const setTargetRef = (ref, propName) =>
		setTimeout(() => updateState({ [propName]: ref }));

	/**
	 * RESETTING DISPLAY SEPARATE TIME SERIES SELECTION
	 * 1) One element selected in each breakdown
	 * doesn't make sense for split view so...
	 * 2) Multi-level multi-element(B1 - E11, E12... & B2 - E21, E22,...)
	 * is not supported...
	 * 3) Even when multi-element is not supported(eg. formula indicator without aggregate support enabled),
	 * single elements in each breakdown does not make sense
	 */
	if (
		filtersCount <= 1 ||
		hasMultiLevelMultiElement ||
		(splitViewPreview !== false &&
			(!isMultiElementSupported || filtersCount <= 1))
	)
		dispatch('SN_KPI_DETAILS#SET_SERIES_DISPLAY_TYPE', { value: false });

	const accessibleResults =
		searchKey && searchKey.length
			? breakdowns.reduce(
				({ count }, breakdown, index) => {
					count += breakdown.elements.length;
					if (index === breakdowns.length - 1)
						return { message: t('{0} results available', [count]) };
					return { count, index };
				},
				{
					count: 0,
					message: !searchInProgress
						? t('No results found for {0}', [searchKey])
						: t('Searching for {0}', [searchKey]),
				}
			  )
			: { mesage: '' };

	const noResults =
		!searchInProgress &&
		searchKey &&
		searchKey.length > 2 &&
		breakdowns.length === 0;

	return (
		<Fragment>
			{!isFilterPanelLoaded ? (
				<div className="ah__filters-panel-loading">
					<now-loader size="lg"></now-loader>
					<span className="info" role="alert" aria-live="polite">
						{t('Loading')}
					</span>
				</div>
			) : (
				<div className="ah__filters-panel fill-vertical">
					<div className="ah__filters-panel--info-header">
						<div className="ah__filters-panel--search">
							{accessibleResults.message ? (
								<span
									id="search-results"
									className="sr-only"
									aria-controls="filter-search"
									role="status"
									aria-live="assertive"
								>
									{accessibleResults.message}
								</span>
							) : (
								''
							)}
							<now-input
								id="filter-search"
								type="text"
								aria-label={t('Filters search')}
								placeholder={t('Search (Minimum {0} characters)', [
									3,
								])}
								hook-insert={({ elm }) => setTargetRef(elm, 'searchInputRef')}
								on-keyup={(event) => {
									const path =
										event.path || (event.composedPath && event.composedPath());
									if (
										path &&
										path[0] &&
										path[0].tagName === 'INPUT' &&
										((!searchKey && path[0].value.length > 2) || searchKey)
									) {
										dispatch('SN_KPI_DETAILS#SEARCH_FILTERS', {
											searchKey: path[0].value,
										});
									}
								}}
							>
								<now-button-iconic
									slot="start"
									icon="magnifying-glass-outline"
									variant="tertiary"
									bare
									size="md"
									disabled
								/>
								{searchKey && (
									<div className="ah__filters-panel--clear-search" slot="end">
										<now-button-iconic
											className="clear-input"
											size="sm"
											icon="close-fill"
											variant="tertiary"
											hide-padding
											bare
											autofocus
											tooltip-content={t('Clear filter search text')}
											on-click={() => {
												if (searchInputRef) {
													searchInputRef.value = '';
													searchInputRef.focus();
												}
												dispatch('SN_KPI_DETAILS#SEARCH_FILTERS', {
													searchKey: '',
												});
											}}
										/>
									</div>
								)}
							</now-input>
						</div>
						<div className="ah__filters-panel--info">
							<div className="message-container">
								{getSelectedFiltersForInfo(filterInfo, dispatch)}
								<span className="elements-message-limit">
									{!collectBreakdownMatrix &&
									previewBreakdownIds.length >= MAX_BREAKDOWN_LEVELS - 1
										? t('Only one breakdown at a time is supported by this KPI')
										: t('Maximum {0} filters types applicable', [
											MAX_BREAKDOWN_LEVELS,
										  ])}
								</span>
							</div>
							<div>
								<div className="ah-filters-button-container">
									<now-button
										name="apply"
										label={t('Apply')}
										size="sm"
										variant="primary"
										disabled={disableApply}
										on-click={() =>
											!disableApply &&
											dispatch('SN_KPI_DETAILS#BREAKDOWN_ELEMENT_APPLY', {
												splitViewEnabled: splitViewPreview,
												force: filtersCount <= 1,
											})
										}
									/>
									<div
										class={{ showMessage: hasMultiLevelMultiElement }}
										hook-insert={({ elm }) =>
											setTargetRef(elm, 'applyButtonRef')
										}
										className="ah-filters-tooltip"
									/>
								</div>
								<now-tooltip
									id="ah-tooltip-apply"
									target-ref={applyButtonRef}
									delay={50}
									content={t(
										'Only {0} breakdown element(s) allowed for second filter type',
										[1]
									)}
									position={['top-start top-end']}
								/>
							</div>
						</div>
						<div className="ah__filters-panel--series-display-type">
							<now-checkbox
								hook-insert={({ elm }) =>
									setTargetRef(elm, 'checkboxTargetRef')
								}
								hook-destroy={() => setTargetRef(null)}
								size="sm"
								aria-describedby="ah-series-type-checkbox-tooltip"
								append-to-payload={{
									type: SERIES_DISPLAY_TYPE,
								}}
								config-aria={{
									'aria-label': t(
										'Display as separate time series. Toggles between separate and combined time series display'
									),
								}}
								manage-checked
								checked={
									filtersCount > 1 &&
									((splitViewPreview && isMultiElementSupported) ||
										!isMultiElementSupported)
								}
								readonly={
									hasMultiLevelMultiElement ||
									!isMultiElementSupported ||
									filtersCount <= 1
								}
							/>
							<span className="ah__filters-panel--series-display-type--message">
								{t('Display as separate time series')}
							</span>
							{!isMultiElementSupported ? (
								<Fragment>
									<now-icon
										hook-insert={({ elm }) =>
											setTargetRef(elm, 'infoIconTargetRef')
										}
										hook-destroy={() => setTargetRef(null)}
										tabIndex="0"
										className="ah__filters-panel--series-display-type--multi-not-supported-info"
										aria-describedby="ah-info-tooltip"
										icon="circle-info-outline"
										size="sm"
									></now-icon>
									<now-tooltip
										id="ah-info-tooltip"
										target-ref={infoIconTargetRef}
										content={t(
											'Combined scores are not available for this KPI.'
										)}
										delay={333}
									/>
								</Fragment>
							) : (
								<now-tooltip
									id="ah-series-type-checkbox-tooltip"
									target-ref={checkboxTargetRef}
									content={t(
										'Toggles between separate and combined time series display'
									)}
									delay={333}
								/>
							)}
						</div>
					</div>
					{noResults ? (
						<div className="ah__filters-panel--collapse">
							<div className="ah__filters-panel--collapse-scrollable">
								<div className="ah__filters-panel--collapse-scrollable-error">
									<img
										className="logo"
										src={NO_SEARCH_RESULTS}
										alt={t('No search match found')}
									></img>
									<span className="info-primary">
										{t('No results found for {0}', [searchKey])}
									</span>
									<span className="info-secondary">
										{t('Try using different keywords.')}
									</span>
								</div>
							</div>
						</div>
					) : (
						<div className="ah__filters-panel--collapse">
							<div className="ah__filters-panel--collapse-scrollable">
								{breakdowns.map((breakdownInfo, index) => {
									return (
										<sn-breakdown-collapse
											key={breakdownInfo.breakdown.sysId}
											selectedItemsMap={convertArrayToObject(
												breakdownInfo.selectedItems
											)}
											{...omit(breakdownInfo, 'selectedItems')}
											searchInProgress={searchInProgress}
											search-key={searchKey}
											show-multi-element-error-message={
												!isMultiElementSupported &&
												hasMultipleElements(publishedBreakdowns)
											}
											on-keyup={function (ele) {
												const arrow = ele.keyCode;
												const path =
													ele.path || (ele.composedPath && ele.composedPath());
												if (
													path &&
													path[0] &&
													path[0].nodeName === 'NOW-COLLAPSE-TRIGGER' &&
													[38, 40].indexOf(arrow) > -1
												) {
													ele.preventDefault();
													const nodes = ele.target.parentElement.querySelectorAll(
														'sn-breakdown-collapse'
													);
													if (index + 1 < nodes.length || index - 1 >= 0)
														if (38 === arrow && nodes[index - 1]) {
															const collapseNode = nodes[
																index - 1
															].shadowRoot.querySelector(
																'now-collapse-trigger'
															);
															collapseNode.focus();
															collapseNode.scrollIntoView();
														} else if (40 === arrow && nodes[index + 1]) {
															const collapseNode = nodes[
																index + 1
															].shadowRoot.querySelector(
																'now-collapse-trigger'
															);
															collapseNode.focus();
															collapseNode.scrollIntoView();
														}
												}
											}}
										/>
									);
								})}
							</div>
						</div>
					)}
				</div>
			)}
		</Fragment>
	);
};
