export const scoreQuery = `query ($config: String!) {
    AppDetailedScorecard_Query {
        uuidQuery(config:$config) {
          scoreInfo {
            change
            change_color
            change_percent
            change_arrow
            score
            score_value
            last_score_date
            human_date
            target
            target_color
            gap
            gap_percent
            date
          }
        }
    }
}`;
