import '@servicenow/now-record-date-picker';
import { t } from 'sn-translate';
import { getFormattedDate } from '../../helpers/dateUtil';
import { PICKER_DATE_FORMAT, CHART_DATE_FORMAT } from '../../constants';
import { getPointerStyle } from '../../helpers/changePointer';
import { userLanguage } from '../../helpers/common';

export const view = ({ properties: { data, selectedDate, scoreDate } }) => {
	const pointerStyle = getPointerStyle(data.change_arrow, data.change_color);

	const formattedScoreDate = getFormattedDate(
		scoreDate,
		CHART_DATE_FORMAT,
		PICKER_DATE_FORMAT
	);
	const scoreColor = {
		color: data.target_color
	};
	const scoreDefaultClasses = ['score'];

	if (data.score_not_available_message)
		scoreDefaultClasses.push('score-not-available');

	const scoreContainer = ['score'];
	if (!data.score) scoreContainer.push('score--no-score');

	const userLocale = userLanguage();

	if (data) {
		return (
			<div className="ah-main-score-container">
				<div
					className="ah-score-details"
					aria-label={t('score details')}
					role="status"
				>
					{formattedScoreDate && (
						<div className="date-picker">
							<now-record-date-picker
								format={PICKER_DATE_FORMAT}
								value={selectedDate ? selectedDate : formattedScoreDate}
								language={userLocale}
								append-to-payload={{ type: 'scoreDate' }}
							/>
						</div>
					)}

					{data.isScoreLoading ? (
						<div className="ah-score-loading-container">
							<now-loader size="md"></now-loader>
							<span role="alert" aria-live="polite">
								{t('Loading...')}
							</span>
						</div>
					) : undefined}

					{data.score ? (
						<span className="score" style={scoreColor} aria-label={t('Score {0}' ,[data.score])}>
							{data.score}
						</span>
					) : (
						<span
							className={
								data.score_not_available_message
									? scoreDefaultClasses.join(' ')
									: scoreContainer.join(' ')
							}
						>
							{data.score_not_available_message}
						</span>
					)}

					<div className="score-change-info-container">
						{data.change_arrow ? (
							<span
								aria-label={data.change_arrow === 'up' ? t('Increase') : t('Decrease')}
								className={`change-pointer offset ${data.change_arrow}`}
								style={pointerStyle}
							></span>
						) : (
							''
						)}
						{data.change ? (
							data.change_percent ? (
								<span className="change-percent" aria-label={t('Change {0} Change Percent {1}', [data.change, data.change_percent])}>
									{data.change} &#40;{data.change_percent}&#41;
								</span>
							) : (
								<span className="change-percent" aria-label={t('Change {0}', [data.change])}>{data.change}</span>
							)
						) : (
							''
						)}
					</div>

					{data.target && data.score ? <div className="separator">|</div> : ' '}

					{data.target && data.score ? (
						<div className="target">
							<span className="target-label">{t('Target')}</span>
							<span className="target-value">{data.target}</span>
						</div>
					) : (
						''
					)}

					{data.gap ? <div className="separator">|</div> : ' '}

					{data.gap ? (
						<div className="gap">
							<span className="gap-label">{t('Gap')}</span>
							<span className="gap-value">
								{data.gap} &#40;{data.gap_percent}&#41;
							</span>
						</div>
					) : (
						''
					)}
				</div>
			</div>
		);
	}
	return (
		<div className="ah-main-score-container">
			<div
				className="ah-score-details"
				aria-label={t('score details')}
				role="status"
			>
				<span className="score">{t('No data')}</span>
			</div>
		</div>
	);
};
