import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { view } from './breakdown-collapse-view';
import styles from './breakdown-collapse-styles.scss';
import { handlers } from './breakdown-collapse-handlers';

createCustomElement('sn-breakdown-collapse', {
	renderer: {
		type: snabbdom
	},
	view,
	properties: {
		breakdown: {},
		elements: {},
		elementFilter: {},
		expanded: {},
		disableCollapse: {
			default: false
		},
		selectedItemsMap: {
			default: {}
		},
		fetchStatus: {},
		sortBy: {},
		sortDirection: {},
		scoresAvailable: {},
		errorMessage: {},
		showMultiElementErrorMessage: {},
		searchKey: {},
		searchInProgress: {}
	},
	actionHandlers: handlers,
	styles
});
