import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { view } from './threshold-form-view';
import { actionHandlers } from './action-handlers';
import styles from './styles.scss';

createCustomElement('sn-pa-threshold-form', {
	renderer: {
		type: snabbdom,
	},
	view,
	properties: {
		uuid: {
			schema: {
				type: 'string',
			},
		},
		createThreshold: {
			schema: {
				type: 'boolean',
			},
			default: false,
		},
		thresholdClickedValue: {
			schema: {
				type: 'object',
			},
			default: {},
		},
		thresholdType: {
			schema: {
				type: 'string',
			},
			default: 'global',
		},
		items: {
			schema: {
				type: 'array',
			},
			default: [],
		},
		hasCreateThresholdRole: {
			schema: {
				type: 'boolean',
			},
		},
	},
	styles,
	actionHandlers,
});
