import { buildUuidString } from '../../../helpers/util';

export const actionHandlers = {
	'NOW_ALERT#ACTION_CLICKED': ({
		action: { payload },
		updateState
	}) => {
		if (payload.action.type === 'dismiss') {
			if (payload.action.message === 'premium-message') 
				updateState({ showPremiumMessage: false });
			if (payload.action.message === 'aggregate-message')	
				updateState({ showAggregateMessage: false });
		}
	},
	'NOW_BUTTON_ICONIC#CLICKED': ({
		action: { payload: { type, sysId, lastScoreDate, date, appliedBreakdowns } },
	}) => {
		if (type === 'promin') {
			const uuid = appliedBreakdowns ? buildUuidString(sysId, appliedBreakdowns) : buildUuidString(sysId);
			const arr = ['/promin.do?source=pa'];
			arr.push(`uuid=${encodeURIComponent(uuid)}`);
			arr.push(`date=${date ? date : lastScoreDate}`);
			window.open(arr.join('&'), sysId);
		}
	}
};