import { createCustomElement } from '@seismic/core';
import snabbdom from '@seismic/snabbdom-renderer';
import { view } from './skip-link-view';
import styles from './styles.scss';

createCustomElement('sn-skip-link', {
	renderer: {
		type: snabbdom
	},
	view,
	properties: {
		label: {
			default: '',
			reflect: true
		},
		to: {
			reflect: true
		},
		focusAction: {}
	},
	styles
});
