/**
 * Please do not add any more action handlers here..split it into new file...
 */

import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { get } from 'lodash';
import { getConfig } from '../helpers/get-config';
import { parsePreferences, isTrue } from '../helpers/util';
import {
	getFormattedDate,
	getDateRange,
	getNewStartDate,
	getProcessedDate,
	isValidDate
} from '../helpers/dateUtil';
import {
	CHART_DATE_FORMAT,
	PICKER_DATE_FORMAT,
	SERIES_DISPLAY_TYPE_SET,
	SERIES_DISPLAY_TYPE,
	INDICATOR_FREQUENCIES
} from '../constants';
import { scorecardQuery } from '../resources/query';
import { updatePreferences } from './preferences';
import { getBreakdownFilterManager } from '../store/managers/BreakdownFilterManager';
import { handleScorecardFetchRequest } from './life-cycle-handlers';
import { SCORE_NOT_AVAILABLE } from './update-score-action-handlers';
import { hasMultipleElements, isNull } from '../helpers/common';
import { t } from 'sn-translate';

const fetchDetailedScorecard = createGraphQLEffect(scorecardQuery, {
	variableList: ['config'],
	startActionType: 'SN_KPI_DETAILS#FETCH_DETAILED_SCORECARD_STARTED',
	successActionType: 'SN_KPI_DETAILS#FETCH_DETAILED_SCORECARD_SUCCEEDED',
	errorActionType: 'SN_KPI_DETAILS#FETCH_DETAILED_SCORECARD_FAILED'
});

export const actionHandlers = {
	/**
	 * All the initial tasks
	 * can go here which are required
	 * for the first render of the views
	 * NOTE: View is not rendered until `POST_BOOTSTRAP`
	 * is complete, a loader is shown till then
	 */
	'SN_KPI_DETAILS#INIT_BOOTSTRAP': ({ updateState, dispatch }) => {
		const breakdownFilterManager = getBreakdownFilterManager();
		breakdownFilterManager.setDispatcher(dispatch);
		updateState({ breakdownFilterManager, scorecard: {} });
		dispatch('SN_KPI_DETAILS#POST_BOOTSTRAP');
	},
	/**
	 * View will get rendered
	 * for the first time after
	 * completion of this action
	 * NOTE: This is action handler is
	 * as good as `COMPONENT_BOOTSTRAPPED`
	 *
	 * ACTION FLOW
	 * -----------
	 * COMPONENT_BOOTSTRAPPED => INIT_BOOTSTRAP => POST_BOOTSTRAP
	 */
	'SN_KPI_DETAILS#POST_BOOTSTRAP': (coeffects) => {
		handleScorecardFetchRequest(coeffects);
		coeffects.updateState({ BOOTSTRAP_COMPLETE: true });
	},
	'SN_KPI_DETAILS#FETCH_DETAILED_SCORECARD': fetchDetailedScorecard,
	'SN_KPI_DETAILS#FETCH_DETAILED_SCORECARD_SUCCEEDED': coeffects => {
		const {
			updateState,
			state: {
				properties: { indicatorSysid, scoreDate, domain },
				startDate,
				endDate,
				breakdownFilterManager
			},
			dispatch,
			action: { payload, meta }
		} = coeffects;

		// Throws error when indicatorSysId is null/invalid or API fails
		const errors = get(payload, 'errors');
		if (errors && errors.length !== 0) {
			const errorMessage = errors[0].message || '';
			if (
				errorMessage.indexOf(
					'Security rules prevent access to this indicator'
				) > -1
			) {
				updateState({ isAclError: true });
			} else if (errorMessage.indexOf('Not a valid Indicator') > -1) {
				updateState({ isInvalidSysid: true });
			} else
				updateState({ isNetworkIssue: true });
			updateState({ isFilterPanelLoaded: false });
		} else {
			const scorecard = get(
				payload,
				'data.AppDetailedScorecard_Query.uuidQuery',
				{}
			);

			const {
				indicator = {},
				preferences = [],
				predictiveEnabled = false,
				scoreInfo,
			} = scorecard;
			const {
				type,
				collectRecords,
				firstScoreDate,
				lastScoreDate,
				factsTable,
				multiElementSupported
			} = indicator;

			if (scorecard && meta.init === true)
				breakdownFilterManager.initBreakdowns(scorecard.breakdowns);

			let scorecardCopy = {...scorecard};
			if (scoreInfo && !scoreInfo.score) {
				scorecardCopy = {
					...scorecardCopy,
					...{ scoreInfo: { ...scoreInfo, score_not_available_message: SCORE_NOT_AVAILABLE } }
				};
			}

			updateState({
				scorecard: scorecardCopy,
				isInvalidSysid: false,
				isSignalsPluginEnabled: predictiveEnabled,
				isNetworkIssue: false,
				seriesDisplayType: !multiElementSupported,
				isFilterPanelLoaded: true
			});

			if (!startDate || !endDate) {
				// Assign values to startDate, endDate and showRecords on first load from the Preferences
				let prefs = parsePreferences(preferences);
				const isFetchRecords = 
					type === 'AUTOMATED' && collectRecords && !isNull(factsTable);
				const prefShowRecords = 
					isFetchRecords
						? isTrue(get(prefs, 'show_records', 'false'))
						: false;
				const newStartDate = get(prefs, 'start_date', firstScoreDate);
				const newEndDate = get(prefs, 'end_date', lastScoreDate);

				const date = 
					scoreDate && isValidDate(scoreDate) ? scoreDate : lastScoreDate;
				updateState({
					startDate: newStartDate,
					endDate: newEndDate,
					showRecords: prefShowRecords
				});

				// Fetches the records if indicator type is AUTOMATED, collectRecords and showRecords is true
				if (isFetchRecords && prefShowRecords && lastScoreDate) {
					const appliedBreakdowns = breakdownFilterManager.getPublishedBreakdowns();
					let config = getConfig({}, 'indicator_sysid', indicatorSysid);
					config = getConfig(config, 'start_date', date);
					if (appliedBreakdowns)
						config = getConfig(
							config,
							'applied_breakdowns',
							Object.values(appliedBreakdowns).map(breakdown => ({
								breakdown_id: breakdown.sysId,
								elements: breakdown.elements.map(e => e.sysId)
							}))
						);

					if (domain) config = getConfig(config, 'domain', domain);

					dispatch('SN_KPI_DETAILS#SYSIDS_FETCH_REQUESTED', {
						data: JSON.stringify(config)
					});
				}
			}
		}
	},
	'SN_KPI_DETAILS#FETCH_DETAILED_SCORECARD_FAILED': coeffects => {
		const { updateState } = coeffects;
		updateState({ isNetworkIssue: true });
	},
	// Fetch the preferences stored
	'SN_KPI_DETAILS#PREFERENCE_UPDATE': updatePreferences,

	'SN_KPI_DETAILS#PREFERENCE_UPDATE_SUCCEEDED': () => {
		// eslint-disable-next-line no-console
		console.log('Updated Preferences Successfully!');
	},

	'SN_KPI_DETAILS#PREFERENCE_UPDATE_FAILED': () => {
		// eslint-disable-next-line no-console
		console.warn('Error Updating Preferences!');
	},
	'SN_KPI_DETAILS#AGGREGATED_SCORE_NOT_AVAILABLE': ({
		updateState,
		state: { scorecard },
		action: { payload }
	}) => {
		const scoreInfo = {
			score: '',
			score_value: '',
			score_not_available_message: payload.message
		};
		updateState({
			scorecard: {
				...scorecard,
				scoreInfo
			}
		});
	},
	/**
	 * Action dispatched when toggle button is toggled for show/hide records
	 */
	'NOW_TOGGLE#CHECKED_SET': ({
		updateState,
		dispatch,
		action: { payload },
		state: {
			properties: { indicatorSysid, scoreDate, domain },
			selectedDate,
			breakdownFilterManager,
			scorecard: {
				indicator: { lastScoreDate }
			}
		}
	}) => {
		// Converts date to YYYY-MM-DD format
		const formattedSelectedDate = getFormattedDate(
			selectedDate,
			PICKER_DATE_FORMAT,
			CHART_DATE_FORMAT
		);

		if (payload.type === 'showRecords') {
			if (payload.value) {
				const date = 
					scoreDate && isValidDate(scoreDate) ? scoreDate : lastScoreDate;
				const appliedBreakdowns = breakdownFilterManager.getPublishedBreakdowns();
				let config = getConfig({}, 'indicator_sysid', indicatorSysid);
				config = getConfig(
					config,
					'start_date',
					formattedSelectedDate || date
				);
				if (appliedBreakdowns)
					config = getConfig(
						config,
						'applied_breakdowns',
						Object.values(appliedBreakdowns).map(breakdown => ({
							breakdown_id: breakdown.sysId,
							elements: breakdown.elements.map(e => e.sysId)
						}))
					);

				if (domain) config = getConfig(config, 'domain', domain);

				dispatch('SN_KPI_DETAILS#SYSIDS_FETCH_REQUESTED', {
					data: JSON.stringify(config)
				});
			}

			dispatch('SN_KPI_DETAILS#PREFERENCE_UPDATE', {
				data: {
					show_records: payload.value
				},
				uuid: indicatorSysid,
				client_type: 'workspace'
			});
			updateState({ showRecords: payload.value });
		}
	},

	/**
	 * Handles the action dispatched when point is clicked on the Time-Series Chart.
	 * Gives the current date corresponding to the click of a point on the chart.
	 */
	'SN_KPI_DETAILS#DATE_SELECTED': coeffects => {
		const {
			action: { payload },
			state: { breakdownFilterManager },
			updateState
		} = coeffects;

		// Converts date to MM DD YYYY format
		const formattedDate = getFormattedDate(payload.date, CHART_DATE_FORMAT, PICKER_DATE_FORMAT);
		updateState({ selectedDate: formattedDate });
		breakdownFilterManager.resetBreakdownInfo();
	},

	/**
	 * Handles the action dispatched when the date range is changed in Date Range Picker component.
	 */
	'NOW_DATE_RANGE_PICKER#VALUE_CHANGED': coeffects => {
		const {
			action,
			updateState,
			dispatch,
			properties: { indicatorSysid }
		} = coeffects;
		const { payload } = action;

		/* Performs the following operations only when user has selected some start and end in the date range picker */
		if(payload.start && payload.end) {

			// YYYY-MM-DD format
			const formattedStartDate = getFormattedDate(
				payload.start,
				PICKER_DATE_FORMAT,
				CHART_DATE_FORMAT,
			);

			// YYYY-MM-DD format
			const formattedEndDate = getFormattedDate(
				payload.end,
				PICKER_DATE_FORMAT,
				CHART_DATE_FORMAT
			);

			dispatch('SN_KPI_DETAILS#PREFERENCE_UPDATE', {
				data: {
					start_date: formattedStartDate,
					end_date: formattedEndDate
				},
				uuid: indicatorSysid,
				client_type: 'workspace'
			});
			updateState({
				startDate: formattedStartDate,
				endDate: formattedEndDate,
			});
		}
	},

	/**
	 * Updates the endDate
	 */
	'SN_KPI_DETAILS#UPDATE_END_DATE': coeffects => {
		const {
			action: { 
				payload: { date } 
			},
			state: {
				properties: { indicatorSysid },
				scorecard: {
					indicator: { frequency }
				},
			},
			dispatch,
			updateState,
		} = coeffects;

		const newEndDate = date
			.add(3, INDICATOR_FREQUENCIES[frequency].type)
			.format(CHART_DATE_FORMAT);
		
		dispatch('SN_KPI_DETAILS#PREFERENCE_UPDATE', {
			data: {
				end_date: newEndDate
			},
			uuid: indicatorSysid,
			client_type: 'workspace'
		});

		updateState({ endDate: newEndDate });
	},

	/**
	 * Updates the start date
	 */
	'SN_KPI_DETAILS#UPDATE_START_DATE': coeffects => {
		const {
			action: { 
				payload: { date } 
			},
			state: {
				properties: { indicatorSysid },
			},
			dispatch,
			updateState,
		} = coeffects;
		
		dispatch('SN_KPI_DETAILS#PREFERENCE_UPDATE', {
			data: {
				start_date: date
			},
			uuid: indicatorSysid,
			client_type: 'workspace'
		});

		updateState({ startDate: date });
	},

	[SERIES_DISPLAY_TYPE_SET]: ({
		action: { payload: seriesDisplayType },
		updateState
	}) => updateState({ [SERIES_DISPLAY_TYPE]: seriesDisplayType }),

	/**
	 * Handles the action dispatched when the OK button is clicked and the value is saved
	 * in Date Picker component.
	 */
	'NOW_RECORD_DATE_PICKER#VALUE_CHANGED': coeffects => {
		const {
			action: { payload },
			dispatch,
			state: {
				properties: { indicatorSysid, domain },
				startDate,
				endDate,
				showRecords,
				breakdownFilterManager,
				scorecard: {
					indicator: { firstScoreDate, lastScoreDate }
				}
			},
			updateState
		} = coeffects;

		/* Performs the following operations only when user has selected some value in the date picker */
		if (payload.value && payload.type === 'scoreDate') {

			const range = getDateRange(startDate, endDate, CHART_DATE_FORMAT);
			const newStartDate = getNewStartDate(payload.value, range, PICKER_DATE_FORMAT);

			// converts date to YYYY-MM-DD format
			let formattedNewStartDate = getFormattedDate(
				newStartDate,
				PICKER_DATE_FORMAT,
				CHART_DATE_FORMAT
			);
			let formattedNewEndDate = getFormattedDate(
				payload.value,
				PICKER_DATE_FORMAT,
				CHART_DATE_FORMAT
			);

			const appliedBreakdowns = breakdownFilterManager.getPublishedBreakdowns();
			let config = getConfig({}, 'indicator', indicatorSysid);
			config = getConfig(config, 'date', formattedNewEndDate);
			if (appliedBreakdowns)
				config = getConfig(
					config,
					'breakdowns',
					Object.values(appliedBreakdowns).map(breakdown => ({
						breakdownId: breakdown.sysId,
						elements: breakdown.elements.map(e => e.sysId)
					}))
				);

			if (domain) config = getConfig(config, 'domain', domain);
			
			// Dispatched to fetch the score for current selected date
			dispatch('SN_KPI_DETAILS#FETCH_ONLY_SCORE', {
				config: JSON.stringify(config)
			});

			// Dispatched to fetch the records if show record is true
			if (showRecords) {
				let configRecords = getConfig({}, 'indicator_sysid', indicatorSysid);
				configRecords = getConfig(
					configRecords,
					'start_date',
					formattedNewEndDate || lastScoreDate
				);
				if (appliedBreakdowns)
					configRecords = getConfig(
						configRecords,
						'applied_breakdowns',
						Object.values(appliedBreakdowns).map(breakdown => ({
							breakdown_id: breakdown.sysId,
							elements: breakdown.elements.map(e => e.sysId)
						}))
					);
				
				if (domain) configRecords = getConfig(configRecords, 'domain', domain);
				
				dispatch('SN_KPI_DETAILS#SYSIDS_FETCH_REQUESTED', {
					data: JSON.stringify(configRecords)
				});
			}

			if (formattedNewStartDate < firstScoreDate)
				formattedNewStartDate = firstScoreDate;

			// Date Range should not change if current date selected is outside the score fetch range
			if (
				formattedNewEndDate < firstScoreDate ||
				formattedNewEndDate > lastScoreDate
			) {
				formattedNewEndDate = endDate;
				formattedNewStartDate = startDate;
			}

			updateState({
				startDate: formattedNewStartDate,
				endDate: formattedNewEndDate,
				selectedDate: payload.value
			});

			// Updating the prefences as date range also changes
			dispatch('SN_KPI_DETAILS#PREFERENCE_UPDATE', {
				data: {
					start_date: formattedNewStartDate,
					end_date: formattedNewEndDate
				},
				uuid: indicatorSysid,
				client_type: 'workspace'
			});

			breakdownFilterManager.resetBreakdownInfo();

		}
	},

	/**
	 * Handles the action dispatched on clicking the Time-Series Chart
	 */
	'SN_VISUALIZATION#POINT_CLICKED': coeffects => {
		const {
			/* updateState, */
			action: {
				payload: {
					data: { seriesDataset }
				}
			},
			dispatch,
			state: {
				properties: { indicatorSysid, domain },
				scorecard: {
					indicator: { lastScoreDate, multiElementSupported }
				},
				showRecords,
				breakdownFilterManager
			}
		} = coeffects;

		const processedDate = getProcessedDate(seriesDataset.x);
		const appliedBreakdowns = breakdownFilterManager.getPublishedBreakdowns();
		let config = getConfig({}, 'indicator', indicatorSysid);
		config = getConfig(config, 'date', processedDate);
		if (appliedBreakdowns)
			config = getConfig(
				config,
				'breakdowns',
				Object.values(appliedBreakdowns).map(breakdown => ({
					breakdownId: breakdown.sysId,
					elements: breakdown.elements.map(e => e.sysId)
				}))
			);

		if (domain) config = getConfig(config, 'domain', domain);

		// when multiple elements selected one any level and multiElementSupported is not there then show no scores
		if (!multiElementSupported && hasMultipleElements(appliedBreakdowns)) {
			dispatch('SN_KPI_DETAILS#AGGREGATED_SCORE_NOT_AVAILABLE', {
				message: t('Combined score not available for this KPI')
			});
		} else {
			dispatch('SN_KPI_DETAILS#FETCH_ONLY_SCORE', {
				config: JSON.stringify(config)
			});
		}

		dispatch('SN_KPI_DETAILS#DATE_SELECTED', { date: processedDate });
		if (showRecords) {
			let configRecords = getConfig({}, 'indicator_sysid', indicatorSysid);
			configRecords = getConfig(
				configRecords,
				'start_date',
				processedDate || lastScoreDate
			);
			if (appliedBreakdowns)
				configRecords = getConfig(
					configRecords,
					'applied_breakdowns',
					Object.values(appliedBreakdowns).map(breakdown => ({
						breakdown_id: breakdown.sysId,
						elements: breakdown.elements.map(e => e.sysId)
					}))
				);
			
			if (domain) configRecords = getConfig(configRecords, 'domain', domain);

			dispatch('SN_KPI_DETAILS#SYSIDS_FETCH_REQUESTED', {
				data: JSON.stringify(configRecords)
			});
		}
	},
};
