
import { cloneDeep } from 'lodash';

export const getElementIds = (arr) => {
	if (!arr)
		return [];
	if (typeof arr[0] === 'string')
		return cloneDeep(arr);
	return arr.map(e => e.sysId);
};

export const fromElementIds = (arr) => {
	if (!arr)
		return [];
	if (typeof arr[0] === 'object')
		return cloneDeep(arr);
	return arr.map(e => ({sysId: e}));
};
