export const scorecardQuery = `query ($config: String!) {
    AppDetailedScorecard_Query {
        uuidQuery(config:$config) {
          indicator {
            sysId
            name
            type
            description
            direction
            frequency
            frequencyLabel
            firstScoreDate
            lastScoreDate
            factsTable
            collectRecords
            multiElementSupported
            collectBreakdownMatrix
          }
          properties {
            chartLineColor
            chartColorScheme
          }
          tags
          scoreInfo {
            change
            change_color
            change_percent
            change_arrow
            score
            score_value
            last_score_date
            human_date
            target
            target_color
            gap
            gap_percent
            date
          }
          formula,
          permissions{
            name
            value
          }
          preferences {
            name
            value
          }
          formulaComponents{
            key
            display
            uUID
          }
          breakdowns {
              sysId
              name
              elementFilters {
                sysId
                title
                filter
              }
              elements {
                sysId,
                name
              }
          },
          predictiveEnabled,
          isPremium,
          prominEnabled
        }
    }
}`;
