// @flow
import { frequencyPeriod } from '../../../helpers/frequency-period';
import type { Period } from '../../../helpers/frequency-period';

const DAILY_POINTS = 14;
const MEDIUM_RANGE_POINTS = 12;
const LONG_RANGE_POINTS = 8;
const LOOK_AHEAD_POINTS = 6;

type DateRange = {
	startDate: string,
	endDate: string
}
type SignalTimes = {
	dateInMillis: number, // startDate
	endAtMillis: number
}

function getDateRange(signals: SignalTimes[], frequency: string): DateRange | undefined {
	if (!signals || signals.length === 0)
		return undefined;

	const period = frequencyPeriod(frequency);
	const firstSignal = signals[0];
	if (!(firstSignal.dateInMillis && firstSignal.endAtMillis))
		return undefined;
	return {
		startDate: adjustDateByPoints(firstSignal.dateInMillis, getNumberOfPointsForStartAdjustment(period), period),
		endDate: adjustDateByPoints(firstSignal.endAtMillis, LOOK_AHEAD_POINTS, period)
	};
}

function adjustDateByPoints(time: number, points: number, period: Period): string {
	const date = new Date(time);
	switch (period.name) {
	case 'd':
		date.setUTCDate(date.getUTCDate() + points * period.value);
		break;
	case 'w':
		date.setUTCDate(date.getUTCDate() + points * 7 * period.value);
		break;
	case 'M':
		date.setUTCMonth(date.getUTCMonth() + points * period.value);
		break;
	case 'Q':
		date.setUTCMonth(date.getUTCMonth() + 3 * points * period.value);
		break;
	case 'Y':
		date.setUTCFullYear(date.getUTCFullYear() + points * period.value);
		break;
	default:
		date.setUTCDate(date.getUTCDate() + points * period.value);
		break;
	}
	return formatDate(date);
}

function getNumberOfPointsForStartAdjustment(period: Period): number {
	switch (period.name) {
	case 'd':
		return -DAILY_POINTS;
	case 'w':
	case 'M':
		return -MEDIUM_RANGE_POINTS;
	case 'Q':
	case 'Y':
		return -LONG_RANGE_POINTS;
	default:
		return -DAILY_POINTS;
	}
}

function formatDate(date: Date): string {
	return date.toISOString().split('T')[0];
}

function narrowRange(range: DateRange, window: DateRange) {
	if (!range || !window)
		return range;
	return {
		startDate: formatDate(new Date(Math.max(
			new Date(range.startDate).getTime(),
			new Date(window.startDate).getTime()
		))),
		endDate: formatDate(new Date(Math.min(
			new Date(range.endDate).getTime(),
			new Date(window.endDate).getTime()
		))),
	};
}

export const signalPanelHandlers = {
	'SN_KPI_SIGNALS#SIGNALS_SELECTED': ({
		action: {
			payload: {
				showSignals,
				hasFreshSignals,
				isStable,
				isDisabled,
				signals
			}
		},
		state: {
			properties: {
				indicator,
				firstScoreDate,
				lastScoreDate,
				startDate,
				endDate
			},
			refreshChart,
		},
		updateState
	}) => {
		const originalRange = { startDate, endDate };
		let newRange = getDateRange(signals, indicator.frequency);
		newRange = narrowRange(newRange, { startDate: firstScoreDate, endDate: lastScoreDate });
		if (!newRange)
			newRange = originalRange;

		if (showSignals == 'active')
			showSignals = !isDisabled && showSignals;

		updateState({
			signalFilters: {
				showSignals,
				hasFreshSignals,
				isStable,
				isDisabled,
				originalRange,
				...newRange
			},
			refreshChart: !refreshChart
		});
	},
};
