/**
 * A comparator function which returns integer value based on
 * the result after comparing two elements
 *
 * @param {Array<Element} elementA
 * @param {Array<Element} elementB
 *
 * @returns Integer {-1, 0, 1}
 */
export function sortByProperty(elementA, elementB, prop = 'name') {
	if (!elementA || !elementB) return;

	return (elementA[prop]).toLowerCase() > (elementB[prop]).toLowerCase()
		? -1
		: (elementA[prop]).toLowerCase() === (elementB[prop]).toLowerCase()
			? 0
			: 1;
}
