import get from 'lodash/get';
export const view = ({ properties: { filters } }) => {
	return (Array.isArray(filters) && filters.length ? <div className='kpi-info'>
		{filters.map((breakdown) => {
			const breakdownName = get(breakdown, 'name');
			const elementName = get(breakdown, 'breakdown.elements[0].name');
			if (elementName)
				return (
					<label className="kpi-info-label">
						{breakdownName}&nbsp;<b>{elementName}</b>
					</label>
				);
		})}
	</div> : undefined
	);
};