import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { view } from './panel-menu-view';
import styles from './panel-menu-view.scss';
import { handlers } from './panelMenuActionhandlers';

createCustomElement('sn-panel-menu', {
	renderer: {
		type: snabbdom
	},
	view,
	properties: {
		selectedMenu: {
			default: {}
		},
		menu: {},
		menuItems: {
			default: []
		}
	},
	actionHandlers: handlers,
	styles
});
