import { t } from 'sn-translate';

import '@servicenow/now-button';
import '@servicenow/now-popover';
import '@servicenow/now-tabs';
import '@servicenow/now-card';
import '@servicenow/now-loader';
import '@servicenow/now-content-tree';
import '@servicenow/now-empty-state';
import '@servicenow/now-heading';
import './ThresholdForm';
import './ThresholdPopoverContent';
import '../../components/MainContainer/KPIInfoView';

import { wrapWithHandler, getSortedGroup } from './utils';

import { CREATE_THRESHOLD_CLICKED } from './constants';

const getEmptyState = (message = '') => (
	<now-empty-state
		type="not-configured"
		variant="medium"
		headerText={message}
	/>
);

const getList = (items = [], thresholdType = 'global', isThresholdLoading) => (
	<div className="thresholds-list--container">
		<now-tabs
			items={[
				{ label: t('Global'), id: 'global' },
				{ label: t('Personal'), id: 'personal' },
			]}
			selected-item={thresholdType}
			fixed-width
			append-to-payload={{
				type: 'THRESHOLD_TYPE',
			}}
		/>
		{items && !items.getItemsOfType(thresholdType).length ? (
			<div className="empty-state">
				{getEmptyState(t('No {0} thresholds created yet.', thresholdType))}
			</div>
		) : (
			''
		)}
		{isThresholdLoading ? (
			<now-loader size="lg" label={t('Fetching thresholds')}></now-loader>
		) : (
			<now-content-tree
				items={getSortedGroup(items.getItemsOfType(thresholdType)).map(
					(item) => ({
						id: item.sys_id,
						label: `${item.label} ${item.formatted_value}`.trim(),
						threshold: item,
						actions: [{ type: 'icon', icon: 'chevron-right-outline', clickActionType: 'NOW_CONTENT_TREE#ITEM_CLICKED'}],
					})
				)}
			/>
		)}
	</div>
);

export const view = (
	{
		properties: {
			uuid,
			hasCreateThresholdRole,
			hasMultipleElements,
			filterInfo,
			opened,
		},
		items,
		thresholdType,
		focusToCreate,
		createThreshold,
		thresholdClickedValue,
		isThresholdLoading,
	},
	{ updateState, dispatch }
) => {
	const createButton = (
		<now-button-iconic
			tooltipContent={
				!hasCreateThresholdRole &&
				(thresholdType === 'global' || !thresholdType)
					? t(
						'You do not have the appropriate role to create a global threshold.'
					  )
					: t('Create Threshold')
			}
			size="md"
			variant="tertiary"
			icon="plus-outline"
			hook-insert={(elm) => setTargetRef(elm, 'createButtonRef')}
			hook-destroy={() => setTargetRef(null, 'createButtonRef')}
			append-to-payload={{ type: CREATE_THRESHOLD_CLICKED }}
		/>
	);
	const setTargetRef = (ref, propName) =>
		setTimeout(() => updateState({ [propName]: ref }));

	if (createThreshold || thresholdClickedValue)
		return (
			<sn-pa-threshold-form
				uuid={uuid}
				create-threshold={createThreshold}
				threshold-clicked-value={thresholdClickedValue}
				threshold-type={thresholdType}
				threshold-form-opened={true}
				items={items}
				has-create-threshold-role={hasCreateThresholdRole}
			>
				{Array.isArray(filterInfo) && filterInfo.length ? (
					<sn-kpi-info filters={filterInfo} />
				) : (
					<div></div>
				)}
			</sn-pa-threshold-form>
		);

	if (focusToCreate) {
		dispatch('FOCUS_CREATE', {createButton});
	}

	return (
		<div className="thresholds-container">
			<div className="thresholds-header">
				<div className="thresholds-header--title">
					<now-heading
						label={t('Thresholds')}
						variant="header-secondary"
						hasNoMargin
					/>
					<now-popover>
						<now-button-iconic
							icon="circle-info-outline"
							size="md"
							variant="tertiary"
							hook-insert={({ elm }) => setTargetRef(elm, 'infoButtonRef')}
							hook-destroy={() => setTargetRef(null, 'infoButtonRef')}
							aria-describedby="thresholds-title-info"
							slot="trigger"
							bare
						/>
						<sn-threshold-popover-content
							slot="content"
							message={t('Normal range of scores for this indicator')}
						/>
					</now-popover>
				</div>
				{hasMultipleElements ? '' : createButton}
			</div>
			{!hasMultipleElements ? (
				<div className="slot">
					<slot />
				</div>
			) : undefined}
			{hasMultipleElements ? (
				<div className="thresholds-empty-state-multielement">
					{getEmptyState(
						t('Thresholds are not available for selected filters.')
					)}
				</div>
			) : items && !items.length ? (
				<div className="thresholds-empty-state-multielement">
					{getEmptyState(t('No thresholds are created yet.'))}
				</div>
			) : (
				getList(wrapWithHandler(items), thresholdType, isThresholdLoading)
			)}
		</div>
	);
};
