/**
 * Parse string of form:
 * breakdown_id:element_id;breakdown2_id;element2_id
 *
 * into object of form:
 * [{
 *     sysId: breakdown_id,
 *     elements: [ element_id ]
 * },
 * {
 *     sysId: breakdown_id2,
 *     elements: [ element_id2 ]
 * }]
 *
 * for use in filtering from URL.
 */
export const parseBreakdowns = str => {
	const breakdownStrings = str.split(';');
	const breakdownMap = breakdownStrings.reduce((map, seg) => {
		if (!seg || !seg.includes(':'))
			return map;
		let [key, value] = seg.split(':');
		if (map[key])
			map[key].push(value);
		else
			map[key] = [value];
		return map;
	}, {});
	return Object.entries(breakdownMap).map(([key, value]) => ({
		sysId: key,
		elements: value
	}));
};
