import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { get } from 'lodash';
import { scoreQuery } from '../resources/scoresQuery';
import { t } from 'sn-translate';

const fetchOnlyScore = createGraphQLEffect(scoreQuery, {
	variableList: ['config'],
	startActionType: 'SN_KPI_DETAILS#FETCH_ONLY_SCORE_STARTED',
	successActionType: 'SN_KPI_DETAILS#FETCH_ONLY_SCORE_SUCCEEDED',
	errorActionType: 'SN_KPI_DETAILS#FETCH_ONLY_SCORE_FAILED'
});

const SCORE_UPDATE_ERROR = t(
	'Unable to update the score.Please refresh the entire page.'
);
export const SCORE_NOT_AVAILABLE = t(
	'No scores available for the selected date'
);

export const updateScoreHandlers = {
	'SN_KPI_DETAILS#FETCH_ONLY_SCORE': fetchOnlyScore,
	'SN_KPI_DETAILS#FETCH_ONLY_SCORE_STARTED': coeffects => {
		const {
			updateState,
			state: { scorecard }
		} = coeffects;
		updateState({
			scorecard: {
				...scorecard,
				...{ scoreInfo: { isScoreLoading: true } }
			}
		});
	},
	'SN_KPI_DETAILS#FETCH_ONLY_SCORE_SUCCEEDED': coeffects => {
		const {
			updateState,
			state: { scorecard },
			action: { payload }
		} = coeffects;
		const errors = get(payload, 'errors');
		if (errors && errors.length !== 0) {
			updateState({
				scorecard: {
					...scorecard,
					...{
						scoreInfo: {
							score_not_available_message: SCORE_UPDATE_ERROR,
							isScoreLoading: false
						}
					}
				}
			});
		} else {
			const result = get(
				payload,
				'data.AppDetailedScorecard_Query.uuidQuery',
				{}
			);
			if (result.scoreInfo && !result.scoreInfo.score) {
				updateState({
					scorecard: {
						...scorecard,
						...{
							scoreInfo: {
								score_not_available_message: SCORE_NOT_AVAILABLE,
								isScoreLoading: false
							}
						}
					},
				});
			} else {
				updateState({
					scorecard: {
						...scorecard,
						...{ scoreInfo: { ...result.scoreInfo, isScoreLoading: false } }
					}
				});
			}
		}
	},
	'SN_KPI_DETAILS#FETCH_ONLY_SCORE_FAILED': coeffects => {
		const {
			updateState,
			state: { scorecard }
		} = coeffects;
		updateState({
			scorecard: {
				...scorecard,
				...{
					scoreInfo: {
						score_not_available_message: SCORE_UPDATE_ERROR,
						isScoreLoading: false
					}
				}
			}
		});
	}
};
