// @flow
// shamelessly stolen from par-components
export type Period = {
	name: 'd'|'w'|'M'|'Q'|'Y',
	value: number
}

export const frequencyPeriod = (frequency: string): Period => {
	switch (frequency.toLowerCase()) {
	case 'daily':
		return {
			name: 'd',
			value: 1,
		};

	case 'weekly':
		return {
			name: 'w',
			value: 1,
		};

	case 'bi_weekly':
		return {
			name: 'w',
			value: 2,
		};

	case 'four_weekly':
		return {
			name: 'w',
			value: 4,
		};

	case 'monthly':
		return {
			name: 'M',
			value: 1,
		};

	case 'bi_monthly':
		return {
			name: 'M',
			value: 2,
		};

		// For the fiscal quarterly use only 1M since moment doesn't know what months are actually part of that quarter
		// TODO: implement a proper Fiscal Quarterly frequency to be used in this case
	case 'fiscal_quarterly':
		return {
			name: 'Q',
			value: 1,
		};

	case 'quarterly':
		return {
			name: 'Q',
			value: 1,
		};

	case 'half_yearly':
		return {
			name: 'Y',
			value: 0.5,
		};

	case 'fiscal_yearly':
	case 'yearly':
		return {
			name: 'Y',
			value: 1,
		};
	}

	return {
		name: 'd',
		value: 1,
	};
};
