import { t } from 'sn-translate';
import '@servicenow/now-popover';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import './PopoverContent';
import '../ScoreDetails';
import { hasMultipleElements } from '../../helpers/common';

export const view = ({
	properties: {
		kpiDetails,
		scoreInfo,
		selectedDate,
		scoreDate,
		firstScoreDate,
		lastScoreDate,
		aggregate,
		isPremium,
		prominEnabled,
		breakdownFilterManager,
	},
	showPremiumMessage = true,
	showAggregateMessage = !!aggregate,
}) => {
	const { indicator = {}, formulaInfo, tags } = kpiDetails;
	const { collectRecords, type, sysId } = indicator;
	const { date } = scoreInfo;
	const isLoading = Object.entries(indicator).length === 0;
	
	const appliedBreakdowns =
		breakdownFilterManager && breakdownFilterManager.getPublishedBreakdowns();

	const isProminEnabled =
		!hasMultipleElements(appliedBreakdowns) &&
		!(firstScoreDate === null || lastScoreDate === null) &&
		prominEnabled
			? type === 'AUTOMATED'
				? collectRecords
					? true
					: false
				: true
			: false;
	return (
		<header className="ah__kpi-details--header grid-content">
			{isLoading ? (
				<div className="ah__kpi-details--loading">
					<now-loader size="md"></now-loader>
					<span role="alert" aria-live="polite">
						{t('Loading')}
					</span>
				</div>
			) : (
				<Fragment>
					{!isPremium && showPremiumMessage ? (
						<div className="ah_kpi-details--message">
							<now-alert
								status="info"
								icon="info-circle-outline"
								content={t(
									'To view more than 180 days of historical data you must license Performance Analytics'
								)}
								action={{ type: 'dismiss', message: 'premium-message' }}
							></now-alert>
						</div>
					) : (
						''
					)}
					{showAggregateMessage ? (
						<div className="ah_kpi-details--aggregate-message">
							<now-alert
								status="info"
								icon="info-circle-outline"
								content={t(
									'KPI details page supports only {0} (default aggregate) frequency', indicator.frequencyLabel
								)}
								action={{ type: 'dismiss', message: 'aggregate-message' }}
							></now-alert>
						</div>
					) : (
						''
					)}
					<h1 className="ah__kpi-details--sub-title">{t('KPI Details')}</h1>
					<div className="ah__kpi-details--title">
						<span title={indicator.name} aria-label={indicator.name}>
							{indicator.name}
						</span>
						<now-popover>
							<now-button-iconic
								slot="trigger"
								icon="circle-info-outline"
								size="md"
								tooltip-content={t('info')}
								config-aria={{ 'aria-label': t('KPI Info') }}
								role="button"
								bare
								variant="tertiary"
							/>
							<sn-popover-content
								slot="content"
								indicator={indicator}
								formula-info={formulaInfo}
								tags={tags}
							/>
						</now-popover>
						{isProminEnabled ? (
							<now-button-iconic
								icon="tree-view-long-outline"
								size="md"
								tooltip-content={t('Open in Process Optimization')}
								config-aria={{
									'aria-label': t('Open in Process Optimization'),
								}}
								variant="tertiary"
								role="button"
								bare
								append-to-payload={{
									type: 'promin',
									sysId,
									lastScoreDate,
									date,
									appliedBreakdowns,
								}}
							/>
						) : (
							''
						)}
					</div>
					{lastScoreDate ? (
						<sn-score-details
							data={scoreInfo}
							selected-date={selectedDate}
							score-date={scoreDate}
						/>
					) : (
						''
					)}
				</Fragment>
			)}
		</header>
	);
};
