import { createHttpEffect } from '@servicenow/ui-effect-http';

const PREFERENCE_END_POINT = 'api/now/pa/ds/preference/:uuid';

const updatePreferences = createHttpEffect(PREFERENCE_END_POINT, {
	method: 'PATCH',
	pathParams: ['uuid'],
	batch: true,
	dataParam: 'data',
	queryParams:['client_type'],
	successActionType: 'SN_KPI_DETAILS#PREFERENCE_UPDATE_SUCCEEDED',
	errorActionType: 'SN_KPI_DETAILS#PREFERENCE_UPDATE_FAILED'
});

export {
	updatePreferences
};
