import { t } from 'sn-translate';
import '@servicenow/now-radio-buttons';
import '@servicenow/now-input';
import '@servicenow/now-select';
import '@servicenow/now-alert';

import {
	FORM_NAVIGATE_BACK_CLICKED,
	THRESHOLD_CONDITION_LIST,
	THRESHOLD_CONDITION_TYPE,
	THRESHOLD_VALUE,
	THRESHOLD_TYPE,
	CREATE_THRESHOLD_SAVE,
	CREATE_THRESHOLD_CANCEL,
	UPDATE_THRESHOLD_SAVE,
	UPDATE_THRESHOLD_CANCEL,
	DELETE_THRESHOLD,
} from './constants';

import { focusToNode } from '../utils';

function capitalizeFirstLetter(string = '') {
	return string.charAt(0).toUpperCase() + string.slice(1);
}
export const view = (
{
	properties: {
		createThreshold,
		thresholdType,
		thresholdClickedValue,
		hasCreateThresholdRole,
	},
	init,
	backBtnElm,
	value,
	thresholdConditionType,
	selectedThresholdType,
	selectedThreshold,
	hasOverlappingThreshold,
},
	{ updateState }
) => {
	const thresholdTypeList = [];
	if (createThreshold)
		thresholdTypeList.push({
			id: 'select_option',
			label: t('Select Option'),
		});

	for (let item in THRESHOLD_CONDITION_LIST)
		thresholdTypeList.push(THRESHOLD_CONDITION_LIST[item]);

	const options = [];

	if (hasCreateThresholdRole)
		options.push({
			id: 'global',
			label: t('Global'),
			checked: thresholdType === 'global',
		});

	options.push({
		id: 'personal',
		label: t('Personal'),
		checked: hasCreateThresholdRole ? thresholdType === 'personal' : true,
	});

	if (init && backBtnElm) {
		focusToNode(backBtnElm);
		updateState({ init: false });
	}

	const setTargetRef = (elm) => {
		updateState({ backBtnElm: elm });
	};

	const capitalizedThreshold = capitalizeFirstLetter(selectedThresholdType);

	return (
		<div className="sn-pa-threshold__form">
			<div className="sn-pa-threshold__form-header">
				<now-button-bare
					className="back_button"
					label={t('Back')}
					iconStart="chevron-left-outline"
					variant="tertiary"
					hook-insert={({ elm }) => setTargetRef(elm)}
					hook-destroy={() => setTargetRef(null)}
					append-to-payload={{ type: FORM_NAVIGATE_BACK_CLICKED }}
					size="md"
					configAria={{ button: { 'aria-label': t('Back') } }}
				/>
				<h6 className="sn-pa-threshold__form-header--title">
					{createThreshold
						? t('New Threshold')
						: thresholdType === 'global'
							? t('Global Threshold')
							: t('Personal Threshold')}
				</h6>
			</div>
			<slot />
			<div className="sn-pa-threshold__form-controls">
				{createThreshold ? (
					<now-radio-buttons
						className="threshold_type_radio"
						label={t('Visibility')}
						helper-content={t(
							'Global thresholds are visible to everyone and personal thresholds only to you.'
						)}
						options={options}
						value={thresholdType}
						layout="horizontal"
						manage-value
						required
						append-to-payload={{
							type: THRESHOLD_TYPE,
						}}
					></now-radio-buttons>
				) : undefined}

				<now-select
					className="threshold_type_select"
					label={t('Threshold Type')}
					items={thresholdTypeList}
					selected-item={
						createThreshold ? 'select_option' : thresholdConditionType
					}
					required
					disabled={
						!hasCreateThresholdRole &&
						thresholdType === 'global' &&
						!createThreshold
					}
					append-to-payload={{
						type: THRESHOLD_CONDITION_TYPE,
					}}
				/>
				{[
					THRESHOLD_CONDITION_LIST[0].id,
					THRESHOLD_CONDITION_LIST[1].id,
				].indexOf(thresholdConditionType) === -1 ? (
					<now-input
						className="threshold_value"
						label={t('Threshold Value')}
						type="number"
						value={value || ''}
						manage-value={true}
						required
						readonly={
							!hasCreateThresholdRole &&
							thresholdType === 'global' &&
							!createThreshold
						}
						append-to-payload={{
							type: THRESHOLD_VALUE,
						}}
					/>
				) : undefined}
			</div>
			<slot name="error">
				<div>
					{hasOverlappingThreshold ? (
						<now-alert
							status="critical"
							icon="info-circle-outline"
							content={t('{0} "{1}" threshold already exists.', [
								capitalizedThreshold,
								selectedThreshold
									? selectedThreshold
									: thresholdClickedValue.label,
							])}
							append-to-payload={{ type: 'dismiss-thresholds' }}
							action={{ type: 'dismiss' }}
							expanded
						/>
					) : undefined}
				</div>
			</slot>
			<div className={`actions${createThreshold ? ' create' : ''}`}>
				{!createThreshold ? (
					<now-button
						size="sm"
						variant="secondary"
						label={t('Delete')}
						append-to-payload={{ type: DELETE_THRESHOLD }}
						disabled={
							!hasCreateThresholdRole &&
							thresholdType === 'global' &&
							!createThreshold
						}
					/>
				) : undefined}
				<now-button
					size="sm"
					variant="secondary"
					label={t('Cancel')}
					append-to-payload={{
						type: createThreshold
							? CREATE_THRESHOLD_CANCEL
							: UPDATE_THRESHOLD_CANCEL,
					}}
				/>
				<now-button
					size="sm"
					variant="primary"
					label={createThreshold ? t('Save') : t('Update')}
					disabled={
						((!value ||
							!thresholdConditionType ||
							thresholdConditionType === 'select_option') &&
							thresholdConditionType !== THRESHOLD_CONDITION_LIST[0].id &&
							thresholdConditionType !== THRESHOLD_CONDITION_LIST[1].id) ||
						hasOverlappingThreshold ||
						(!hasCreateThresholdRole &&
							thresholdType === 'global' &&
							!createThreshold)
					}
					append-to-payload={{
						type: createThreshold
							? CREATE_THRESHOLD_SAVE
							: UPDATE_THRESHOLD_SAVE,
					}}
				/>
			</div>
		</div>
	);
};
