import { t } from 'sn-translate';
import { get } from 'lodash';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import '../Toolbar';
import '../ChartContainer';
import '../SidePanel';
import '../../views/filters-panel';
import '../../views/ThresholdsPanel';
import './KPIInfoView';

import { panelSlots, SERIES_DISPLAY_TYPE_SPLIT } from '../../constants';
import 'sn-kpi-signals';
import 'sn-pa-target-list-connected';
import NO_SCORE_YET from '../../icons/no-score-yet.svg';
import {
	breakdownInfo,
	filterInfo,
	searchResultsFound,
} from './main-view-utils';
import {
	shallowEquals,
	buildUuidString,
	buildQueryString,
} from '../../helpers/util';
import { hasMultipleElements } from '../../helpers/common';

const handleRecords = (
	showRecords,
	recordsQuery,
	tableName,
	recordListError,
	currentPage
) => {
	if (recordListError && Object.keys(recordListError).length) {
		const { primary, secondary, logo, logoAltMessage } = recordListError;
		return (
			<div className="ah__records--error">
				<img className="logo" src={logo} alt={logoAltMessage}></img>
				<span className="info-primary">{primary}</span>
				<span className="info-secondary">{secondary}</span>
			</div>
		);
	} else
		return (
			<Fragment>
				{showRecords && recordsQuery ? (
					<now-record-list-connected
						table={tableName}
						query={recordsQuery}
						hideCellFilter
						hideHeader
						limit={10}
						page={currentPage}
						hideQuickEdit
						hideRowSelector
					/>
				) : undefined}
			</Fragment>
		);
};

const getTargetsListView = (
	selectedMenu,
	indicator,
	appliedBreakdowns,
	averageScoreValue,
	filter
) => (
	<sn-pa-target-list-connected
		query-string={buildQueryString(indicator.sysId, appliedBreakdowns)}
		uuid={buildUuidString(indicator.sysId, appliedBreakdowns)}
		manage-cancel-edit={
			!selectedMenu || selectedMenu.menuId !== panelSlots.TARGETS
		}
		cancel-edit={!selectedMenu || selectedMenu.menuId !== panelSlots.TARGETS}
		is-visible={selectedMenu && selectedMenu.menuId === panelSlots.TARGETS}
		average-score={averageScoreValue}
		has-multiple-elements={hasMultipleElements(appliedBreakdowns)}
	>
		{filter.length ? (
			<div slot="kpi-info">
				{filter.map((breakdown) => {
					const breakdownName = get(breakdown, 'name');
					const elementName = get(breakdown, 'breakdown.elements[0].name');
					if (elementName)
						return (
							<label className="kpi-info">
								{breakdownName}&nbsp;<b>{elementName}</b>
							</label>
						);
				})}
			</div>
		) : undefined}
	</sn-pa-target-list-connected>
);

export const view = (
	{
		properties: {
			indicator,
			panel,
			domain,
			startDate,
			endDate,
			chartLineColor,
			chartColorScheme,
			firstScoreDate,
			lastScoreDate,
			recordsQuery,
			recordListError,
			showRecords,
			scoreInfo,
			collectRecords,
			tableName,
			factsTable,
			collectBreakdownMatrix,
			breakdowns,
			breakdownFilterManager,
			seriesDisplayType,
			isMultiElementSupported,
			isSignalsPluginEnabled,
			isFilterPanelLoaded,
			searchInProgress,
			hasCreateThresholdRole,
			user,
			showTargets: showTargetsProp,
			showThresholds: showThresholdsProp,
			showForecast: showForecastProp,
			showTrend: showTrendProp,
			showComments: showCommentsProp,
			showLabels: showlabelsProp,
		},
		currentPage = 1,
		selectedMenu,
		refreshChart,
		signalFilters = {},
		activeBreakdownCollapse,
		splitViewEnabled,
		targetValueClicked,
		averageScore,
		hasNoForecastData,
		hasNoThresholdData,
		hasNoTargetData,
		hasNoTrendLineData,
		hasNoCommentData,
		showTargets = showTargetsProp,
		showThresholds = showThresholdsProp,
		showForecast = showForecastProp,
		showTrend = showTrendProp,
		showComments = showCommentsProp,
		showLabels = showlabelsProp,
		forecastLastDataPoint,
	},
	{ dispatch }
) => {
	const menuItems = [
		{
			menuId: panelSlots.FILTERS,
			menuTitle: t('Filters'),
			iconName: 'filter-outline',
			badgeText: breakdownFilterManager.getAppliedElementsCount(),
		},
	];

	const kpiInfo = filterInfo(breakdownFilterManager.getPreviewBreakdowns());
	const filter = get(kpiInfo, 'filters');

	if (isSignalsPluginEnabled) {
		const isSupported = breakdownFilterManager.getAppliedElementsCount() <= 1;
		let badgeText = undefined;
		let menuTitle = t('KPI Signals');
		if (!isSupported) {
			menuTitle = t('KPI Signals not supported');
		} else if(signalFilters.isStable) {
			badgeText = <now-icon style={{ color: '#42B8A2' }} icon="status-fill" />;
			menuTitle = t('KPI Signals process stable');
		} else if (signalFilters.hasFreshSignals) {
			badgeText = <now-icon style={{ color: '#ff9011' }} icon="status-fill" />;
			menuTitle = t('New KPI signal');
		} else if (signalFilters.showSignals && !signalFilters.isDisabled) {
			badgeText = <now-icon icon="status-outline" />;
			menuTitle = t('No new KPI signals');
		}

		menuItems.push({
			menuId: panelSlots.SIGNALS,
			menuTitle: menuTitle,
			iconName: 'heartbeat-outline',
			badgeText: badgeText,
			customHeader: true,
		});
	}
	if (selectedMenu === undefined && panel)
		selectedMenu = menuItems.find(
			(menuItem) => menuItem.menuId === panel.toUpperCase()
		);

	const isSignalsEnabled =
		isSignalsPluginEnabled &&
		selectedMenu &&
		selectedMenu.menuId === panelSlots.SIGNALS;

	if (isSignalsEnabled) {
		if (shallowEquals({ startDate, endDate }, signalFilters.originalRange)) {
			startDate = signalFilters.startDate;
			endDate = signalFilters.endDate;
		}
		showRecords = false;
	}

	const averageScoreValue =
		startDate > lastScoreDate || endDate < firstScoreDate
			? undefined
			: averageScore;

	menuItems.push(
		{
			menuId: panelSlots.TARGETS,
			tooltip: t('Targets'),
			iconName: 'target-outline',
			noPadding: true,
		},
		{
			menuId: panelSlots.THRESHOLDS,
			menuTitle: t('Thresholds'),
			iconName: 'chart-threshold-outline',
			customHeader: true,
		}
	);

	// Shows error message if we do not have any scores for the indicator
	if (lastScoreDate === null || firstScoreDate === null) {
		return (
			<main className="ah__main-container no-scores-yet">
				<div className="ah__main-container-no-scores-yet">
					<img
						className="logo"
						src={NO_SCORE_YET}
						alt={t('No scores collected yet')}
					/>
					<span className="info-primary">{t('No scores collected yet')}</span>
					<span className="info-secondary">
						{t('Contact your admin for more information')}
					</span>
				</div>
			</main>
		);
	}

	const appliedBreakdowns = breakdownFilterManager.getPublishedBreakdowns();
	const seriesDataConfig = {
		uuid: { indicator: indicator.sysId, domain },
	};

	if (Object.keys(appliedBreakdowns).length > 0) {
		seriesDataConfig.uuid.breakdowns = Object.values(appliedBreakdowns).map(
			(breakdown) => ({
				sys_id: breakdown.sysId,
				element_sys_ids: breakdown.elements.map((e) => e.sysId),
			})
		);
	}

	if (seriesDisplayType === SERIES_DISPLAY_TYPE_SPLIT)
		seriesDataConfig.group_by = Object.keys(appliedBreakdowns);

	if (!selectedMenu || selectedMenu.menuId !== panelSlots.TARGETS)
		dispatch('TARGET_VALUE_CLICKED#CLEAR_VALUE');

	const derivedSeries = {
		showTargets,
		showThresholds,
		showForecast,
		showTrend,
		showComments,
		showLabels,
	};

	const derivedSeriesDataInfo = {
		hasNoForecastData,
		hasNoThresholdData,
		hasNoTargetData,
		hasNoTrendLineData,
		hasNoCommentData,
	};

	return (
		<main className="ah__main-container">
			<div className="left-column">
				<sn-toolbar
					indicator-sysId={indicator.sysId}
					indicator-type={indicator.type}
					start-date={startDate}
					end-date={endDate}
					facts-table={factsTable}
					collect-records={collectRecords}
					show-records={showRecords}
					is-signals-enabled={isSignalsEnabled}
					has-score={scoreInfo && scoreInfo.score}
					derived-series-data-info={derivedSeriesDataInfo}
					has-multi-element={hasMultipleElements(appliedBreakdowns)}
					has-multi-series={seriesDisplayType === SERIES_DISPLAY_TYPE_SPLIT}
					derived-series={derivedSeries}
				/>
				<div className={`chart--${showRecords ? 'records' : 'fill'}`}>
					<sn-chart-container
						refreshChart={refreshChart}
						start-date={startDate}
						end-date={endDate}
						chart-line-color={chartLineColor}
						chart-color-scheme={chartColorScheme}
						first-score-date={firstScoreDate}
						last-score-date={lastScoreDate}
						showRecords={showRecords}
						seriesDisplayType={seriesDisplayType}
						show-signals={isSignalsEnabled && signalFilters.showSignals}
						target-value-clicked={targetValueClicked}
						derived-series={derivedSeries}
						forecast-last-data-point={forecastLastDataPoint}
						seriesDataConfig={JSON.stringify(seriesDataConfig)}
					/>
					<div className="ah__records">
						{handleRecords(
							showRecords,
							recordsQuery,
							tableName,
							recordListError,
							currentPage
						)}
						<sn-skip-link
							label={t('Skip to visualization')}
							focus-action="SN_KPI_DETAILS#VISUALIZATION_FOCUS"
						/>
					</div>
				</div>
			</div>
			<sn-side-panel
				menuItems={menuItems}
				selectedMenu={selectedMenu}
				noPadding={selectedMenu && selectedMenu.menuId === panelSlots.FILTERS}
			>
				<sn-filters-panel
					breakdowns={breakdownInfo({
						breakdowns,
						collectBreakdownMatrix,
						activeBreakdownCollapse,
						breakdownFilterManager,
					})}
					filter-info={filterInfo(
						breakdownFilterManager.getPreviewBreakdowns()
					)}
					collect-breakdown-matrix={collectBreakdownMatrix}
					seriesDisplayType={seriesDisplayType}
					isMultiElementSupported={isMultiElementSupported}
					slot={panelSlots.FILTERS}
					is-filter-panel-loaded={isFilterPanelLoaded}
					has-selection-changed={breakdownFilterManager.hasSelectionChanged()}
					search-key={breakdownFilterManager.getSearchKey()}
					published-breakdowns={breakdownFilterManager.getPublishedBreakdowns()}
					searchResultsFound={searchResultsFound({
						searchKey: breakdownFilterManager.getSearchKey(),
						breakdownFilterManager,
					})}
					splitViewEnabled={splitViewEnabled}
					activeBreakdownCollapse={activeBreakdownCollapse}
					searchInProgress={searchInProgress}
					isSelected={(selectedMenu && selectedMenu.menuId === panelSlots.FILTERS) || false}
				/>
				<div className="targets-panel-container" slot={panelSlots.TARGETS}>
					{getTargetsListView(
						selectedMenu,
						indicator,
						appliedBreakdowns,
						averageScoreValue,
						filter
					)}
				</div>
				<div
					className="thresholds-panel-container"
					slot={panelSlots.THRESHOLDS}
				>
					{<sn-pa-thresholds-panel
						opened={
							selectedMenu && selectedMenu.menuId === panelSlots.THRESHOLDS
						}
						uuid={buildUuidString(indicator.sysId, appliedBreakdowns)}
						has-create-threshold-role={hasCreateThresholdRole}
						has-multiple-elements={hasMultipleElements(appliedBreakdowns)}
						filter-info={filter}
					>
						{ Array.isArray(filter) && filter.length ? <sn-kpi-info filters={filter} /> : undefined}
					</sn-pa-thresholds-panel>}
				</div>
				{isSignalsPluginEnabled ? (
					<div
						className="fill-vertical ah__side-panel--slot"
						slot={panelSlots.SIGNALS}
					>
						<sn-kpi-signals
							uuid={{ indicator, breakdowns: Object.values(appliedBreakdowns) }}
							refreshPanel={isSignalsEnabled}
							opened={isSignalsEnabled}
							user={user}
						/>
					</div>
				) : undefined}
			</sn-side-panel>
		</main>
	);
};
