import {
	PANEL_MENU_ITEM_SELECTED_SET,
	PANEL_MENU_SELECTED_SET,
} from './panelMenuConstants';
export const handlers = {
	[PANEL_MENU_ITEM_SELECTED_SET]: ({ action: { payload }, dispatch }) => {
		if (payload.disabled) return;
		dispatch(PANEL_MENU_SELECTED_SET, payload.menu);
		dispatch('SN_KPI_DETAILS#FOCUS_TO_MENU', { index: payload.index });
	},
};
