export const indicatorQuery = `query ($config: String!) {
    AppDetailedScorecard_Query {
        uuidQuery(config:$config) {
          indicator {
            sysId
            name
            type
            description
            direction
            frequencyLabel
          }
          tags
          formula,
          formulaComponents {
            key
            display
            uUID
          }
		    }
	  }
}`;
