import { actionTypes } from '@servicenow/ui-core';
const { COMPONENT_BOOTSTRAPPED } = actionTypes;
import { createHttpEffect } from '@servicenow/ui-effect-http';
import get from 'lodash/get';
import {
	THRESHOLD_VALUE,
	THRESHOLD_TYPE,
	THRESHOLD_CONDITION_TYPE,
	THRESHOLD_CONDITION_LIST,
	CREATE_THRESHOLD_SAVE,
	UPDATE_THRESHOLD_SAVE,
	DELETE_THRESHOLD,
	CREATE_THRESHOLD_REQUEST,
	CREATE_THRESHOLD_STARTED,
	CREATE_THRESHOLD_SUCCEEDED,
	CREATE_THRESHOLD_FAILED,
	UPDATE_THRESHOLD_REQUEST,
	UPDATE_THRESHOLD_STARTED,
	UPDATE_THRESHOLD_SUCCEEDED,
	UPDATE_THRESHOLD_FAILED,
	DELETE_THRESHOLD_REQUEST,
	DELETE_THRESHOLD_STARTED,
	DELETE_THRESHOLD_SUCCEEDED,
	DELETE_THRESHOLD_FAILED,
	FORM_NAVIGATE_BACK_CLICKED,
} from './constants';

import { isOverLappingThreshold } from '../utils';

const createThreshold = createHttpEffect('/api/now/pa/thresholds/insert', {
	method: 'POST',
	dataParam: 'data',
	startActionType: CREATE_THRESHOLD_STARTED,
	successActionType: CREATE_THRESHOLD_SUCCEEDED,
	errorActionType: CREATE_THRESHOLD_FAILED,
});

const updateThreshold = createHttpEffect('/api/now/pa/thresholds/:sysId', {
	method: 'PUT',
	dataParam: 'data',
	pathParams: ['sysId'],
	startActionType: UPDATE_THRESHOLD_STARTED,
	successActionType: UPDATE_THRESHOLD_SUCCEEDED,
	errorActionType: UPDATE_THRESHOLD_FAILED,
});

const deleteThreshold = createHttpEffect('/api/now/pa/thresholds/:sysId', {
	method: 'DELETE',
	pathParams: ['sysId'],
	startActionType: DELETE_THRESHOLD_STARTED,
	successActionType: DELETE_THRESHOLD_SUCCEEDED,
	errorActionType: DELETE_THRESHOLD_FAILED,
});

export const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: ({
		properties: { thresholdClickedValue, thresholdType, hasCreateThresholdRole },
		updateState,
	}) => {
		updateState({
			init: true,
			value: get(thresholdClickedValue, 'value'),
			thresholdConditionType: get(thresholdClickedValue, 'condition'),
			selectedThresholdType: hasCreateThresholdRole ? thresholdType : 'personal',
		});
	},

	/**
	 * Calls the API for creating the threshold
	 */
	[CREATE_THRESHOLD_REQUEST]: createThreshold,
	[CREATE_THRESHOLD_FAILED]: ({ action: { payload } }) =>
		console.error(payload, CREATE_THRESHOLD_FAILED),

	/**
	 * Calls the API for updating an existing threshold
	 */
	[UPDATE_THRESHOLD_REQUEST]: updateThreshold,
	[UPDATE_THRESHOLD_FAILED]: ({ action: { payload } }) =>
		console.error(payload, UPDATE_THRESHOLD_FAILED),

	/**
	 * Calls the API to delete an existing threshold
	 */
	[DELETE_THRESHOLD_REQUEST]: deleteThreshold,
	[DELETE_THRESHOLD_FAILED]: ({ action: { payload } }) =>
		console.error(payload, UPDATE_THRESHOLD_FAILED),

	/**
	 * Handles event dispatched by bare-button component on threshold form
	 */
	'NOW_BUTTON_BARE#CLICKED': ({
		action: {
			payload: { type },
			stopPropagation,
		},
		dispatch,
	}) => {
		if (type === FORM_NAVIGATE_BACK_CLICKED) {
			stopPropagation();
			dispatch(type);
		}
	},

	/**
	 * Handles the button clicked event in the threshold form
	 */
	'NOW_BUTTON#CLICKED': ({
		action: {
			payload: { type },
			stopPropagation,
		},
		dispatch,
		state: {
			properties: { uuid, thresholdClickedValue },
			value,
			thresholdConditionType,
			selectedThresholdType,
		},
	}) => {
		stopPropagation();
		switch (type) {
		case CREATE_THRESHOLD_SAVE:
			dispatch(CREATE_THRESHOLD_REQUEST, {
				data: {
					uuid: uuid,
					value: value,
					condition: thresholdConditionType,
					type: selectedThresholdType,
				},
			});
			break;

		case UPDATE_THRESHOLD_SAVE:
			dispatch(UPDATE_THRESHOLD_REQUEST, {
				sysId: thresholdClickedValue.sys_id,
				data: {
					value:
							[
								THRESHOLD_CONDITION_LIST[0].id,
								THRESHOLD_CONDITION_LIST[1].id,
							].indexOf(thresholdConditionType) > -1
								? undefined
								: value,
					condition: thresholdConditionType,
				},
			});
			break;

		case DELETE_THRESHOLD:
			dispatch(DELETE_THRESHOLD_REQUEST, {
				sysId: thresholdClickedValue.sys_id,
			});
			break;
		}

		dispatch(type);
	},

	/**
	 * Handles the radio button selection change event in the threshold form
	 */
	'NOW_RADIO_BUTTONS#VALUE_SET': ({
		action: { payload, stopPropagation },
		state: {
			properties: { items },
			value,
			thresholdConditionType,
		},
		updateState,
	}) => {
		if (payload.type === THRESHOLD_TYPE) {
			stopPropagation();
			updateState({ selectedThresholdType: payload.value });
		}
		const isOverlap = isOverLappingThreshold(
			items,
			thresholdConditionType,
			value,
			payload.value
		);
		updateState({ hasOverlappingThreshold: isOverlap ? true : false });
	},

	/**
	 * Handles select box selection change event in the threshold form
	 */
	'NOW_SELECT#SELECTED_ITEM_SET': ({
		action: {
			payload: { type, item },
		},
		state: {
			properties: { items },
			value,
			selectedThresholdType,
		},
		updateState,
	}) => {
		if (type === THRESHOLD_CONDITION_TYPE)
			updateState({
				thresholdConditionType: item.id,
				selectedThreshold: item.label,
			});
		const isOverlap = isOverLappingThreshold(
			items,
			item.id,
			value,
			selectedThresholdType
		);
		updateState({ hasOverlappingThreshold: isOverlap ? true : false });
	},

	/**
	 * Handles the event when user types the value in form in the threshold form
	 */
	'NOW_INPUT#INPUT': ({
		action: { payload, stopPropagation },
		state: {
			properties: { items },
			selectedThresholdType,
			thresholdConditionType,
		},
		updateState,
	}) => {
		if (payload.type === THRESHOLD_VALUE) {
			stopPropagation();
			updateState({ value: payload.fieldValue });
		}
		const isOverlap = isOverLappingThreshold(
			items,
			thresholdConditionType,
			payload.fieldValue,
			selectedThresholdType
		);
		updateState({ hasOverlappingThreshold: isOverlap ? true : false });
	},

	'NOW_ALERT#ACTION_CLICKED': ({
		action: {
			payload: { type },
		},
		state: { value, thresholdConditionType },
		updateState,
		dispatch,
	}) => {
		if (
			type === 'dismiss-thresholds' &&
			value &&
			!(
				thresholdConditionType === 'all_time_high' ||
				thresholdConditionType === 'all_time_low'
			)
		)
			updateState({ value: '', hasOverlappingThreshold: false });
		else dispatch(FORM_NAVIGATE_BACK_CLICKED);
	},
};
