import { t } from 'sn-translate';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import get from 'lodash/get';
import '../components/KPIDetailsHeader';
import '../components/MainContainer';
import './InitialView';
import '../components/Error/KPIAclError';
import 'sn-kpi-signals';
import INVALID_SYS_ID from '../icons/no-valid-sysid.svg';
import NETWORK_ISSUE from '../icons/no-network.svg';
import { SERIES_DISPLAY_TYPE } from '../constants';
import { isValidDate } from '../helpers/dateUtil';
import { parsePreferences, isTrue } from '../helpers/util';

const renderSignalsConfig = (state, { dispatch }) => {
	const {
		scorecard: {
			indicator: { sysId },
		},
		properties: { user, domain },
	} = state;

	return (
		<Fragment>
			<now-button
				className="ah__main-container-back-btn"
				label={t('Back')}
				icon="arrow-left-fill"
				variant="tertiary"
				size="md"
				bare={true}
				on-click={() => {
					dispatch('SN_KPI_SIGNALS_CONFIG_CLOSE');
				}}
				hook-insert={({ elm }) => {
					requestAnimationFrame(() => {
						setTimeout(() => {
							elm.shadowRoot.querySelector('button').focus();
						}, 0);
					});
				}}
				configAria={{
					'aria-label': t('Back to KPI details'),
				}}
			></now-button>
			<sn-kpi-signals-config indicatorId={sysId} user={user} domain={domain} />
		</Fragment>
	);
};

export function MainView(state, helpers) {
	const {
		properties: { indicatorSysid, panel, scoreDate, domain, aggregate, user },
		isInvalidSysid,
		BOOTSTRAP_COMPLETE,
		isAclError,
		isNetworkIssue,
		isFilterPanelLoaded,
		searchInProgress,
		showSignalsConfig,
	} = state;

	if (!BOOTSTRAP_COMPLETE) {
		return <sn-init-view />;
	}

	if (!indicatorSysid || isInvalidSysid)
		return (
			<div className="ah__main-container-invalid-sysid">
				<img className="logo" src={INVALID_SYS_ID} alt={t('Invalid sysid')} />
				<span className="info-primary">{t('Page not found')}</span>
				<span className="info-secondary">
					{t('Try to find the KPI you want in Analytics Center.')}
				</span>
			</div>
		);
	else if (indicatorSysid && isAclError)
		return <sn-acl-error indicator={indicatorSysid} domain={domain} />;
	else if (isNetworkIssue)
		return (
			<div className="ah__main-container-network-issue">
				<img
					className="logo"
					src={NETWORK_ISSUE}
					alt={t('No internet connection')}
				/>
				<span className="info-primary">{t('No internet connection')}</span>
				<span className="info-secondary">
					{t('Check your connection and then refresh the page.')}
				</span>
			</div>
		);
	else {
		const {
			scorecard,
			breakdownFilterManager,
			isSignalsPluginEnabled,
			[SERIES_DISPLAY_TYPE]: seriesDisplayType,
			startDate,
			endDate,
			selectedDate,
			showRecords,
			records,
			recordListError,
		} = state;

		const {
			indicator = {},
			properties = {},
			preferences = [],
			permissions,
			formula,
			formulaComponents,
			tags,
			scoreInfo,
			isPremium,
			prominEnabled,
		} = scorecard;

		const {
			collectRecords,
			firstScoreDate,
			lastScoreDate,
			factsTable,
			multiElementSupported,
			collectBreakdownMatrix,
		} = indicator;

		const { chartLineColor, chartColorScheme } = properties;
		const { query, table } = records;
		const kpiDetails = {
			indicator,
			formulaInfo: {
				formula,
				formulaComponents,
			},
			tags,
		};

		let hasCreateThresholdRole = [];

		if (permissions)
			hasCreateThresholdRole = permissions.filter(
				(role) => role.name === 'can_create_global_threshold'
			);

		const date =
			scoreDate && isValidDate(scoreDate) ? scoreDate : lastScoreDate;

		let preferencesParsed = {};
		if (Array.isArray(preferences)) {
			const prefs = parsePreferences(preferences);
			preferencesParsed = {
				showTargets: isTrue(get(prefs, 'show_targets', 'true')),
				showThresholds: isTrue(get(prefs, 'show_thresholds', 'true')),
				showForecast: isTrue(get(prefs, 'show_forecast', 'false')),
				showTrend: isTrue(get(prefs, 'show_trend', 'false')),
				showComments: isTrue(get(prefs, 'show_comments', 'true')),
				showLabels: isTrue(get(prefs, 'show_labels', 'false')),
			};
		}

		return (
			<Fragment>
				<div class={{"ah__main-container-signals-config": true, hidden: !showSignalsConfig}}>
					{showSignalsConfig ? renderSignalsConfig(state, helpers) : undefined}
				</div>
				<sn-kpi-details-header
					class={{hidden: showSignalsConfig}}
					kpi-details={kpiDetails}
					score-info={scoreInfo}
					selected-date={selectedDate}
					first-score-date={firstScoreDate}
					last-score-date={lastScoreDate}
					score-date={date}
					aggregate={aggregate}
					is-premium={isPremium}
					promin-enabled={prominEnabled}
					breakdown-filter-manager={breakdownFilterManager}
				/>
				<sn-main-container
					class={{hidden: showSignalsConfig}}
					user={user}
					indicator={indicator}
					{...preferencesParsed}
					panel={panel}
					domain={domain}
					start-date={startDate}
					end-date={endDate}
					selected-date={selectedDate}
					chart-line-color={chartLineColor}
					chart-color-scheme={chartColorScheme}
					score-info={scoreInfo}
					records-query={query}
					record-list-error={recordListError}
					show-records={showRecords}
					collect-records={collectRecords}
					table-name={table}
					facts-table={factsTable}
					breakdowns={breakdownFilterManager.getBreakdowns()}
					breakdown-filter-manager={breakdownFilterManager}
					is-signals-plugin-enabled={isSignalsPluginEnabled}
					first-score-date={firstScoreDate}
					last-score-date={lastScoreDate}
					collect-breakdown-matrix={collectBreakdownMatrix}
					series-display-type={seriesDisplayType}
					is-multi-element-supported={multiElementSupported}
					is-filter-panel-loaded={isFilterPanelLoaded}
					search-in-progress={searchInProgress}
					has-create-threshold-role={
						hasCreateThresholdRole && hasCreateThresholdRole.length
							? hasCreateThresholdRole[0].value
							: false
					}
				/>
			</Fragment>
		);
	}
}
