import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { get } from 'lodash';
import { indicatorQuery } from '../../../resources/indicatorQuery';
import { actionTypes } from '@servicenow/ui-core';
import { getConfig } from '../../../helpers/get-config';
const { COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED } = actionTypes;

const fetchIndicatorData = createGraphQLEffect(indicatorQuery, {
	variableList: ['config'],
	startActionType: 'FETCH_INDICATOR_DATA#STARTED',
	successActionType: 'FETCH_INDICATOR_DATA#SUCCEEDED',
	errorActionType: 'FETCH_INDICATOR_DATA#FAILED'
});

const handleProp = ({
	state: {
		properties: { indicator, domain }
	},
	dispatch
}) => {
	let config = {};
	if (indicator) config = getConfig(config, 'indicator', indicator);
	if (domain) config = getConfig(config, 'domain', domain);

	dispatch('FETCH_INDICATOR_DATA', {
		config: JSON.stringify(config)
	});
};

export const handlers = {
	[COMPONENT_BOOTSTRAPPED]: handleProp,
	[COMPONENT_PROPERTY_CHANGED]: handleProp,
	['FETCH_INDICATOR_DATA']: fetchIndicatorData,
	['FETCH_INDICATOR_DATA#SUCCEEDED']: ({
		action: { payload },
		updateState
	}) => {
		const errors = get(payload, 'errors');
		if (errors && errors.length !== 0) {
			updateState({ hasError: true });
		} else if (payload) {
			const indicatorData = get(
				payload,
				'data.AppDetailedScorecard_Query.indicatorQuery',
				{}
			);
			updateState({ ...indicatorData, hasError: false });
		}
	},
	['FETCH_INDICATOR_DATA#FAILED']: ({
		updateState
	}) => {
		updateState({ hasError: true });
	}
};
