import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

import { view } from './chart-options-view';
import styles from './chart-options-styles.scss';

createCustomElement('sn-chart-options', {
	renderer: {
		type: snabbdom,
	},
	properties: {
		hasMultiElement: {
			default: false,
		},
		hasMultiSeries: {
			default: false,
		},
		derivedSeriesDataInfo: {
			default: {},
		},
		derivedSeries: {
			default: {},
		},
	},
	view,
	styles,
});
