import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { view } from './panel-menu-item-view';
import styles from './panel-menu-item-view.scss';

createCustomElement('sn-panel-menu-item', {
	renderer: {
		type: snabbdom
	},
	view,
	properties: {
		menu: {},
		menuId: {
			reflect: true
		},
		menuTitle: {
			reflect: true
		},
		iconName: {
			reflect: false
		},
		tooltip: {
			reflect: true
		},
		selected: {
			reflect: true,
			default: false
		},
		disabled: {
			reflect: true,
			default: false
		},
		focusable: {
			default: false
		},
		badgeText: {},
		menuIndex: {}
	},
	styles
});
