export const handlers = {
	'SN_KPI_DETAILS#BREAKDOWN_ELEMENT_SELECT': ({
		action: {
			payload: { breakdownId, element }
		},
		dispatch
	}) => {
		dispatch('SN_KPI_DETAILS#BREAKDOWN_ELEMENT_SELECT_PREVIEW', {
			breakdownId,
			element
		});
	},
	'NOW_CHECKBOX#CHECKED_SET': ({
		action: {
			payload: { breakdown, breakdownId, element, type }
		},
		dispatch
	}) => {
		if (type === 'element-toggle')
			dispatch('SN_KPI_DETAILS#BREAKDOWN_ELEMENT_TOGGLE_SET', {
				breakdown,
				breakdownId,
				element
			});
	},
	'NOW_DROPDOWN#SELECTED_ITEMS_SET': ({
		state: {
			properties: { breakdown }
		},
		action: {
			payload: { value, type, sortDirection }
		},
		dispatch
	}) => {
		if (type === 'filter') {
			dispatch('SN_KPI_DETAILS#BREAKDOWN_ELEMENT_FILTER_SET', {
				breakdownId: breakdown.sysId,
				elementFilterSysId: value[0],
				sortDirection,
				breakdown
			});
		} else if (type === 'sort') {
			dispatch('SN_KPI_DETAILS#BREAKDOWN_ELEMENT_SORT', {
				breakdownId: breakdown.sysId,
				sortBy: value[0],
				sortDirection,
				breakdown
			});
		}
	}
};
