export const view = ({ properties: { text, computedText, computedHighlightText } }) => {
	const getMatchPartsGrouped = (matches = [], lastMatch = 0) => {
		const index = computedText.indexOf(computedHighlightText, lastMatch);
		if (index > -1) {
			matches.push({text: text.slice(lastMatch, index), match: false});
			matches.push({text: text.slice(index, index + computedHighlightText.length), match: true});

			return getMatchPartsGrouped(matches, index + computedHighlightText.length);
		} else
			matches.push({text: text.slice(lastMatch), match: false});

		return matches;
	};

	const matches = computedHighlightText ? getMatchPartsGrouped() : [];

	if (matches.length > 1)
		return <p className="search-match">{matches.map(matchItem => <span class={{match: matchItem.match}}>{matchItem.text}</span>)}</p>;

	return <span>{text}</span>;
};
