import { panelSlots } from '../constants';

export const signalPanelConfigHandlers = {
	'SN_KPI_SIGNALS_CONFIG#OPEN': ({ updateState }) => {
		updateState({ showSignalsConfig: true, isSignalsPluginEnabled: false });
	},
	'SN_KPI_SIGNALS_CONFIG_CLOSE': ({ host, updateState, updateProperties}) => {
		updateState({ showSignalsConfig: false, isSignalsPluginEnabled: true  });
		if (host) 
			requestAnimationFrame(() => { 
				setTimeout(() => host.focus(), 0);
			});
	}
};