import { createHttpEffect } from '@servicenow/ui-effect-http';
import { get } from 'lodash';
import { getConfig } from '../helpers/get-config';
import { hasMultipleElements } from '../helpers/common';
import { t } from 'sn-translate';
import { CHART_DATE_FORMAT, PICKER_DATE_FORMAT } from '../constants';
import { FetchStatus } from '../constants';
import { getFormattedDate, getParsedDate, isValidDate } from '../helpers/dateUtil';
import { getElementIds } from '../helpers/elements';

const handleBreakdownElementFetch = createHttpEffect(
	'/api/now/v2/pa/detailed_scorecard/breakdown/elements/:breakdown',
	{
		method: 'POST',
		pathParams: ['breakdown'],
		batch: true,
		dataParam: 'data',
		startActionType: 'SN_KPI_DETAILS#FETCH_BREAKDOWN_ELEMENT_STARTED',
		successActionType: 'SN_KPI_DETAILS#FETCH_BREAKDOWN_ELEMENT_SUCCEEDED',
		errorActionType: 'SN_KPI_DETAILS#FETCH_BREAKDOWN_ELEMENT_FAILED'
	}
);

const handleBreakdownElementSearch = createHttpEffect(
	'/api/now/v2/pa/detailed_scorecard/breakdown/elements/search/:searchkey',
	{
		method: 'POST',
		pathParams: ['searchkey'],
		dataParam: 'data',
		batch: true,
		startActionType: 'SN_KPI_DETAILS#SEARCH_BREAKDOWN_ELEMENT_STARTED',
		successActionType: 'SN_KPI_DETAILS#SEARCH_BREAKDOWN_ELEMENT_SUCCEEDED',
		errorActionType: 'SN_KPI_DETAILS#SEARCH_BREAKDOWN_ELEMENT_FAILED'
	}
);

export const filterManagerHandlers = {
	'SN_KPI_DETAILS#FETCH_BREAKDOWN_ELEMENT_REQUESTED': {
		...handleBreakdownElementFetch,
		interceptors: [context => {
			const { coeffects: { action } } = context;
			action.stopPropagation();
			return context;
		}]
	},
	'SN_KPI_DETAILS#FETCH_BREAKDOWN_ELEMENT_SUCCEEDED': ({
		action: { payload, meta },
		state
	}) => {
		const { breakdownFilterManager, selectedDate, scorecard } = state;
		const { indicator: { firstScoreDate, lastScoreDate } } = scorecard;
		const formattedSelectedDate = getFormattedDate(
			selectedDate,
			PICKER_DATE_FORMAT,
			CHART_DATE_FORMAT
		);
		const isSelectedDateOutOfRange = getParsedDate(firstScoreDate, lastScoreDate, formattedSelectedDate);
		if (isSelectedDateOutOfRange)
			payload.result = [];	
		breakdownFilterManager.setElementsForBreakdown(
			meta.breakdownId,
			payload.result
		);
		breakdownFilterManager.syncStore();
	},
	'SN_KPI_DETAILS#FETCH_BREAKDOWN_ELEMENT_FAILED': ({
		action,
		state: { breakdownFilterManager }
	}) => {
		const {
			meta: { breakdownId },
			payload
		} = action;
		const message = get(payload, 'data.error.message');
		if (action.error && message === 'Scores for this combination is excluded')
			breakdownFilterManager.addExcludedCombination(breakdownId);
		breakdownFilterManager.setElementFetchStatus(breakdownId, FetchStatus.FAIL);
	},
	FETCH_BREAKDOWN_ELEMENTS: ({
		action: {
			payload: { breakdownId, appliedBreakdowns, elementFilter }
		},
		state: {
			properties: { scoreDate, domain },
			scorecard: {
				indicator: { sysId, lastScoreDate }
			},
			selectedDate
		},
		dispatch
	}) => {
		const formattedSelectedDate = getFormattedDate(
			selectedDate,
			PICKER_DATE_FORMAT,
			CHART_DATE_FORMAT
		);
		const date = 
			scoreDate && isValidDate(scoreDate) ? scoreDate : lastScoreDate;
		dispatch(
			'SN_KPI_DETAILS#FETCH_BREAKDOWN_ELEMENT_REQUESTED',
			{
				breakdown: breakdownId,
				data: {
					indicator_sysid: sysId,
					element_filter: elementFilter,
					applied_breakdowns: appliedBreakdowns.map(breakdown => ({
						breakdown_id: breakdown.sysId,
						elements: getElementIds(breakdown.elements)
					})),
					date: formattedSelectedDate || date,
					domain
				}
			},
			{ breakdownId }
		);
	},
	'SN_KPI_DETAILS#BREAKDOWN_FILTER_MANAGER_SYNC': ({
		action: { payload: breakdownFilterManager },
		updateState
	}) => {
		updateState({ breakdownFilterManager });
	},
	'SN_KPI_DETAILS#BREAKDOWNS_APPLIED': ({
		action: { payload: appliedBreakdowns = [] },
		state: {
			properties: { scoreDate, domain },
			scorecard: {
				indicator: { sysId, lastScoreDate, multiElementSupported }
			},
			selectedDate,
			showRecords
		},
		dispatch
	}) => {
		const formattedSelectedDate = getFormattedDate(
			selectedDate,
			PICKER_DATE_FORMAT,
			CHART_DATE_FORMAT
		);
		const date = 
			scoreDate && isValidDate(scoreDate) ? scoreDate : lastScoreDate;
		let config = getConfig({}, 'indicator', sysId);
		config = getConfig(config, 'date', formattedSelectedDate || date);
		config = getConfig(
			config,
			'breakdowns',
			appliedBreakdowns.map(breakdown => ({
				breakdownId: breakdown.sysId,
				elements: getElementIds(breakdown.elements)
			}))
		);
		if (domain) config = getConfig(config, 'domain', domain);

		// when multiple elements selected one any level and multiElementSupported is not there then show no scores
		if (!multiElementSupported && hasMultipleElements(appliedBreakdowns))
			dispatch('SN_KPI_DETAILS#AGGREGATED_SCORE_NOT_AVAILABLE', {
				message: t('Combined score not available for this KPI')
			});
		else
			dispatch('SN_KPI_DETAILS#FETCH_ONLY_SCORE', {
				config: JSON.stringify(config)
			});

		if (showRecords) {
			let configRecords = getConfig({}, 'indicator_sysid', sysId);
			configRecords = getConfig(configRecords, 'start_date', formattedSelectedDate || date);
			configRecords = getConfig(
				configRecords,
				'applied_breakdowns',
				Object.values(appliedBreakdowns).map(breakdown => ({
					breakdown_id: breakdown.sysId,
					elements: getElementIds(breakdown.elements)
				}))
			);
			if (domain) configRecords = getConfig(configRecords, 'domain', domain);

			dispatch('SN_KPI_DETAILS#SYSIDS_FETCH_REQUESTED', {
				data: JSON.stringify(configRecords)
			});
		}
	},
	'SN_KPI_DETAILS#BREAKDOWN_ELEMENT_SEARCH_REQUESTED': handleBreakdownElementSearch,
	'SN_KPI_DETAILS#BREAKDOWN_ELEMENT_SEARCH': ({
		action: {
			payload: { searchKey: searchkey }
		},
		state: {
			properties: { domain },
			scorecard: {
				indicator: { sysId, lastScoreDate }
			},
			breakdownFilterManager
		},
		dispatch,
		updateState
	}) => {
		const appliedBreakdowns = breakdownFilterManager.getPreviewBreakdowns();
		if (searchkey) {
			dispatch(
				'SN_KPI_DETAILS#BREAKDOWN_ELEMENT_SEARCH_REQUESTED',
				{
					searchkey,
					data: {
						indicator_sysid: sysId,
						applied_breakdowns: Object.values(appliedBreakdowns).map(
							breakdown => ({
								breakdown_id: breakdown.sysId,
								elements: getElementIds(breakdown.elements)
							})
						),
						date: lastScoreDate,
						domain
					}
				},
				{
					searchkey
				}
			);
			updateState({ searchInProgress: true });
		}
	},
	'SN_KPI_DETAILS#SEARCH_BREAKDOWN_ELEMENT_SUCCEEDED': ({
		action: {
			payload,
			meta: { searchkey }
		},
		state: { breakdownFilterManager },
		updateState
	}) => {
		breakdownFilterManager.saveSearchResults(searchkey, payload.result);
		updateState({ searchInProgress: false });
	}
};
