/**
 * Converts a valid color string to hex form.
 * @param {String} color
 *
 * @returns String
 */
export function getHexColor(color) {
	if (color) {
		if (color.indexOf('#') === 0) return color;

		const splitColor = color
			.replace(/[(rgba?)/(/)]/gi, '')
			.trim()
			.split(',');

		if (splitColor.length >= 3) {
			return (
				'#' +
				getHexValue(splitColor[0]) +
				getHexValue(splitColor[1]) +
				getHexValue(splitColor[2])
			);
		} else return '';
	} else return '';
}

/**
 *
 * @param {string} value
 * @returns string
 */
function getHexValue(value) {
	const hexValue = parseInt(value, 10).toString(16);
	return hexValue.length === 1 ? '0' + hexValue : hexValue;
}
