import '@servicenow/now-toggle';
import { t } from 'sn-translate';
import { isNull } from '../../helpers/common';

export const view = (
	{
		properties: {
			indicatorSysId,
			indicatorType,
			factsTable,
			collectRecords,
			showRecords,
			hasScore,
			isSignalsEnabled,
		},
		targetRef,
	},
	{ updateState }
) => {
	const showRecordClass = ['show-records'];
	const disableShowRecords =
		isSignalsEnabled ||
		isNull(factsTable) ||
		!(indicatorType === 'AUTOMATED' && collectRecords);
	let indicator;
	if (indicatorType) indicator = indicatorType.toLowerCase();

	const setTargetRef = (ref, propName) => updateState({ [propName]: ref });

	// Show info Text if indicator type is not AUTOMATED or collectRecords is turned off
	// Change styles of show records when it is disabled
	let infoText = '';
	if (disableShowRecords) {
		showRecordClass.push('disable-show-records');

		if (isSignalsEnabled)
			infoText = t('Records unavailable while KPI Signals is open.');
		else if (indicatorType !== 'AUTOMATED')
			infoText =
				t('Records are not available for') +
				' ' +
				indicator +
				' ' +
				t('indicators');
		else if (isNull(factsTable))
			infoText = t('No records are associated with manually entered scores');
		else if (hasScore) infoText = t('Records are not collected for this KPI');
	}

	return (
		<div className="ah-toolbar-actions">
			<span className={showRecordClass.join(' ')}>{t('Show Records')}</span>
			<now-toggle
				hook-insert={({ elm }) => setTargetRef(elm, 'targetRef')}
				hook-destroy={() => setTargetRef(null)}
				size="sm"
				disabled={disableShowRecords}
				checked={showRecords}
				manage-checked
				append-to-payload={{ type: 'showRecords' }}
				aria-describedby={`ah-tooltip-${indicatorSysId}`}
				config-aria={{
					'aria-label': t('Show records'),
				}}
			></now-toggle>
			<now-tooltip
				id={`ah-tooltip-${indicatorSysId}`}
				target-ref={targetRef}
				content={infoText}
				position={['top-start']}
				delay={50}
			/>
		</div>
	);
};
