import cloneDeep from 'lodash/cloneDeep';
import get from 'lodash/get';
import { THRESHOLD_CONDITION_LIST } from './ThresholdForm/constants';

export const parseQueryResponse = (data, init = true) => {
	let result;
	if (init) data = cloneDeep(data);
	if (data && typeof data === 'object')
		Object.keys(data).forEach((key) => {
			if (
				key === 'value' &&
				(typeof data[key] !== 'object' ||
					(typeof data[key] === 'object' && data[key] == undefined))
			) {
				if (Object.keys(data).indexOf('displayValue') === -1)
					result = data[key];
				else
					result = {
						[key]: data[key],
						displayValue: data.displayValue,
					};
			} else if (key === '_reference' && data[key]) {
				data = { ...parseQueryResponse(data[key], false) };
			} else {
				data[key] = parseQueryResponse(data[key], false);
			}
		});
	return result !== undefined ? result : data;
};

export const wrapWithHandler = (obj = []) => {
	if (typeof obj !== 'object') {
		// eslint-disable-next-line no-console
		console.error(Error(`Handler got non-object to proxy :::-> <${obj}>`));
		obj = [];
	}

	return new Proxy(obj, {
		get: function (target, prop) {
			switch (prop) {
			case 'getItemsOfType':
				return function (type = 'all') {
					if (Array.isArray(target))
						return target.filter(
							(_target) =>
								(type === 'global' && get(_target, 'is_global') === true) ||
									(type === 'personal' && get(_target, 'is_global') !== true) ||
									type === 'all'
						);
				};
			default:
				return Reflect.get(...arguments);
			}
		},
	});
};

export const getSortedGroup = (
	items,
	order = ['all_time_high', 'more_than', 'less_than', 'all_time_low']
) => {
	let result = [];
	const groups = {};

	if (Array.isArray(items)) {
		items.forEach((item) => {
			if (groups[item.condition]) groups[item.condition].push(item);
			else groups[item.condition] = [item];
		});

		order.forEach((groupName) => {
			result = result.concat(groups[groupName] || []);
		});

		return result;
	}
	return items;
};

/**
 * Returns true if the passed value, thresholdConditionTye and thresholdType
 * matches the corresponding same elements for any item in the items list
 *
 * @param {Array} items
 * @param {string} thresholdConditionType
 * @param {number} value
 * @param {string} selectedThresholdType
 *
 * @returns boolean
 */
export const isOverLappingThreshold = (
	items,
	thresholdConditionType,
	value,
	selectedThresholdType = 'global'
) => {
	if (
		(Array.isArray(items) && items.length === 0) ||
		thresholdConditionType === 'select_option'
	)
		return false;
	const isGlobal = selectedThresholdType === 'global';
	let result = false;
	for (let i in items) {
		if (items[i].is_global === isGlobal) {
			result =
				items[i].condition === thresholdConditionType &&
				([
					THRESHOLD_CONDITION_LIST[0].id,
					THRESHOLD_CONDITION_LIST[1].id,
				].indexOf(thresholdConditionType) < 0
					? items[i].value === parseInt(value)
					: true);
		}
		if (result) return result;
	}
	return false;
};

export function focusToNode(node, query) {
	let focusNode = node;

	if (query) focusNode = (node.shadowRoot || node).querySelector(query);

	if (focusNode && typeof focusNode.focus === 'function')
		setTimeout(() => focusNode.focus(), 0);
}
