import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { view } from './threshold-panel-view';
import { actionHandlers } from './action-handlers';
import styles from './styles.scss';

createCustomElement('sn-pa-thresholds-panel', {
	renderer: {
		type: snabbdom,
	},
	properties: {
		opened: {
			schema: {
				type: 'boolean',
			},
			default: false,
		},
		queryString: {
			schema: {
				type: 'string',
			},
		},
		uuid: {
			schema: {
				type: 'string',
			},
		},
		hasCreateThresholdRole: {
			schema: {
				type: 'boolean',
			}
		},
		hasMultipleElements: {
			schema: {
				type: 'boolean',
			}
		},
		filterInfo: {
			schema: {
				type: 'object',
			}
		},
	},
	view,
	actionHandlers,
	styles,
});
