import { get, isPlainObject, clone } from 'lodash';

export const hasMultipleElements = (breakdowns) => {
	let clonedBreakdowns = clone(breakdowns);
	let hasMultipleElements = false;
	if (isPlainObject(clonedBreakdowns))
		clonedBreakdowns = Object.values(breakdowns);

	if (Array.isArray(clonedBreakdowns)) {
		for (let index = 0; index < clonedBreakdowns.length; index++) {
			const elements = clonedBreakdowns[index].elements;
			if (elements.length > 1) {
				hasMultipleElements = true;
				break;
			}
		}
	}
	return hasMultipleElements;
};

export function isNull(x) {
	if (x && x !== 'undefined') return false;
	return true;
}

export const convertArrayToObject = (array) => {
	if (!Array.isArray(array)) return array;
	return array.reduce((obj, item) => {
		obj[item] = item;
		return obj;
	}, {});
};

/**
 * Returns the current user language being used.
 */
export const userLanguage = () => {
	return get(
		window,
		'ux_globals.presource["sn-workspace-header:wsUserData"].data.GlideDomain_Query.user.language',
		'en'
	);
};
