import { t } from 'sn-translate';
import '@servicenow/now-button';
import '@servicenow/now-popover';
import '../ToolbarActions';
import './ChartOptions';
import '@servicenow/now-date-range-picker';
import { getFormattedDate } from '../../helpers/dateUtil';
import { CHART_DATE_FORMAT, PICKER_DATE_FORMAT } from '../../constants';
import { userLanguage } from '../../helpers/common';
import '../SkipLink';

export const view = ({
	properties: {
		indicatorSysId,
		indicatorType,
		startDate,
		endDate,
		factsTable,
		collectRecords,
		showRecords,
		hasScore,
		derivedSeriesDataInfo,
		hasMultiElement,
		hasMultiSeries,
		derivedSeries,
		isSignalsEnabled,
	}
}) => {
	const formattedStartDate = getFormattedDate(
		startDate,
		CHART_DATE_FORMAT,
		PICKER_DATE_FORMAT
	);
	const formattedEndDate = getFormattedDate(
		endDate,
		CHART_DATE_FORMAT,
		PICKER_DATE_FORMAT
	);

	const userLocale = userLanguage();
	return (
		<div className="ah__toolbar">
			<sn-toolbar-actions
				indicator-sysId={indicatorSysId}
				indicator-type={indicatorType}
				facts-table={factsTable}
				collect-records={collectRecords}
				show-records={showRecords}
				has-score={hasScore}
				is-signals-enabled={isSignalsEnabled}
			/>
			<div className="actions">
				{renderChartOptions(
					isSignalsEnabled,
					derivedSeriesDataInfo,
					hasMultiElement,
					hasMultiSeries,
					derivedSeries
				)}
				<div className="date-range">
					{formattedStartDate && formattedEndDate && (
						<now-date-range-picker
							format={PICKER_DATE_FORMAT}
							start={formattedStartDate}
							end={formattedEndDate}
							labelStart=" "
							labelEnd=" "
							language={userLocale}
						/>
					)}
					<sn-skip-link
						label={t('Skip to side panel')}
						focus-action="SN_KPI_DETAILS#SIDE_PANEL_FOCUS"
					/>
				</div>
			</div>
		</div>
	);
};

const renderChartOptions = (isSignalsEnabled,
	derivedSeriesDataInfo,
	hasMultiElement,
	hasMultiSeries,
	derivedSeries) => {

	const chartOptionsDisabled = isSignalsEnabled;
	let chartOptionsTooltip = undefined;
	if (isSignalsEnabled)
		chartOptionsTooltip = t('Chart options are not available while KPI Signals is open.');
	const chartOptionsButton = (
		<now-button
			slot="trigger"
			icon="sliders-outline"
			size="sm"
			label={t('Chart Options')}
			config-aria={{'aria-label': t('Chart Options')}}
			variant="tertiary"
			disabled={chartOptionsDisabled}
			tooltipContent={chartOptionsTooltip}
			append-to-payload={{
				type: 'chart_options',
			}}
		/>
	);
	return (
		<div className="chart-options">
			{ chartOptionsDisabled ? chartOptionsButton : (
				<now-popover
					positions={[
						{target: 'bottom-center', content: 'top-center'},
						{target: 'top-center', content: 'bottom-center'},
						{target: 'center-end', content: 'center-start'},
						{target: 'center-start', content: 'center-end'},
					]}
				>
					{chartOptionsButton}
					<sn-chart-options
						slot="content"
						derived-series-data-info={derivedSeriesDataInfo}
						has-multi-element={hasMultiElement}
						has-multi-series={hasMultiSeries}
						derived-series={derivedSeries}
					/>
				</now-popover>
			)}
		</div>
	);
};
